import { Component, Input } from '@angular/core';
import { Dateservice } from '../../../../share/service/date.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../../share/service/master-data.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { CurrencyFormatterPipe } from '../../../../share/pipe/currency-formatter.pipe';
import { TimelineMapFields } from '../../../../share/component/timeline/timeline-mapfields';
import { TimelineComponent } from '../../../../share/component/timeline/timeline.component';
import { Constants } from '../../../../share/service/constants';
import { PermissionService } from '../../../../share/service/permission.service';
import { ItemSliding } from 'ionic-angular';
import { GetinformationService } from "../../../../share/service/getInformation.service";
@Component({
    selector: 'timeline-mobile',
    templateUrl: './timeline.html',
    providers: [TimelineMapFields]
})
export class MobileTimelineComponent extends TimelineComponent {

    @Input("showDeleteBtn") disableDeleteBtn: boolean;

    constructor(
        public dateservice: Dateservice,
        public translateService: TranslateService,
        public masterDataService: MasterDataService,
        public bankCodeDataService: BankCodeDataService,
        public timelineMapFields: TimelineMapFields,
        public currencyFormatter: CurrencyFormatterPipe,
        public constants: Constants,
        public permissionService: PermissionService,
        public getinformationService: GetinformationService,
    ) {
        super(dateservice, translateService, masterDataService, bankCodeDataService, timelineMapFields, currencyFormatter, constants, permissionService,getinformationService);
    }

    public createTimelineData(data: Array<any>): Array<any> {
        
        let mapfields = this.timelineMapFields.data[this.timelineKey];

        let returnData = data;
        if (data && mapfields) {
            returnData = [];
            
            
            for (let item of data) {
                let timeline = {};
                timeline["accountFromNumber"] = item[mapfields["accountFromNumber"]];
                timeline["accountFromName"] = item[mapfields["accountFromName"]];
                timeline["accountToNumber"] = item[mapfields["accountToNumber"]];
                timeline["accountToName"] = item[mapfields["accountToName"]];
                timeline["status"] = item[mapfields["status"]];
                timeline["statusDesc"] = item[mapfields["statusDesc"]];
                timeline["statusCode"] = item[mapfields["statusCode"]];
                timeline["bankName"] = item[mapfields["bankName"]];
                timeline["rawData"] = item;
                //rtp
                timeline["rtpReferenceNo"] = item[mapfields["rtpReferenceNo"]];

                timeline["billerName"] = item[mapfields["biller"]];
                timeline["billerAliasName"] = item[mapfields["paymentFor"]];
                timeline["categoryId"] = item[mapfields["categoryId"]];
                // format transaction date
                let transactionDate;

                if (this.timelineTitle === 'schedule') {
                    transactionDate = this.dateservice.parseDate(item[mapfields["debitDate"]]);
                    timeline["transMonth"] = this.dateservice.formatDate(transactionDate, 'MMM', this.translateService.currentLang);
                } else {
                    transactionDate = this.dateservice.parseDate(item[mapfields["transactionDate"]]);
                    timeline["transMonth"] = this.dateservice.formatDate(transactionDate, 'MMM', this.translateService.currentLang);
                }

                timeline["transDate"] = this.dateservice.formatDate(transactionDate, 'DD', this.translateService.currentLang);
                timeline["amount"] = this.currencyFormatter.transform(item[mapfields["amount"]]) || "0.00";

                returnData.push(timeline);
            }
        }
        
        return returnData;
    }

    showDelete(data: any, itemSlide: ItemSliding) {
        this.onClickActions.emit({ name: 'delete', data: data });
        //itemSlide.close();
    }

    onSelectItem(data: any) {
        this.onClickActions.emit({ name: 'info', data: data });
    }

    private _itemSliding: any = null;
    itemSliding(slidingItem: ItemSliding){
        this._itemSliding = slidingItem;
    }

    hideItemSliding(){
        
        if(this._itemSliding !== null){
            this._itemSliding.close();
        }
    }
}



