import { QRGeneratorBase } from '../qr-generator-base';
import { Constants } from '../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { qrGeneratorService } from '../qr-generator.service';
import { RequestToPayService } from '../../request-to-pay/request-to-pay.service';
import { MasterDataService } from '../../../share/service/master-data.service'; 
import { CurrencyFormatterPipe } from '../../../share/pipe/currency-formatter.pipe';
import { PermissionService, PermissionChangeRoute, PermissionMainMenu } from '../../../share/service/permission.service';
import { ModalController, AlertController} from 'ionic-angular';

@Component({
    selector: 'pib-Mobile-qr-generator',
    templateUrl: './qr-generator-mobile.html'
})

export class PIBMobileQRGeneratorComponent extends QRGeneratorBase implements OnInit {
    private stepWizard: any;
    
    constructor(
    protected requestToPayService: RequestToPayService,
    protected translate: TranslateService,
    protected constants: Constants,
    protected qrGeneratorService: qrGeneratorService,
    protected currencyFormatter: CurrencyFormatterPipe,
    protected permissionChangeRoute: PermissionChangeRoute,
    protected modalCtrl: ModalController,
    protected alertCtrl: AlertController,
    )
     {
        super(requestToPayService,translate,constants,qrGeneratorService,currencyFormatter,permissionChangeRoute,modalCtrl,alertCtrl);
    }

    public ngOnInit() {
        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.complete' }],
                step: 0,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
        if(this.qrGeneratorService.getQRCodeData()){
            let selectedAccData = this.qrGeneratorService.getQRCodeData();
            this.myPromptPayAccount.anyIDValue = selectedAccData.anyIDValue;
            this.myPromptPayAccount.anyIDType = selectedAccData.anyIDType;
            this.myPromptPayAccount.accountName = selectedAccData.accountName;
            this.qrGeneratorService.setQRCodeData(undefined);
        } 
    }

}



