import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../../share/service/constants';
import { UtilService } from '../../../../../share/service/util.service';
import { Dateservice } from '../../../../../share/service/date.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { TransactionHistoryPage } from '../../../transaction-history-page';
import { ReportService } from '../../../../../share/service/report.service';
import { BillPaymentService } from '../../../../bill-payment/bill-payment.service';
import { MasterDataService } from '../../../../../share/service/master-data.service';
import { MyAccountService } from '../../../../../pages/my-account/my-account.service';
import { PermissionChangeRoute, PermissionService } from '../../../../../share/service/permission.service';
import { FundTransferService } from '../../../../../share/service/fund-transfer.service';

@Component({
    selector: 'bill-pay-transaction-history-page',
    templateUrl: '../transaction-history-page.html'
})
export class BillPayTransactionHistoryPageComponent extends TransactionHistoryPage implements OnInit {

    selectOptions: { title: string; };
    ionInfiniteScrollData: { count: number; max: number; enabled: boolean; threshold: string; itemCount: number; };

    constructor(
        public myAccountService: MyAccountService,
        public constants: Constants,
        public fundTransferService: FundTransferService,
        public billPaymentService: BillPaymentService,
        public translateService: TranslateService,
        private permissionChangeRoute: PermissionChangeRoute,
        public utilService: UtilService,
        public dateservice: Dateservice,
        public reportService: ReportService,
        public masterDataService: MasterDataService,
        public permissionService: PermissionService) {
        super(myAccountService, constants, fundTransferService, billPaymentService, translateService, utilService, dateservice, reportService, masterDataService, permissionService);
    }

    ngOnInit(): void {

        this.selectOptions = {
            title: this.translateService.instant('label.selectPeriod')
        };

        this.timeline = {
            data: {
                rawData: [],
                resetData: true
            },
            title: 'history',
            subtitle: 'biller'
        };

        this.monthList = {
            options: [],
            selected: {
                label: "",
                value: ""
            }
        };

        this.historyData = {
            period: '',
            paging: {
                page: 1,
                pageSize: '20'
            },
            accountNo: ''
        };

        this.masterDataService.getAllAnyIDTypes().then((result: any) => {
        });

        this.initialMonthList().then((result: any) => {
            this.historyData.period = this.monthList.selected.value;
            this.getBillerHistory();
        });
    }

    onChangePeriod(period: any): void {
        
        this.historyData.paging.page = 1;

        this.getBillerHistory();
    }

    onActions(data: any): void {
        console.log('BillPayTransactionHistoryPageComponent', data)
        let tmpdata = data.data;
        this.billPaymentService.setBillPaymentObj(tmpdata.rawData);
        this.permissionChangeRoute.changeRoute('HISTORY_PAY_BILL.DETAIL');
    }

    isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }
}



