import { NavController } from 'ionic-angular';
import { Component, OnInit, AfterViewInit, } from '@angular/core';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { MyAccountStatementPageComponent } from '../../../../pages/my-account/mobile/pib/my-account-statement-page.component';
import { Constants } from '../../../../share/service/constants';
import { PromptPayRegisterServiceMain } from '../../prompt-pay-register.service';
import { qrGeneratorService } from '../../../../pages/qr-generator/qr-generator.service';
import { DashBoardService } from '../../../../pages/dashboard/dashboard.service';
import { TranslateService } from "ng2-translate/src/translate.service";

@Component({
  selector: 'my-kk-prompt-pay-detail',
  templateUrl: './my-kk-prompt-pay-detail.html'
})
// Component class
export class MyKKPromptPayDetailComponent implements OnInit {
    private permissionManage: any = {};
    private selectAccount: any;
    constructor(private navCtrl: NavController,
        private permissionChangeRoute: PermissionChangeRoute,
        private permissionService: PermissionService,
        private constants: Constants,
        private promptPayRegisterService: PromptPayRegisterServiceMain,
        private qrGeneratorService: qrGeneratorService,
        private dashBoardService: DashBoardService,
        private translate: TranslateService) {
    }

    ngOnInit(): void {
        this.selectAccount =  this.promptPayRegisterService.selectedAccount;
        this.promptPayRegisterService.selectedAccount = undefined;
    }

    private onTabsSelect(selected): void {
    switch(selected){
        case 'createRTP':
          this.dashBoardService.selectDashBoardDetails = this.selectAccount;
          this.permissionChangeRoute.prevUrl='MY_ACCOUNTS.DETAIL';
          this.permissionChangeRoute.changeRoute('MY_RTP.ADD');
          break;
        case 'QRGenerate':
          this.qrGeneratorService.setQRCodeData(this.selectAccount);
          this.permissionChangeRoute.changeRoute('QR_GENERATOR');
          break;
        case 'edit':
          this.promptPayRegisterService.selectedAccount = this.selectAccount;
          this.permissionChangeRoute.changeRoute('MY_KK_PROMPTPAY.EDIT');
        break;
        default:
          break;
      }
  }
  
}



