import { Constants } from '../../../../share/service/constants';
import { BillPaymentService } from '../../bill-payment.service';
import { StepWizard } from '../../../../share/component/step-wizard/step-wizard.component';

export class BillPayment {
    protected stepWizard: StepWizard;

    constructor(public constants: Constants, public billPaymentService: BillPaymentService) {
    }

    init(stepNo: number): void {
        this.stepWizard = {
            input: {
                data: [
                    { name: '1', label: 'stepWizard.enterDetails' },
                    { name: '2', label: 'stepWizard.confirm' },
                    { name: '3', label: 'stepWizard.complete' }
                ],
                step: stepNo,
                style: this.constants.STYLE_PIB_MOBILE
            }
        };
    }
}   


