import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { TranslateService } from 'ng2-translate';
import { FormBuilder, Validators } from '@angular/forms';
import { ModalController, ViewController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { OrderByPipe } from '../../../../share/pipe/order-by.pipe';
import { UtilService } from '../../../../share/service/util.service';
import { PermissionAction } from '../../../../share/service/permission.service';
import { PromptPayRegisterComponent } from '../../prompt-pay-register.component';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { StepWizard } from '../../../../share/component/step-wizard/step-wizard.component';
import { PromptPayRegisterServiceMain } from '../../../../pages/prompt-pay/prompt-pay-register.service';
import { FromAccountList } from '../../../../share/component/from-account-list/mobile/from-account-list.component';
import { TermAndConditionModalService } from '../../../../share/component/terms-and-conditions/terms-and-conditions-modal.service';

@Component({
    selector: 'pib-mobile-prompt-pay-register',
    templateUrl: './pib-mobile-prompt-pay-register.html'
})
export class PIBMobilePromptPayRegisterComponent extends PromptPayRegisterComponent {
    public AnyIDType;
    swiperOptions: any;
    inputMenuSwiper = [];
    public acceptCond = false;
    public selectedAccount: any;
    public selectedAnyIDType = false;
    public stepWizard: StepWizard;
    private isError: boolean = false;

    constructor(public permissionAction: PermissionAction,
        public promptPayRegisterServiceMain: PromptPayRegisterServiceMain,
        public constants: Constants,
        public termAndConditionModalService: TermAndConditionModalService,
        public permissionChangeRoute: PermissionChangeRoute,
        public translate: TranslateService,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        public fb: FormBuilder,
        public orderBy: OrderByPipe,
        public utilService: UtilService) {
        super(permissionAction, promptPayRegisterServiceMain, constants, termAndConditionModalService, permissionChangeRoute, translate, fb, orderBy, utilService);

        this.stepwizardStyle = this.constants.STYLE_PIB_MOBILE
        this.selectedAccount = this.translate.instant('lbl.drpSelect');
        this.isAgree = false;
    }

    ngOnInit(): void {
        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }, { name: '3', label: 'stepWizard.complete' }],
                step: 0,
                style: this.stepwizardStyle
            }
        }
        this.registerPromptPayFormGroup = this.fb.group({
            'account': ['', [
                Validators.required,
            ]
            ],
            'anyIDType': ['', [
                Validators.required,
            ]
            ],
            'isAgree': false
        });

        this.inquiryMyAccount();
        let resultAnyID = this.promptPayRegisterServiceMain.registerAnyIDInfo;
        if(resultAnyID.responseMsg == this.constants.RESP_CODE_SUCCESS){
            this.resultAnyIDInfo = resultAnyID.resultAnyIDInfo;
        }else{
            this.isError = true;
            let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: resultAnyID.responseMsg,
                buttons: [{
                    text: this.translate.instant('button.ok'),
                    role: 'oky',
                    handler: () => {
                        this.permissionChangeRoute.changeRoute('KK_PRODUCT_SERVICE');
                    }
                }]
            });
        
            alert.present();
        }
        this.swiperOptions = { lock: false, index: 0 };

        for (let i in this.resultAnyIDInfo) {
            this.inputMenuSwiper.push({
                srcImg: this.resultAnyIDInfo[i].icon,
                type: this.resultAnyIDInfo[i].anyIDType,
                name: this.resultAnyIDInfo[i].descriptionEN,
                color: this.resultAnyIDInfo[i].iconColor,
                index: i
            });
        }
    }

    navToPromptPayRegisterConfirm() {
        this.permissionChangeRoute.changeRoute('KK_PRODUCT_SERVICE.REGISTER_KK_PROMPT_PAY_STEP2');
    }

    swiperChange(event) {
        this.registerPromptPayFormGroup.patchValue({ anyIDType: event.type });
        this.onChange();
    }

    openModal(selected: any) {
        let modal = this.modalCtrl.create(FromAccountList, { accTypeFilter: this.constants.ACCOUNT_TYPE_TD });

        modal.onDidDismiss(data => {
            if (data !== undefined && data !== null) {
                let myAccName = data.aliasName;
                let myAccNo = data.accNo;
                this.registerPromptPayFormGroup.patchValue({ account: data.accId });
                // this.selectedAccount = myAccName + '\n' + myAccNo
                this.selectedAccount = data;
            }
        });

        modal.present();
    }

    opentermAndCondModal() {
        let modal = this.modalCtrl.create(termAndConditionModal);
        modal.present();
    }

    toggleTermAndCond() {
        this.isAgree = !this.isAgree;
        this.registerPromptPayFormGroup.patchValue({ isAgree: this.isAgree });
    }

    private showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    public verifyPromptPayRegisterStep(): void {
        let messageAlert = '';

        if (this.registerPromptPayFormGroup.valid && this.registerPromptPayFormGroup.value.isAgree == true) {
            this.registerAnyIDConfirm();
        } else {
            if (this.registerPromptPayFormGroup.value.anyIDType === '') {
                messageAlert = this.translate.instant('label.warningSelectAnyID');
                this.showAlert(messageAlert);
            } else if (this.registerPromptPayFormGroup.value.account === '') {
                messageAlert = this.translate.instant('label.warningSelectAccount');
                this.showAlert(messageAlert);
            } else {
                messageAlert = this.translate.instant('label.warningAcceptTermAndCond');
                this.showAlert(messageAlert);
            }
        }
    }

    public registerAnyIDConfirm() {
        let anyIDType = this.registerPromptPayFormGroup.value.anyIDType;
        let myAccountID = this.registerPromptPayFormGroup.value.account;
        let param = { anyIDType, myAccountID };

        this.promptPayRegisterServiceMain.registerAnyIDConfirm(param).then((objResponse: any) => {
            this.result = objResponse.responseJSON.result;

            if (this.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                this.resultAnyIDConfirm = this.result.value;
                this.promptPayRegisterServiceMain.verifyTransactionId = this.resultAnyIDConfirm.verifyTransactionId;
                let checkRegister = this.resultAnyIDConfirm.register;

                if (checkRegister == true) { //temp data for route back
                    this.promptPayRegisterServiceMain.tempRegisterAnyIDData = {
                        tempRegisterAnyID: this.registerPromptPayFormGroup.value,
                        anyIDTypeNameSelected: this.anyIDTypeNameSelected,
                        resultAnyIDConfirm: this.resultAnyIDConfirm,
                        selectedAccount: this.selectedAccount
                    };
                    this.permissionChangeRoute.changeRoute('KK_PRODUCT_SERVICE.REGISTER_KK_PROMPT_PAY_STEP2');
                } else {
                    let messageAlert = this.result.responseStatus.errorMessage;
                    this.showAlert(messageAlert);
                }
            } else {
                let messageAlert = this.result.responseStatus.errorMessage;
                this.showAlert(messageAlert);
            }
        }, function (error) {
            
        });
    }
}

@Component({
    template: `
                <ion-header>
                    <ion-navbar>
                        <ion-title>
                            {{'label.termAndCondition'|translate}}
                        </ion-title>
                        <ion-buttons end>
                            <button ion-button icon-only (click)="dismiss()">
                                <ion-icon class="icon-large" name="close"></ion-icon>
                            </button>
                        </ion-buttons>
                    </ion-navbar>
                </ion-header>
                <ion-content>
                    <ion-list>
                        <terms-and-conditions [termAction]="termAction" [Style]="Style"></terms-and-conditions>
                    </ion-list>
                </ion-content>
            `
})
export class termAndConditionModal {
    name: string;
    Style: string = this.constants.STYLE_PIB_MOBILE;
    termAction: string = 'anyid_term_and_con';

    constructor(public viewCtrl: ViewController,
        public constants: Constants) { 
    }

    public dismiss() {
        this.viewCtrl.dismiss();
    }
}



