import { OnInit,ViewChild} from '@angular/core';
import { RequestToPayService } from '../request-to-pay/request-to-pay.service';
import { Constants } from '../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { qrGeneratorService } from './qr-generator.service';
import { CurrencyFormatterPipe } from '../../share/pipe/currency-formatter.pipe';
import { PermissionChangeRoute } from '../../share/service/permission.service';
import { AlertMessageComponent } from '../../share/component/alert-message/alert-message.component';
import { ModalController, AlertController} from 'ionic-angular';
import { FromAccountListModal } from '../request-to-pay/mobile/pib/add/from-account-list-modal.component';
import {PIBMobileQRGeneratorCompleteComponent } from './mobile/mobile-qr-generate-complete.component';
export class QRGeneratorBase{

    public message: string;
    public type: string;
    public myPromptPayAccount = {
        anyIDValue : '',
        anyIDType :{},
        accountName: ''
    };
    private model: any = {
        amount: "0.00",
        isDisableAmount: true
    };
    public amountText:string = "0.00";
    public hiddenInputText = false;
    //private isDisableAmount: boolean = true;

    constructor(
    protected requestToPayService: RequestToPayService,
    protected translate: TranslateService,
    protected constants: Constants,
    protected qrGeneratorService: qrGeneratorService,
    protected currencyFormatter: CurrencyFormatterPipe,
    protected permissionChangeRoute: PermissionChangeRoute,
    protected modalCtrl: ModalController,
    protected alertCtrl: AlertController
    ) {
    
    }


    goToNextStep(Form): void {


        if (this.myPromptPayAccount.anyIDValue == '') {
            this.showAlert(this.translate.instant('valErr.myPromptPayAccountRequire'))
        } 
        else  if (!this.model.isDisableAmount && this.model.amount == '0.00') {
            this.showAlert(this.translate.instant('valErr.amountRequire'))
        }
        else {

            if (this.myPromptPayAccount.anyIDValue !== '') {

                let promise = null
                let QRgenerateData = {
                    fromAnyIdType:   this.myPromptPayAccount.anyIDType,
                    fromAnyIdValue:  this.myPromptPayAccount.anyIDValue,
                    amount:  this.model.amount == '0.00'? null : this.currencyFormatter.parse(parseFloat(this.model.amount).toString()),
                };
                promise = this.qrGeneratorService.generateQRCodeData(QRgenerateData);

                if (promise != null) {
                    promise.then((result: any) => {
                        
                        if (typeof result.responseCode === 'undefined') {
                            let QRData = {
                                value: result.value,
                                myPromptPayAccount: this.myPromptPayAccount,
                                amount : this.model.amount,
                                isDisableAmount: this.model.isDisableAmount,
                                accountName: this.myPromptPayAccount.accountName
                            }
                            this.qrGeneratorService.setQRCodeData(QRData);                     
                            this.permissionChangeRoute.changeRoute('QR_GENERATOR_COMPLETE');
                        } else {
                            this.showAlert(result.errorMessage);
                        }
                    });
                }
            }
        }
    }

    protected showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    onFocus(event) {
        this.hiddenInputText = true;
        const value = this.currencyFormatter.parse(event.value);
        this.model.amount = value;
        this.amountText = value;
    }

    onBlur(event) {
        this.hiddenInputText = false;
        this.model.amount = this.currencyFormatter.transform(event.value,false);
        this.amountText = this.currencyFormatter.transform(event.value,true);
    }

    openModal(action: string, selected: any) {
        if (action == 'FROM_ACCOUNT_LIST') {
             let modal = this.modalCtrl.create(FromAccountListModal, { account: selected });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {
                    
                    this.myPromptPayAccount = {
                    anyIDValue : data.anyIDValue,
                    anyIDType : data.anyIDType,
                    accountName: data.accountName
                    };
                }
            });

            modal.present();
        }
    }

    chkModelChange($event){
        if($event){
           this.model.amount = "0.00";
           this.amountText = "0.00";
        }
    }
}


