import * as moment from 'moment';
import { Component, OnInit } from '@angular/core';
import { BankBean } from '../../../bean/bank-bean';
import { Dictionary } from '../../../bean/dictionary';
import { Constants } from '../../../service/constants';
import { AccountBean } from '../../../bean/account-bean';
import { TransferBean } from '../../../bean/transfer-bean';
import { Dateservice } from '../../../service/date.service';
import { UtilService } from '../../../service/util.service';
import { StrFormatPipe } from '../../../pipe/string-format.pipe';
import { TranslateService } from "ng2-translate/src/translate.service";
import { DropdownDataService } from '../../../service/dropdown.service';
import { MasterDataService } from '../../../service/master-data.service';
import { FundTransferService } from '../../../service/fund-transfer.service';
import { BankCodeDataService } from '../../../service/bankcode-data.service';
import { CurrencyFormatterPipe } from '../../../pipe/currency-formatter.pipe';
import { ModalController, NavController, AlertController } from 'ionic-angular';
import { MyAccountService } from '../../../../pages/my-account/my-account.service';
import { OtherAccountService } from '../../../../pages/other-account/other-account.service';
import { PermissionChangeRoute, PermissionService } from '../../../service/permission.service';
import { FundTransferStep2 } from '../../../../pages/fund-transfer/mobile/fund-transfer-step2';
import { ToAccountList } from '../../../component/to-account-list/mobile/to-account-list.component';
import { FromAccountList } from '../../../component/from-account-list/mobile/from-account-list.component';
@Component({
    selector: 'input-details',
    templateUrl: './input-details.html'
})

export class InputDetails implements OnInit {

    public model: any = {
        immediateType: this.constants.IMMEDIATE_TYPE_TODAY,
        scheduleType: 0,
        recurringType: this.constants.RECURRING_TYPE_NO,
        recurringTime: 0,
        amount: "0.00",
        mobileNumber: "",
        emailAddress: "",
        transferDate: "",
        note: "",
        notifyLanguage: this.constants.CULTURE_SHORTNAME_ENGLISH,
        isEmailChecked: false,
        isMobileChecked: false,
        tempNotifyMobileNo: "",
        tempNotifyEmail: "",
        rtpReferenceNo: ""
    };

    public amountText:string = "0.00";
    public hiddenInputText = false;

    public dict: Dictionary = {
        'fromAccount': 'lbl.transferFrom',
        'toAccount': 'lbl.transferTo',
        'amount': 'lbl.amount',
        'transferDate': 'lbl.transferDate',
        'scheduleType': 'lbl.frequency',
        'recurringTime': 'lbl.recurringTime',
        'emailAddress': 'lbl.notifyEmailAddress',
        'notifySMS': 'lbl.notifySMS',
        'mobileNumber': 'lbl.mobileNumber'
    };

    public dt: Date;
    protected today: any;
    protected minDate: any;
    protected maxDate: any;
    protected dateOptions: any;
    protected displayNames: any;
    protected scheduleTypes: any;
    protected recurringTypes: any;
    protected recurringTimes: any;
    private disabledEmail = true;
    private disabledMobile = true;
    public submitted: boolean = false;
    private transferObj: TransferBean = null;
    protected fromAccount: any = new AccountBean();
    private toAccount: any = new AccountBean();
    disableAmount:boolean = false;
    private isTodayOnly = false;

    protected fromScannerPage: boolean = false;

    constructor(
        public constants: Constants,
        public navCtrl: NavController,
        public dateService: Dateservice,
        public strFormat: StrFormatPipe,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public myAccountService: MyAccountService,
        public masterDataService: MasterDataService,
        public fundTransferService: FundTransferService,
        public dropdownDataService: DropdownDataService,
        public currencyFormatter: CurrencyFormatterPipe,
        public permissionChangeRoute: PermissionChangeRoute,
        public permissionService: PermissionService,
        public bankCodeDataService: BankCodeDataService,
        public otherAccountService: OtherAccountService,
        public utilService: UtilService
    ) {

        if (this.permissionChangeRoute.prevUrl === 'MY_ACCOUNT') {
            this.fromAccount = new AccountBean();
            let bank = new BankBean();
            bank.bankCode = this.myAccountService.selectAccountDetailData.bankCode;
            bank.bankName = this.myAccountService.selectAccountDetailData.bankName;
            bank.shortName = this.myAccountService.selectAccountDetailData.shortName;
            bank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(bank.bankCode);

            this.fromAccount.accId = this.myAccountService.selectAccountDetailData.myAccountID;
            this.fromAccount.accName = this.myAccountService.selectAccountDetailData.accName;
            this.fromAccount.accNo = this.myAccountService.selectAccountDetailData.myAccountNumber;
            this.fromAccount.accountType = this.myAccountService.selectAccountDetailData.accountType;
            this.fromAccount.aliasName = this.myAccountService.selectAccountDetailData.myAccountAliasName;
            this.fromAccount.balance = this.myAccountService.selectAccountDetailData.myAvailableBalance;
            this.fromAccount.benefitAcc = this.myAccountService.selectAccountDetailData.myLedgerBalance;
            this.fromAccount.bank = bank;

            this.transferObj = new TransferBean();
            this.transferObj.srcAccount = this.fromAccount;
            this.transferObj.destAccount = new AccountBean();

            this.permissionChangeRoute.prevUrl = null;
        } else if (this.permissionChangeRoute.prevUrl === 'OTHER_ACCOUNT') {
            this.transferObj = new TransferBean();
            this.transferObj.srcAccount = new AccountBean();
            this.toAccount = this.otherAccountService.selectAccountDetailData.accountBean;
            this.transferObj.destAccount = this.toAccount;
        } else if (this.permissionChangeRoute.targetAction === 'QR_READ') {
            this.fromScannerPage = true;
            this.permissionChangeRoute.targetAction = null;
        }
    }

    ngOnInit(): void {
        let isImmediateType = false;

        this.dateOptions = this.dateService.getMasterDates();
        this.displayNames = this.dict != null ? Object.keys(this.dict) : [];
        this.recurringTypes = this.dropdownDataService.recurringTypes;

        this.masterDataService.getRecurringTimeList().then((result: any) => {
            this.recurringTimes = result;
        });

        this.masterDataService.getAllScheduleList().then((result: any) => {
            this.scheduleTypes = result;
        });

        this.transferObj = this.fundTransferService.getTransferObj();

        if (this.transferObj != null) {


            this.fromAccount = this.transferObj.srcAccount;
            this.toAccount = this.transferObj.destAccount;
            this.model.immediateType = this.transferObj.immediateType || this.constants.IMMEDIATE_TYPE_TODAY;
            this.model.scheduleType = this.transferObj.scheduleType.schedTypeId || 0;
            this.model.recurringTime = this.transferObj.recurringTime !== undefined ? this.transferObj.recurringTime.time : 0;
            this.model.amount = this.currencyFormatter.transform(this.transferObj.transferAmount,false) || "0.00";
            this.amountText = this.model.amount;
            this.model.mobileNumber = this.transferObj.notifyMobileNo || "";
            this.disabledMobile = this.model.mobileNumber == "";
            this.model.isMobileChecked = !this.disabledMobile;
            this.model.emailAddress = this.transferObj.notifyEmail || "";
            this.disabledEmail = this.model.emailAddress == "";
            this.model.isEmailChecked = !this.disabledEmail;
            this.model.note = this.transferObj.note || "";
            this.model.editType = this.transferObj.editType || "";
            this.model.txnId = this.transferObj.txnId || "";
            this.model.txnMasId = this.transferObj.txnMasId || "";
            this.model.tempNotifyMobileNo = this.transferObj.tempNotifyMobileNo || "";
            this.model.tempNotifyEmail = this.transferObj.tempNotifyEmail || "";
            this.model.rtpReferenceNo = this.transferObj.rtpReferenceNo || "";

            if(this.model.rtpReferenceNo !== ''){
                this.disableAmount = false;
                this.isTodayOnly = true;
            }

            if (this.model.mobileNumber != "") {
                this.model.notifyLanguage = this.transferObj.notifyLang || this.constants.CULTURE_SHORTNAME_ENGLISH;
            }
            if(this.fromScannerPage){
                this.disableAmount = (this.amountText !== "0.00");
            }

            // Default recurring type value
            this.model.recurringType = this.constants.RECURRING_TYPE_NO;

            if (this.fromAccount.allowSchedule) {
                if (this.transferObj.recurringType !== undefined && this.model.editType != '0') {
                    this.model.recurringType = this.transferObj.recurringType;
                }

                if (this.model.immediateType === this.constants.IMMEDIATE_TYPE_LATER) {
                    this.dt = this.dateService.parseDate(this.transferObj.effectiveDate);
                    let bkkISODate = this.dateService.dtBKKISOSting(this.dt);
                    // this.dt.setMinutes(this.dt.getMinutes() + 480);
                    this.model.transferDate = bkkISODate;
                    isImmediateType = true;
                }
            }
        }

        this.masterDataService.getCurrentDate().then((result: any) => {
            let dt = result;
            // dt.setMinutes(dt.getMinutes() + 480);
            let bkkISODate = this.dateService.dtBKKISOSting(dt);
            this.today = this.dateService.formatDate(dt, 'DD/MM/YYYY');
            this.minDate = bkkISODate;
            this.maxDate = (this.isTodayOnly) ? this.minDate : moment(bkkISODate).add(180, 'days').toDate().toISOString();

            if (!isImmediateType) {
                this.dt = dt;
                this.model.transferDate = bkkISODate;
            }
        });
    }

    openModal(action: string, selected: any) {
        if (action == 'FROM_ACCOUNT_LIST') {
            let modal = this.modalCtrl.create(FromAccountList, { account: selected });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {
                    //
                    this.fromAccount = data;
                }
            });

            modal.present();
        }
        if (action == 'TO_ACCOUNT_LIST' && this.model.rtpReferenceNo === '' && !this.fromScannerPage) {
            let modal = this.modalCtrl.create(ToAccountList, { account: selected });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {
                    //
                    this.toAccount = data;
                    this.model.emailAddress = this.toAccount.email || "";
                    this.model.mobileNumber = this.toAccount.mobileNo || "";
                    this.disabledEmail = this.model.emailAddress == "";
                    this.model.isEmailChecked = !this.disabledEmail;
                    this.disabledMobile = this.model.mobileNumber == "";
                    this.model.isMobileChecked = !this.disabledMobile;
                }
            });

            modal.present();
        }
    }

    onEmailCheckedChanged(event) {
        //

        if (event.checked) {
            this.disabledEmail = false;
            this.model.emailAddress = this.model.emailAddress != '' ? this.model.emailAddress : this.model.tempNotifyEmail;
        } else {
            this.disabledEmail = true;
            this.model.tempNotifyEmail = this.model.emailAddress;
            this.model.emailAddress = "";
        }
    }

    onMobileCheckedChanged(event) {
        //

        if (event.checked) {
            this.disabledMobile = false;
            this.model.mobileNumber = this.model.mobileNumber != '' ? this.model.mobileNumber : this.model.tempNotifyMobileNo;
        } else {
            this.disabledMobile = true;
            this.model.tempNotifyMobileNo = this.model.mobileNumber;
            this.model.mobileNumber = "";
        }
    }

    /**
     * check notifyinput change model value
     */
    chkModelChange(val, notifyType) {
        let inputVal = val;
        if (inputVal === "") {
            switch (notifyType) {
                case "email":
                    this.model.tempNotifyEmail = this.model.emailAddress;
                    break;
                case "mobileNumber":
                    this.model.tempNotifyMobileNo = this.model.mobileNumber;
                    break;
                default:
                //no default
            }
        }
    }

    goToNextStep(transferForm): void {
        let data = transferForm.value;
        let valid = transferForm.valid;
        let validationResult = this.validateTransferForm(transferForm);

        if (!validationResult.valid) {
            this.showAlert(this.strFormat.transform(validationResult.message, [this.dict[validationResult.displayName]]));
        } else {
            this.submitted = true;


            if (this.fromAccount.accNo !== undefined && this.toAccount.accNo !== undefined && valid) {

                let promise = null;
                let scheduleType = data.scheduleType || 0;
                let recurringTime = data.recurringTime || 0;
                
                let effectiveDate =  this.dateService.kkTransferDateFormat(this.model.transferDate);
    
                let transferObj = new TransferBean();
                transferObj.srcAccount = this.fromAccount;
                transferObj.destAccount = this.toAccount;
                transferObj.transferAmount = +this.currencyFormatter.parse((data.amount) ? data.amount: this.model.amount);
                transferObj.effectiveDate = effectiveDate;
                transferObj.notifyEmail = data.emailAddress || "";
                transferObj.notifyMobileNo = data.mobileNumber || "";
                transferObj.notifyLang = transferObj.notifyMobileNo != "" ? data.notifyLanguage : "";
                transferObj.recurringTime = this.recurringTimes.find(x => x.time == recurringTime) || {};
                transferObj.note = data.note;
                transferObj.scheduleType = this.scheduleTypes.find(x => x.schedTypeId == scheduleType) || {};
                transferObj.editType = data.editType || "";
                transferObj.txnId = data.txnId || "";
                transferObj.txnMasId = data.txnMasId || "";
                transferObj.tempNotifyEmail = this.model.tempNotifyEmail;
                transferObj.tempNotifyMobileNo = this.model.tempNotifyMobileNo;
                transferObj.immediateType = this.today == effectiveDate ? this.constants.IMMEDIATE_TYPE_TODAY : this.constants.IMMEDIATE_TYPE_LATER;

                if (this.fromAccount.allowSchedule && this.model.recurringType !== undefined && this.model.editType != '0') {
                    transferObj.recurringType = data.recurringType;
                } else {
                    transferObj.recurringType = this.constants.RECURRING_TYPE_NO
                }

                transferObj.rtpReferenceNo = data.rtpReferenceNo || "";
                transferObj.scanFlag = (this.transferObj) ? this.transferObj.scanFlag : "N";

                if (transferObj.rtpReferenceNo !== '' || transferObj.editType == "") {
                    promise = this.fundTransferService.verifyFundTransfer(transferObj);
                } else {
                    promise = this.fundTransferService.verifyEditFundTransfer(transferObj);
                }

                if (promise != null) {
                    promise.then((result: any) => {
                        if (typeof result.responseCode === "undefined") {
                            this.fundTransferService.setTransferObj(result);
                            this.navCtrl.push(FundTransferStep2);
                        } else {
                            //
                            let messageAlert = result.errorMessage;
                            this.showAlert(messageAlert);
                        }
                    });
                }
            }
        }
    }

    protected showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    protected validateTransferForm(form: any): any {
        let fromAccount = this.fromAccount;
        let toAccount = this.toAccount;
        let validationResult = { valid: true, displayName: '', message: '' };

        this.displayNames.forEach(function (name, index) {
            if (validationResult.valid) {
                if (form.controls[name] != undefined && form.controls[name] != null && form.controls[name].errors != null) {
                    for (let err in form.controls[name].errors) {
                        if (['min', 'required'].indexOf(err) != -1) {
                            if (name == 'mobileNumber') name = "notifySMS";
                            validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                            break;
                        }
                        if (['pattern'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.invalid' };
                            break;
                        }
                    }
                }
                if (name == 'fromAccount' && fromAccount.accNo === undefined) {
                    validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                }
                if (name == 'toAccount' && fromAccount.accNo !== undefined && toAccount.accNo === undefined) {
                    validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                }
            }
        });

        return validationResult;
    }

    onFocus(event) {
        this.hiddenInputText = true;
        const value = this.currencyFormatter.parse(event.value);
        this.model.amount = value;
        this.amountText = value;
    }

    onBlur(event) {
        this.hiddenInputText = false;
        this.model.amount = this.currencyFormatter.transform(event.value,false);
        this.amountText = this.currencyFormatter.transform(event.value,true);
    }
}



