import { Constants } from '../../../service/constants';
import { AccountBean } from '../../../bean/account-bean';
import { Component, OnInit } from '@angular/core';
import { AccountService } from '../../../service/account.service';
import { Platform, NavParams, ViewController, AlertController } from 'ionic-angular';
import { TranslateService } from "ng2-translate/src/translate.service";
@Component({
    selector: 'from-account-list',
    templateUrl: './from-account-list.html'
})

export class FromAccountList implements OnInit {
    private accounts: any = [];
    public firstLoad: boolean = true;
    private currentAccount: AccountBean;
    
    constructor(
        public params: NavParams,
        public platform: Platform,
        public constants: Constants,
        public viewCtrl: ViewController,
        public accountService: AccountService,
        public alertCtrl: AlertController,
        public translateService: TranslateService
    ) {
        // 
    }

    ngOnInit(): void {
        this.currentAccount = this.params.get('account') || new AccountBean();
        // 
        this.accountService.getMyAccounts().then((result: any) => {
            this.accounts = result;
            if(result.errorMessage != undefined){
                this.showAlert(result.errorMessage);
            } else {
                this.orderAndFilterAccount();
            }
            this.firstLoad = false;
        });
    } 

    orderAndFilterAccount(){
        
        this.orderByAliasName(this.accounts, '');
        if(this.params.get('accTypeFilter') != undefined ){this.accounts = this.accounts.filter(x => x.name != this.params.get('accTypeFilter'));}
        if(this.params.get('accountNoFilter') != undefined ){
            this.accounts = this.accounts.filter(x => x.accNo != this.params.get('accountNoFilter'));}
        if(this.accounts.length > 0) { this.toggleGroup(this.accounts[0]); }
    }

    onSelectChanged(newValue) {
        // 
        this.viewCtrl.dismiss(newValue);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    toggleGroup(group) {        
        group.show = !group.show;
    }

    isGroupShown(group) {
        return group.show;
    }

    orderByAliasName(array: Array<any>, args: any): Array<any> {
        array.sort((a: any, b: any) => {
        var nameA = a.aliasName && a.aliasName.toLowerCase() || "";
        var nameB = b.aliasName && b.aliasName.toLowerCase() || "";

        if (nameA < nameB) {
            return -1;
        } else if (nameA > nameB) {
            return 1;
        } else {
            return 0;
        }
        });
        return array;
    }

    showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translateService.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translateService.instant('btn.close')]
        });
        alert.present();
    }
}


