import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { UtilService } from '../../../../share/service/util.service';
import { MyAccountDetailComponent } from '../../../../pages/my-account/my-account-detail.component';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';

@Component({
    selector: 'my-account-detail-mobile',
    templateUrl: './my-account-detail.html'
})

export class PIBMobileMyAccountDetailComponent extends MyAccountDetailComponent {
    constructor(public navCtrl: NavController,
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public router: Router,
        public permissionService: PermissionService,
        public utilService: UtilService) {
        super(constants, permissionChangeRoute, router, permissionService, utilService);
    }
}


