import { AlertController, NavController, ItemSliding } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MyAccountListComponent } from '../../my-account-list.component';
import { MyAccountService } from '../../../../pages/my-account/my-account.service';
import { Component, trigger, animate, style, state, transition, NgZone } from '@angular/core';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { OrderByPipe } from '../../../../share/pipe/order-by.pipe';

declare var BUILD_NUM;

@Component({
  selector: 'my-account-list-mobile',
  templateUrl: './my-account-list.html',
  // animations: [
  //   trigger('accordingState', [
  //     state('collapsed', style({
  //       height: '0px'
  //     })),
  //     state('expanded', style({
  //       height: '*'
  //     })),
  //     transition('collapsed <=> expanded', [animate(200)])
  //   ])
  // ]
})
// Component class
export class PIBMobileMyAccountListComponent extends MyAccountListComponent {
  public BUILD_NUM = BUILD_NUM;
  permissionManage: any;
  public according: any = {};
  public data: Array<any> = [];
 
  constructor(public constants: Constants,
    public myAccountService: MyAccountService,
    public alertCtrl: AlertController,
    public translate: TranslateService,
    public permissionChangeRoute: PermissionChangeRoute,
    public translateService: TranslateService,
    public nav: NavController,
    public permissionService: PermissionService,
    public zone: NgZone,
    public orderBy: OrderByPipe) {
    super(constants, myAccountService, translateService, zone, orderBy);

    this.permissionManage = {
      delete: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS,
      moreInfo: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS
    };
  }

  toggleState(selectState) {
    //
    this.according[selectState] = (this.according[selectState] === 'expanded') ? 'collapsed' : 'expanded';
  }

  selectAccountPermission(account, slidingItem: ItemSliding) {
    slidingItem.close();
    if (this.permissionManage.moreInfo) {
      this.onClickSelectAccount(account);
    }
  }

  showConfirm(myAccount: any, slidingItem: ItemSliding) {
    let confirm = this.alertCtrl.create({
      title: this.translate.instant('label.delete.title.myaccount'),
      message: this.translate.instant('lbl.deleteMyAccountMsg'),
      buttons: [{
        text: this.translate.instant('button.cancel'),
        handler: () => {
          
        }
      },
      {
        text: this.translate.instant('button.ok'),
        handler: () => {
          slidingItem.close();
          this.deleteMyAccount(myAccount);
        }
      }]
    });

    confirm.present();
  }

  public deleteMyAccount(myAccount: any) {
    this.myAccountService.deleteMyAccount(myAccount).then((objResponse: any) => {
      let result = objResponse.responseJSON.result;
      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {

        let alert = this.alertCtrl.create({
          title: this.translate.instant('label.Success'),
          subTitle: this.translate.instant('label.deleteMyAccountSuccess', { myAccountNumber: myAccount.myAccountNumber }),
          buttons: [{
            // text: this.translate.instant('button.ok'),
            text: this.translate.instant('button.backToHome'),
            role: 'cancel',
            handler: () => {
              this.onModalHidden();
            }
          }]
        });

        alert.present();
      }
    });
  }

  onModalHidden() {
    this.myAccountService.newMyAccountData = undefined;
    // this.permissionChangeRoute.changeRoute('MY_ACCOUNTS');
    this.nav.popToRoot();
  }

  public getDashboard(): void {
    this.myAccountService.requestDashboard().then((objResponse: any) => {
      this.firstLoad = false;
      let result = objResponse.responseJSON.result;
      //

      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
        this.accountLists = this.orderBy.aliasName(result.value, '');
        //this.initialAccountAccordingData();
      }
    }, function (error) {
      
    });
  }

  public getMyAccount(): void {
    this.myAccountService.requestInquiryMyAccount().then((objResponse: any) => {
      this.firstLoad = false;
      let result = objResponse.responseJSON.result;
      //

      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
        this.accountLists = this.orderBy.aliasName(result.value, '');
        //this.initialAccountAccordingData();
      }
    }, function (error) {
      
    });
  }

  private initialAccountAccordingData() {
    let newData = this.splitAccountList();
    //

    for (let i = 0; i < newData.stateList.length; i++) {
      let accordingData = {};

      if (newData.stateList[i].code === this.constants.ACCOUNT_TYPE_SA) {
        accordingData["stateName"] = newData.stateList[i].name;
        accordingData["accounts"] = newData.savingAccount;
      } else if (newData.stateList[i].code === this.constants.ACCOUNT_TYPE_CA) {
        accordingData["stateName"] = newData.stateList[i].name;
        accordingData["accounts"] = newData.currentAccount;
      } else if (newData.stateList[i].code === this.constants.ACCOUNT_TYPE_TD) {
        accordingData["stateName"] = newData.stateList[i].name;
        accordingData["accounts"] = newData.termAccount;
      }

      this.data.push(accordingData);
    }
    //
  }

  private splitAccountList(): any {
    let savingAccount = [];
    let currentAccount = [];
    let termAccount = [];
    let arrState = [];

    for (let i = 0; i < this.accountLists.length; i++) {
      let accountTypeLabel;
      let accountList = this.accountLists[i];
      let stateName = accountList.accountType;

      if (stateName === this.constants.ACCOUNT_TYPE_SA) {
        savingAccount.push(accountList);
        accountTypeLabel = 'label.savingsaccount';
      } else if (stateName === this.constants.ACCOUNT_TYPE_CA) {
        currentAccount.push(accountList);
        accountTypeLabel = 'label.currentAccounts';
      } else if (stateName === this.constants.ACCOUNT_TYPE_TD) {
        termAccount.push(accountList);
        accountTypeLabel = 'label.tdAccounts';
      }

      if (this.according[accountTypeLabel] === undefined) {
        this.according[accountTypeLabel] = 'expanded';
        arrState.push({ code: stateName, name: accountTypeLabel });
      }
    }

    return { "stateList": arrState, "savingAccount": savingAccount, "currentAccount": currentAccount, "termAccount": termAccount };
  }

  private _itemSliding: any = null;
  itemSliding(slidingItem: ItemSliding){
    
    this._itemSliding = slidingItem;
  }

  hideItemSliding(){
    
    if(this._itemSliding !== null){
      this._itemSliding.close();
    }
  }
}



