import { LandingPage } from '../../landing-page';
import { Component, OnInit, NgZone } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import { GetinformationService } from '../../../../share/service/getInformation.service';
import { AlertController, ModalController, NavController } from 'ionic-angular';
import { TranslateService } from "ng2-translate/src/translate.service";
import { SanitizeHtmlPipe } from '../../../../share/pipe/sanitize-html.pipe';
import { PIBMobileAcceptTermsAndConditionComponent } from "../../../../share/component/terms-and-conditions/mobile/accept-terms-and-conditions-modal.component";
@Component({
    template: `
              <ion-header>
              </ion-header>
              <ion-content>
                  <div class="padd-content">
                    <div *ngIf="result !== undefined" [innerHTML]="result | sanitizeHtml"></div>
                    <!--<button *ngIf="!isHide" ion-button full (click)="dismiss()">Close landing page</button>-->
                  </div>
              </ion-content>
            `
})
export class ModalContentPage implements OnInit {
    public isHide: boolean;
    public type: string;
    public message: string;
    public result: SanitizeHtmlPipe;
    public actionCodeService: string = 'landing_page_pib';
    constructor(public getinformationService: GetinformationService,
        public constants: Constants,
        public platform: Platform,
        public params: NavParams,
        public viewCtrl: ViewController,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public zone: NgZone,
        public modalCtrl: ModalController,
        public navController: NavController,
    ) {
        
        this.isHide = this.params.get('isHide') || false;
    }
    ngOnInit(): void {
        this.inquiryLandingPage();
    }
    dismiss(): void {
        this.viewCtrl.dismiss();
    }

    inquiryLandingPage(): void {
        let challengeResult = this.getinformationService.getChallengeResult();

        this.getinformationService.getInformation("getLandingPagePibProcedure", this.actionCodeService).then((objResponse: any) => {
            let alertPopup;
            if (objResponse.responseJSON.result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                this.result = objResponse.responseJSON.result.value.data;
                if (challengeResult != null) {
                    if(challengeResult.responseCode != 'RIB-E-KKP009'){
                        //Case general (KKP009 is Demo apple case)
                        this.message = challengeResult.errorMessage;
                        this.type = 'warning';
                        alertPopup = this.alertCtrl.create({
                            title: this.type,
                            subTitle: this.message,
                            buttons: [{
                                text: 'Ok',
                                handler: () => {
                                }
                            }]
                            
                        });
                        alertPopup.present();
                    }
                    this.getinformationService.setChallengeResult(null);
                } else if(challengeResult === null){
                    this.showTermAndCondition();
                }
            }
        }, function (error) {

        });
    }

    private showTermAndCondition(){
        let modal = this.modalCtrl.create(PIBMobileAcceptTermsAndConditionComponent);
        modal.present();
        modal.onDidDismiss(data => {
            if (data === 'agree') {
                this.navController.pop();
            }
        });
    }
}



