import { UtilService } from '../../../../share/service/util.service';
import { Component, OnInit, AfterViewInit, NgZone, OnDestroy } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { BillPaymentBean } from '../../../../share/bean/bill-payment-bean';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { GetinformationService } from '../../../../share/service/getInformation.service';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import {BillPaymentService} from "../../bill-payment.service";

declare var BUILD_NUM;

@Component({
    selector: 'pib-mobile-transaction-detail',
    templateUrl: './pib-mobile-transaction-detail.html'
})

export class PIBMobileTransactionDetail implements OnInit, AfterViewInit, OnDestroy {
    private billPaymentObj: BillPaymentBean = null;
    public BUILD_NUM = BUILD_NUM;
    private userCardType: string;
    
    constructor(
        public constants: Constants,
        private platform: Platform,
        public params: NavParams,
        public viewCtrl: ViewController,
        public bankCodeDataService: BankCodeDataService,
        private ngZone: NgZone,
        private getinformationService: GetinformationService,
        public billPaymentService: BillPaymentService,
        public utilService: UtilService
    ) {
        window['angularComponentRef'] = {component: this, zone: ngZone};
    }

    ngOnInit(): void {
        this.billPaymentObj = this.params.get('payment') || new BillPaymentBean();
        console.log('this.billPaymentObj', this.billPaymentObj);
        this.billPaymentService.getCustomerType().then((result: any) => {
            this.userCardType = result;
        });
        // create outside the app
        window['angularComponentRef'] = {
            publicFunc: this.publicFunc.bind(this)
        };
    }

    ngAfterViewInit(){
    }

    ngOnDestroy() {
        window['angularComponent'] = null;
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    captureScreen(){

        setTimeout(() => {
            let userAgentInfo = this.getinformationService.userAgentInfo || {};
            this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'capturescreen');
        }, 500);
    }

    loadImageSuccess(){
        this.captureScreen();
    }
    
    loadImageError(){
        this.captureScreen();
    }
    
    publicFunc() {
        this.ngZone.run(() => this.dismiss());
    }

    isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }

    isCitizenCardType() {
        return this.userCardType === 'I';
    }
}



