import { ModuleWithProviders }  from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { Authentication } from '../pages/login/pib/mobile/authentication';
// Route Configuration
export const routesPibMobile: Routes = [
  { path: 'authentication/:token/:language', component: Authentication },
  // { path: 'login', component: LoginComponent },
   { path: '**', redirectTo: '#/authentication/555555/en' }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(routesPibMobile);


