import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { RequestToPayListPageComponent } from '../../web/request-to-pay-list-page.component';
import { BillPaymentService } from '../../../bill-payment/bill-payment.service';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { RequestToPayService } from '../../request-to-pay.service'
import { MasterDataService } from '../../../../share/service/master-data.service';
import { FundTransferService } from '../../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { NotificationService } from "../../../../share/service/notification.service";
import { AlertController, NavController } from 'ionic-angular';

@Component({
    selector: 'request-to-pay-list-page',
    templateUrl: './request-to-pay-list-page.html'
})
export class PIBMobileRequestToPayListPageComponent extends RequestToPayListPageComponent implements OnInit {

    selectOptions: { title: string; };
    menuName: String;
    constructor(
        public constants: Constants,
        public translate: TranslateService,
        public permissionService:PermissionService,
        public requestToPayService:RequestToPayService,
        public billPaymentService:BillPaymentService,
        public permissionChangeRoute:PermissionChangeRoute,
        public fundTransferService: FundTransferService,
        public bankCodeDataService: BankCodeDataService,
        public masterDataService: MasterDataService,
        public notificationService: NotificationService,
        private alertCtrl: AlertController,
        private navCtrl: NavController
    ) {
        super(constants, translate,permissionService,requestToPayService,billPaymentService,permissionChangeRoute,fundTransferService,bankCodeDataService,masterDataService,notificationService);
    }

    ngOnInit(): void {

        this.selectOptions = {
            title: this.translate.instant('label.selectPeriod')
        };

         this.timeline = { 
            data: {
                rawData: [],
                isAdded: false
            },
            title: ''
        };


        if (this.permissionService.getMenuName()==='RTP_RECEIVE'){
            this.inquiryRequestToPay();
            this.menuName = 'RTP_RECEIVE';
        }else if (this.permissionService.getMenuName()==='MY_RTP'){
            this.inquiryRequestToPayOutgoing();
            this.menuName = 'MY_RTP';
        }
    }

    onActions(data: any): void {

         let tmpdata = data.data;
        if (data.name === 'info') {
           this.requestToPayService.SelectedRTPIncomingData = tmpdata.rawData;
           this.permissionChangeRoute.changeRoute('RTP_RECEIVE_DETAIL');
        } else if (data.name === 'delete'){
            this.showConfirmDeletePopup(tmpdata);
        } else if (data.name  == 'block'){
            this.showConfirmBlockAccount(tmpdata);
        }
    }

    addRequestToPay(){
        this.permissionChangeRoute.changeRoute('MY_RTP.ADD');
    }

    onError(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert.errorMessage,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    showConfirmDeletePopup(tmpdata) {
        let confirm = this.alertCtrl.create({
            title: this.translate.instant('title.confirmDeleteRTPIncomingMsg'),
            subTitle: this.translate.instant('body.confirmDeleteRTPIncomingMsg'),
            buttons: [{
                text: this.translate.instant('button.cancel'),
                handler: () => {
                }
            },
            {
                text: this.translate.instant('button.ok'),
                handler: () => {
                  this.deleteRTPIncomingData(tmpdata);
                }
            }]
        });

        confirm.present();
    }
    
     showConfirmBlockAccount(tmpdata){
        let confirm = this.alertCtrl.create({
        title: this.translate.instant('title.confirmBlockAccMsg'),
        message: this.translate.instant('body.confirmBlockAccMsg'),
            buttons: [{
            text: this.translate.instant('button.cancel'),
            handler: () => {

            }
            },
            {
            text: this.translate.instant('button.ok'),
            handler: () => {
                this.createRequestToPayBlocklist(tmpdata);
            }
            }]
        });
        confirm.present();
    }

    
    public deleteRTPIncomingData(tmpdata: Object){
        
        this.requestToPayService.deleteRTPIncoming(tmpdata).then((result: any) => {
            if(result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS){
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.Success'),
                    subTitle: this.translate.instant('label.title.deleteMyAccountRTPReceiveSuccess'),
                    buttons: [{
                        text: this.translate.instant('button.backToHome'),
                        handler: () => {
                            this.inquiryRequestToPay();
                            this.navCtrl.popToRoot();
                        }
                    }]
                });

                alert.present();
            }else{
                 this.onError(result.responseStatus);
            }

        }, function (error) {

        });
    }

    private createRequestToPayBlocklist(tmpdata){
        let createRTPBlocklistData = {
                name: tmpdata.requesterAccountName,
                type: tmpdata.requesterIdType,
                value: tmpdata.requesterIdValue
        }
        this.requestToPayService.createRequestToPayAnyIdBlocklist(createRTPBlocklistData).then((result: any) => {
            if(result.errorMessage == undefined){
                this.showAlert(this.translate.instant('block.successMsg'),this.translate.instant('label.Success'));
            }else{
                this.showAlert(result.errorMessage , this.translate.instant('label.warning'));
            }

        }, function (error) {

        });
    }


     private showAlert(messageAlert, title) {
      let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
  }
    
}



