import { Constants } from '../../../service/constants';
import { TransferBean } from '../../../bean/transfer-bean';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { TranslateService } from "ng2-translate/src/translate.service";
import { TransferDecisionBean } from '../../../bean/transfer-decision-bean';
import { FundTransferService } from '../../../service/fund-transfer.service';
import { BankCodeDataService } from '../../../service/bankcode-data.service';
import { ModalController, NavController, AlertController } from 'ionic-angular';
import { PermissionChangeRoute, PermissionService } from '../../../service/permission.service';
import { TransactionDetail } from './transaction-detail.component'

@Component({
    selector: 'complete',
    templateUrl: './complete.html'
})

export class Complete implements OnInit, OnDestroy {
    
    authorized: any;
    transferObj: TransferBean = new TransferBean();
    isWaitToSaveSlip = true;
    private decision: TransferDecisionBean = new TransferDecisionBean();

    constructor(
        public constants: Constants,
        public navCtrl: NavController,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public permissionChangeRoute: PermissionChangeRoute,
        public bankCodeDataService: BankCodeDataService,
        public fundTransferService: FundTransferService,
        public permissionService: PermissionService
    ) { }

    ngOnInit(): void {
        this.transferObj = this.fundTransferService.getTransferObj();
        let transferDecisionList = this.transferObj.transferDecisionList;
        this.authorized = {
            printSlip: this.permissionService.getActionCode().PRINT_SLIP_TRANSFER,
            makeTransaction: this.permissionService.getActionCode().FUND_TRANSFER
        };

        // Assign an initial value to fee detail radio button by default.
        if (transferDecisionList != null && transferDecisionList.length > 0) {
            this.decision = transferDecisionList[0];
        }

        setTimeout(() => {
            if (this.transferObj.isAllowPrint) {
                this.openModal('VIEW_TRANSACTION_DETAIL', this.transferObj);
            }else {
                this.isWaitToSaveSlip = false;
            }
        }, 1000);
    }

    ngOnDestroy(): void {
        this.fundTransferService.setTransferObj(null);
    }

    newTransfer() {
        this.fundTransferService.setTransferObj(null);
        this.permissionChangeRoute.targetAction = "NEW";
        this.permissionChangeRoute.pushRoute("FUND_TRANSFER");
    }

    openModal(action: string, selected: any) {
        if (action == 'VIEW_TRANSACTION_DETAIL') {
            let modal = this.modalCtrl.create(TransactionDetail, { transfer: selected });
            modal.present();
            this.isWaitToSaveSlip = false;
        }
    }

    showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });
        
        alert.present();
    }
}



