import { QRGeneratorCompleteBase } from '../qr-generator-complete-base';
import { Constants } from '../../../share/service/constants';
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { qrGeneratorService } from '../qr-generator.service';
import { CurrencyFormatterPipe } from '../../../share/pipe/currency-formatter.pipe';
import { NavController, ModalController } from 'ionic-angular';

declare var BUILD_NUM;

@Component({
    selector: 'pib-mobile-qr-generator-complete',
    templateUrl: './qr-generator-complete-mobile.html'
})

export class PIBMobileQRGeneratorCompleteComponent extends QRGeneratorCompleteBase implements OnInit, AfterViewInit {
    public BUILD_NUM = BUILD_NUM;
    private stepWizard: any;
    
    constructor(
    protected qrGeneratorService: qrGeneratorService,
    protected navCtrl: NavController,
    private constants: Constants,
    protected CurrencyFormatterPipe: CurrencyFormatterPipe,
    protected modalCtrl: ModalController
    )
     {
        super(qrGeneratorService,navCtrl,CurrencyFormatterPipe,modalCtrl);
    }

    public ngOnInit() {
        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.complete' }],
                step: 1,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
        
        this.initial();
    }

    public ngAfterViewInit(){
        this.getLogoPosition();
    }
}



