import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { Dateservice } from '../../../../share/service/date.service';
import { TranslateService } from 'ng2-translate/src/translate.service';
import { MasterDataService } from '../../../../share/service/master-data.service';
import { FundTransferService } from '../../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { ModalController } from 'ionic-angular';
import { TransactionDetail } from '../../../../share/component/step-process/mobile/transaction-detail.component';
import { BankBean } from '../../../../share/bean/bank-bean';
import { AccountBean } from '../../../../share/bean/account-bean';
import { TransferBean } from '../../../../share/bean/transfer-bean';
import { ScheduleTypeBean } from '../../../../share/bean/schedule-type-bean';
import { RecurringTimeBean } from '../../../../share/bean/recurring-time-bean';
import { TransferDecisionBean } from '../../../../share/bean/transfer-decision-bean';
import { TransferStatusBean } from '../../../../share/bean/transfer-status-bean';
import { AnyIDTypeBean } from '../../../../share/bean/anyid-type-bean';
import { PermissionService } from '../../../../share/service/permission.service';
import {QrScannerService} from "../../../qr-scanner/qr-scanner.service";

@Component({
    selector: 'transaction-history-detail-page',
    templateUrl: './transaction-history-detail-page.html'
})
export class TransactionHistoryDetailPageComponent implements OnInit {
    detailData: any;
    anyIDTypeList: any;
    toBankCode: string;
    fromBankCode: string;
    anyIDFromDesc: string;
    authorized: any;
    qrData = null;

    constructor(
        public constants: Constants,
        public fundTransferService: FundTransferService,
        public translate: TranslateService,
        public bankCodeDataService: BankCodeDataService,
        public dateservice: Dateservice,
        public masterDataService: MasterDataService,
        public modalCtrl: ModalController,
        public permissionService: PermissionService,
        public qrScannerService: QrScannerService) {
    }

    ngOnInit(): void {
        this.detailData = this.fundTransferService.getTransferObj();
        this.anyIDTypeList = this.masterDataService.getAnyIDTypeList();
        this.toBankCode = (this.detailData.bankCode) ? this.detailData.bankCode : this.constants.KKP_BANK_CODE;
        this.fromBankCode = (this.detailData.fromBankCode) ? this.detailData.fromBankCode : this.constants.KKP_BANK_CODE;
        this.anyIDFromDesc = (this.detailData.anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) ? this.detailData.bankName : this.anyIDTypeList[this.detailData.anyIDType].desc;
        
        this.authorized = {
            printSlip: this.permissionService.getShortcutActionCodePermission('FUND_TRANSFER','PRINT_SLIP_TRANSFER'),
        };
        console.log('this.detailData' , this.detailData);
    }

    beforeOpenModal() {
        if (this.detailData.transactionRef && this.detailData.canGenQrSlip) {
            this.qrScannerService.generateTransferQRVerifySlipProcedure(this.detailData).then((objResponse: any) => {
                if (objResponse.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                    this.qrData = objResponse.value;
                }
                this.openModal();
            });
        }else {
            //not show qr Data
            this.openModal();
        }
    }

    openModal(){

        let srcAccountObj = new AccountBean();

                let fromBank = new BankBean();
                fromBank.bankCode = this.detailData.fromBankCode||"";
                fromBank.bankName = this.detailData.fromBankName||"";
                fromBank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(fromBank.bankCode);

                srcAccountObj.accId = this.detailData.fromAccountID||"";
                srcAccountObj.accName = this.detailData.fromAccountName||"";
                srcAccountObj.accNo = this.detailData.fromAccountNumber||"";
                srcAccountObj.aliasName = this.detailData.fromAliasName||"";
                srcAccountObj.bank = fromBank;



                let destAccount = new AccountBean();
                destAccount.accId =this.detailData.toAccountID||"";
                destAccount.accNo = this.detailData.toAccountNumber||"";
                destAccount.accName = this.detailData.toAccountName||"";
                destAccount.aliasName = this.detailData.toAliasName||"";
                destAccount.category = this.detailData.toCategoryID||"";
                let anyIDType = this.anyIDTypeList[this.detailData.anyIDType] || new AnyIDTypeBean();
                destAccount.anyIDType = anyIDType;

                let destBank = new BankBean();
                destBank.bankCode = this.detailData.bankCode||"";
                destBank.bankName = this.detailData.bankName||"";
                destBank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(destBank.bankCode);
                destAccount.bank = destBank;

                let transferObj = new TransferBean();
                transferObj.srcAccount = srcAccountObj;
                transferObj.destAccount = destAccount;

                let scheduleType = new ScheduleTypeBean();
                scheduleType.schedTypeId = this.detailData.scheduleType || 0;

                let recurringTime = new RecurringTimeBean();
                recurringTime.time = this.detailData.recurringTimes || 0;
                recurringTime.desc = recurringTime.time + " " + (recurringTime.time == 0 || recurringTime.time == 1 ? this.translate.instant("lbl.time") : this.translate.instant("lbl.times"));
                
                transferObj.scheduleType = scheduleType;
                transferObj.recurringTime = recurringTime;

                //transferObj.effectiveDate = this.detailData.transferDate;
                transferObj.notifyLang = this.detailData.msgLanguage|| "";
                transferObj.notifyMobileNo = this.detailData.toMobileNumber|| "";
                transferObj.notifyEmail = this.detailData.toEmail|| "";
                transferObj.refNo = this.detailData.referenceNumber|| "";
                transferObj.txnDate = this.detailData.transactionDate|| "";
                transferObj.recvDate = this.detailData.ReceiveDate || "";
                transferObj.note = this.detailData.memo|| "";
                transferObj.transferAmount = this.detailData.amount|| "";
                transferObj.txnId = this.detailData.transactionID|| "";
                transferObj.txnMasId = this.detailData.masterTransactionID|| "";
                transferObj.refTxnId = this.detailData.refTxnId|| "";
                transferObj.editType = this.detailData.editType|| "";
                transferObj.immediateType = this.constants.IMMEDIATE_TYPE_LATER;
 
                let transferDecisionList = new TransferDecisionBean;
                transferDecisionList.feeAmount = this.detailData.feeAmount||"";
                transferDecisionList.debitDate = this.detailData.debitDate||"";
                transferDecisionList.creditDate = this.detailData.creditDate||"";
                transferDecisionList.creditTime.name = this.detailData.transferTimeName || '';

                let transferStatus = new TransferStatusBean;
                transferStatus.code = this.detailData.statusCode||"";
                transferStatus.name = this.detailData.status||"";

                transferDecisionList.transferStatus = transferStatus;
                transferDecisionList.desc = this.detailData.fundTransferTypeDescToDisplay || '';//this.detailData.scheduleTypeDesc||"";
                transferObj.transferDecisionList[0] = transferDecisionList;

                transferObj.maskingFromAccount = this.detailData.maskingFromAccount;
                transferObj.maskingToAccount = this.detailData.maskingToAccount;

                transferObj.rtpReferenceNo = this.detailData.rtpReferenceNo || "";
                transferObj.transactionRef = this.detailData.transactionRef;
                transferObj.qrData = this.qrData;

        let modal = this.modalCtrl.create(TransactionDetail, { transfer: transferObj });
        modal.present();
    }
}


