import { Component } from '@angular/core';
import { Constants } from '../../../share/service/constants';
import { PermissionChangeRoute } from '../../../share/service/permission.service';
import { UtilService } from '../../../share/service/util.service';
import { GetinformationService } from '../../../share/service/getInformation.service';

declare var BUILD_NUM;

@Component({
    selector: 'about-mobile',
    template: `<ion-header>
                <ion-navbar>
                    <ion-title>{{'lbl.header.about' | translate}}</ion-title>
                </ion-navbar>
            </ion-header>            
            <ion-content>
                <ion-list>
                    <ion-item class="font-color-dark-blue" *ngFor="let item of items" (click)="goToNextPage(item)">
                       {{ item.menuName | translate }} 
                       <ion-icon name="arrow-forward" item-right></ion-icon>
                    </ion-item> 
                    <ion-item class="font-color-dark-blue" (click)="openTab()">
                        {{ 'lbl.newsAndUpdate' | translate }} 
                        <ion-icon name="arrow-forward" item-right></ion-icon>
                    </ion-item>
                    <ion-item class="font-color-dark-blue">
                        <span Style="float: left;">{{"lbl.version"|translate}}</span> 
                        <span Style="float: right;">{{appVersion}} ({{buildNumber}})</span> 
                    </ion-item>
                </ion-list>
            </ion-content>`
})

export class About {
    appVersion: string;
    buildNumber: string;
    public newsAndUpdateUrl: string;

    public items: any = [
        { menuCode: 'CONTACT_US', menuName: 'lbl.menu.contactus' },
        { menuCode: 'PRIVACY_POLICY', menuName: 'lbl.menu.privacypolicy' },
        { menuCode: 'DISCLAIMER', menuName: 'lbl.menu.disclaimer' },
        { menuCode: 'TERM_AND_CONDITION', menuName: 'lbl.menu.termandcon' }
    ];

    constructor(public permissionChangeRoute: PermissionChangeRoute,
        public constants: Constants,
        public utilService: UtilService,
        public getinformationService: GetinformationService) {
        this.buildNumber = BUILD_NUM;
        this.appVersion = this.constants.VER_PIB_WEB;
        this.newsAndUpdateUrl = this.constants.NEWS_AND_UPDATE_URL;
    }

    public goToNextPage(item): void {
        
        this.permissionChangeRoute.changeRoute(item.menuCode);
    }

    public openTab(): void {
        this.getinformationService
            .getInformation(this.constants.REQ_PROCEDURE_NAME.NEWS_INFORMATION,'news')
            .then((objResponse: any) => {
                if (objResponse.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                    this.newsAndUpdateUrl = objResponse.responseJSON.result.value.data;
                    let userAgentInfo = this.getinformationService.userAgentInfo || {};
                    this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'openSafari', { command: 'openSafari', url: this.newsAndUpdateUrl });
                }else{
                    this.newsAndUpdateUrl = '#';
                }
            });

    }
}


