import { Component, Input, OnChanges, OnInit, SimpleChanges, Output,EventEmitter } from '@angular/core';

@Component({
    selector: 'biller-image',
    templateUrl: "./billerImage.html"
})

export class BillerImageComponent implements OnInit, OnChanges {
    @Input() srcpath: string;
    @Input() defaultpath: string;
    @Input() isSmallIcon = false;
    @Output() loadImageSuccess = new EventEmitter<null>();
    @Output() loadImageError = new EventEmitter<null>();
    success:boolean = false;
    
    constructor(
    ) {
    }
    
    ngOnInit(): void {
    }
    
    ngOnChanges(changes: SimpleChanges): void {
        this.success = false;
    }

    loadSuccess() {
        this.success = true;
        this.loadImageSuccess.emit();
    }
   
    loadError() {
        this.loadImageError.emit();
    }
}



