import { Component, OnInit } from '@angular/core';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { MyAccountService } from '../../../../../pages/my-account/my-account.service';
import { PermissionAction } from '../../../../../share/service/permission.service';
import { Constants } from '../../../../../share/service/constants';
import { MyAccountAddEditConfirmBase } from '../../../my-account-add-edit-confirm-base';
import { AlertController, NavController } from 'ionic-angular';
import { TranslateService } from 'ng2-translate';
@Component({
    selector: 'my-account-add-confirm',
    templateUrl: '../my-account-add-edit-confirm.html'
})
export class MyAccountEditConfirmComponent extends MyAccountAddEditConfirmBase implements OnInit{

    constructor(
        public permissionChangeRoute: PermissionChangeRoute,
        public myAccountService: MyAccountService,
        public permissionAction: PermissionAction,
        public constants:Constants,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public nav: NavController) {
            super(permissionChangeRoute, myAccountService, permissionAction, constants);
    }

    ngOnInit() {
        super.setStyle(this.constants.STYLE_PIB_MOBILE);
        super.setMyAccountAddEditTitle('my.account.edit.title');
        super.init();
        this.isAdd = false;
        this.isRequestOTP = true;
    }

    onSubmitMyAccountSubmit() {
        this.myAccountSubmit();
    }

    //Override
    myAccountSubmit() {
        let objMyAccount: any = {
            myAccountID: this.myAccountService.selectAccountDetailData.myAccountID,
            myAccountAliasName: this.myAccountService.selectAccountDetailData.myAccountAliasName
        }
        this.editMyAccountSubmit(objMyAccount);
    }

    //Override
    editMyAccountSubmit(obj: any) {
        this.myAccountService.invokeServices(this.constants.REQ_ACTION_CODE.MY_ACCOUNT_EDIT, this.constants.REQ_PROCEDURE_NAME.MY_ACCOUNT_EDIT, obj).then((result : any) => {
            if (result.responseJSON.result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.Success'),
                    subTitle: this.translate.instant('label.editMyAccountSuccess', {myAccountNumber: this.myAccountService.newMyAccountData.myAccountNumber}),
                    buttons: [
                        {
                            // text: this.translate.instant('button.ok'),
                            text: this.translate.instant('button.backToHome'),
                            role: 'cancel',
                            handler: () => {
                                this.onModalHidden();
                            }
                        }
                    ]
                });
                alert.present();
            } else {
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.warning'),
                    subTitle: this.translate.instant(result.responseJSON.result.responseStatus.errorMessage),
                    buttons: [
                        {
                            text: this.translate.instant('button.ok'),
                            role: 'cancel',
                            handler: () => {
                                this.onModalHidden();
                            }
                        }
                    ]
                });
                alert.present();
            }
        }, function (error) {

        });
    }

    //Override
    onModalHidden() {
        this.myAccountService.newMyAccountData = undefined;
        this.nav.popToRoot();
    }
}



