import { Component, OnInit, OnDestroy } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { BillPaymentService } from '../../bill-payment.service';
import { BillPayment } from './pib-mobile-bill-payment';
import { NavController } from 'ionic-angular';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../../../../pages/bill-payment-request-to-pay/bill-payment-request-to-pay.service';
import { BillerBean } from '../../../../share/bean/biller-bean';
import { AlertController } from 'ionic-angular';
import { TranslateService } from 'ng2-translate/src/translate.service';
@Component({
    selector: 'pib-mobile-bill-payment-step3',
    templateUrl: './pib-mobile-bill-payment-step3.html'
})

export class PIBMobileBillPaymentStep3 extends BillPayment implements OnInit {
    isExistingBiller: boolean = false;

    constructor(public constants: Constants, 
    public billPaymentService: BillPaymentService,
    public navCtrl: NavController,
    public permissionChangeRoute: PermissionChangeRoute,
    public billPaymentRequestToPayService: BillPaymentRequestToPayService,
    public alertCtrl: AlertController,
    public translateService: TranslateService) {
        super(constants,billPaymentService);
    }

    ngOnInit(): void {
        this.init(2);
    }
    goToMainMenu() {
        this.navCtrl.popToRoot();
    }

    isExistingBillerInfo($event){
        this.isExistingBiller = $event;
    }

    AddNewBiller(){
        let billerForConfirmAddNew = this.billPaymentRequestToPayService.getConfirmBillerProfileForAddNew();
        if(billerForConfirmAddNew instanceof BillerBean ){ 
            let param =  {
                requesterIdValue: billerForConfirmAddNew.promptPayBillerId,
                billReference1: billerForConfirmAddNew.ref1,
                billReference2: billerForConfirmAddNew.ref2,
                billReference3: billerForConfirmAddNew.ref3
            }
            this.billPaymentService.inquiryPayInfo(param).then((result: any) => {
                if (result.responseJSON.result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                    let parseToBillerProfileID = this.billPaymentRequestToPayService.parseObjToBillerBeanToProfileBean(result.responseJSON.result.value,billerForConfirmAddNew);
                    this.billPaymentRequestToPayService.setConfirmBillerProfileForAddNew(parseToBillerProfileID);

                    this.permissionChangeRoute.prevUrl = 'PAY_BILL';
                    this.permissionChangeRoute.changeRoute('MANAGE_BILLER.add');
                }else{
                    let alert = this.alertCtrl.create({
                        title: this.translateService.instant('label.warning'),
                        subTitle: result.responseJSON.result.responseStatus.errorMessage,
                        buttons: [this.translateService.instant('btn.close')]
                    });
                    alert.present();
                }
            }, function (error) {
                
            });
        }else{
            this.permissionChangeRoute.prevUrl = 'PAY_BILL';
            this.permissionChangeRoute.changeRoute('MANAGE_BILLER.add');
        }
    }
}


