import { Constants } from '../../../../../share/service/constants';
import { Component, OnInit } from '@angular/core';
import { Platform, NavParams, ViewController } from 'ionic-angular';
import { RequestToPayService } from '../../../request-to-pay.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { AlertController } from 'ionic-angular';
import { MasterDataService } from '../../../../../share/service/master-data.service';
@Component({
    selector: 'from-account-list-modal',
    templateUrl: './from-account-list-modal.html'
})

export class FromAccountListModal implements OnInit {
    private accounts: any = [];
    public firstLoad: boolean = true;
    private currentAccount: any;
    private anyIDTypes: any = [];
    
    constructor(
        public params: NavParams,
        public platform: Platform,
        public constants: Constants,
        public viewCtrl: ViewController,
        public requestToPayService: RequestToPayService,
        private masterDataService: MasterDataService,
        private translate: TranslateService,
        private PermissionChangeRoute: PermissionChangeRoute,
        private alertCtrl: AlertController) {}

    ngOnInit(): void {
        this.currentAccount = this.params.get('account') || {};
        this.masterDataService.getAllAnyIDTypes().then((result: any) => {
            this.anyIDTypes = result;

            for(let i in this.anyIDTypes){
                if (this.anyIDTypes[i].anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) {
                    delete this.anyIDTypes[i];
                }
            }
           this.requestToPayService.inquiryRequestToPayAnyIdMy(this.anyIDTypes).then((result: any) => {
                 if(result.errorMessage === undefined && result.length > 0){
                    let groups = [];
                        groups[0] = {
                        desc: this.translate.instant('label.myKKPromptPayAcc'),
                        items: result,
                        show: false
                    }
                    this.accounts = groups;
                    if(this.accounts.length > 0) { this.toggleGroup(this.accounts[0]); }
                }else if(result.responseCode == 'RIB-E-ANY019'){
                        let MyaccountNoData = this.alertCtrl.create({
                        title: this.translate.instant('label.warning'),
                        message: result.errorMessage,
                        buttons: [{
                            text: this.translate.instant('button.cancel'),
                            handler: () => {
                            this.viewCtrl.dismiss();
                            }
                        },
                        {
                            text: this.translate.instant('btn.clickToRegisterKK'),
                            handler: () => {
                            this.viewCtrl.dismiss();
                            this.PermissionChangeRoute.changeRoute('KK_PRODUCT_SERVICE');
                            }
                        }]
                        });

                        MyaccountNoData.present();
                }else{
                    this.showAlert(result.errorMessage);
                }
                this.firstLoad = false;
            });
        });

            
    }

    onSelectChanged(newValue) {
        this.viewCtrl.dismiss(newValue);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    toggleGroup(group) {        
        group.show = !group.show;
    }

    isGroupShown(group) {
        return group.show;
    }

    protected showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }
}



