import { Component, OnInit, NgZone, OnDestroy } from '@angular/core';
import { NavController, Platform } from 'ionic-angular';
import { TranslateService } from "ng2-translate/src/translate.service";
import { StrFormatPipe } from '../../../../../share/pipe/string-format.pipe';
import { Constants } from '../../../../../share/service/constants';
import { ModalController, AlertController } from 'ionic-angular';
import { BillerRequestToPayMobileAddEdit } from '../../biller-request-to-pay-mobile-add-edit';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../../../bill-payment-request-to-pay.service';
import { UtilService } from '../../../../../share/service/util.service';
import { ManageBillerAddSearchModal } from '../../../manage-biller-add-search-modal/manage-biller-add-search-modal';
import { QrScannerService } from '../../../../../pages/qr-scanner/qr-scanner.service';
import { BillerProfileBean } from '../../../../../share/bean/biller-profile-bean';
import { GetinformationService } from '../../../../../share/service/getInformation.service';
@Component({
    selector: 'biller-request-to-pay-add',
    templateUrl: '../biller-request-to-pay-add-edit.html'

})
export class BillerRequestToPayAdd extends BillerRequestToPayMobileAddEdit implements OnInit, OnDestroy {

    public titleBillPayment: string;

    constructor(
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected translate: TranslateService,
        private modalCtrl: ModalController,
        protected alertCtrl: AlertController,
        protected strFormat: StrFormatPipe,
        protected navController: NavController,
        public platform: Platform,
        public utilService:UtilService,
        public qrScannerService: QrScannerService,
        public ngZone: NgZone,
        public getinformationService: GetinformationService) {
        super(constants, permissionChangeRoute, billPaymentRequestToPayService, translate, alertCtrl, strFormat, navController, platform);

        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }],
                step: 0,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
        this.titleBillPayment = 'label.titleAddBiller';
        this.channel = this.constants.CHANNEL_PIB_MOBILE;
    }

    ngOnInit() {
        this.isAdd = true;
        this.initMobile();
        window['angularComponentRef'] = {
            publicFunc: this.publicFunc.bind(this)
        };
    }

    readBarcode(){
        let userAgentInfo = this.getinformationService.userAgentInfo || {};
        this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'readBarcode');
    }

    openModal() {
        if(this.isFromAddnewAfterPayBill){
            return true;
        }
        
        let modal = this.modalCtrl.create(ManageBillerAddSearchModal);
        modal.onDidDismiss(data => {

            if (data !== undefined && data !== null) {
                this.selectedBillerProfile = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(data);
                //if category 26 then not show ref2
                if(this.isEDonationCategory(this.selectedBillerProfile.categoryId)) {
                    if(this.selectedBillerProfile.refNoList.length >= 2) {
                        //remove ref 2 from showing
                        console.log('this.selectedBillerProfile', this.selectedBillerProfile)
                        this.selectedBillerProfile.refNoList[1].value = '0';
                    }
                }
            }
        });
        modal.present();
    }

    verifyFromScanBiller(result){
        switch(result.responseCode) { 
          case 0: {
              if(result.format === 'QR_CODE' || result.format === 'CODE_128'){
                  this.inquiryQrcodeBarCodeInfoForAdd(result);
              }
            break; 
          } 
          case 1: { 

            break; 
          } 
          case -1: { 
              this.showAlert(this.translate.instant("lbl.scanPluginError"));
            break; 
          } 
      } 

    }

    onCloseToBillerTab(){
        this.selectedBillerProfile = new BillerProfileBean;
    }

    private inquiryQrcodeBarCodeInfoForAdd(Data){
        this.qrScannerService.inquiryQrcodeBarCodeInfoForAdd(Data).then((objResponse: any) => {
            if (objResponse.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                this.selectedBillerProfile = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(objResponse.value);
            } else {
                this.showAlert(objResponse.responseStatus.errorMessage);
            }
        }, function (error) {

        });
    }

     protected showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    publicFunc(event) {
        this.ngZone.run(() => this.verifyFromScanBiller(event));
    }

    ngOnDestroy() {
        window['angularComponent'] = null;
    }

    public isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }
}



