import { Component, OnInit, AfterViewInit, NgZone, OnDestroy,ViewChild, ElementRef } from '@angular/core';
import { Constants } from '../../share/service/constants';
import { NavParams, ViewController} from 'ionic-angular';
import { GetinformationService } from '../../share/service/getInformation.service';
import { UtilService } from '../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
@Component({
    selector: 'qr-generator-save-modal',
    templateUrl: 'qr-generator-save-modal.html'
})

export class QRGeneratorSaveModalComponent implements OnInit, AfterViewInit, OnDestroy {
    private qrCodeData;
    @ViewChild('childComponent') public childComponent: ElementRef; 
    constructor(
        public constants: Constants,
        public params: NavParams,
        public viewCtrl: ViewController,
        private ngZone: NgZone,
        private getinformationService: GetinformationService,
        private utilService: UtilService,
        private translate: TranslateService
    ) {
    }

    ngOnInit(): void {
       this.qrCodeData = this.params.get('qrCodeData');
        window['angularComponentRef'] = {
            publicFunc: this.publicFunc.bind(this)
        };
    }

     
    ngAfterViewInit(){
        var canvas = this.childComponent.nativeElement;
        var clientScreenWidth = window.screen.width;
        var clientScreenHeight = window.screen.height;

        canvas.width = clientScreenWidth;
        canvas.height = clientScreenHeight;
        
        
        let _qrCodeData = this.qrCodeData;
        let _translate = this.translate;
       
        var ctx = canvas.getContext("2d");
        
        var footerPhatra = 65;

        var bgSize = {w: canvas.width ,h:(canvas.height- (footerPhatra))}
        var qrSize = (_qrCodeData.amount != null)? (2.2/4)* bgSize.w:(2.5/4)* bgSize.w;
        var logoSize = {w:qrSize/6,h: qrSize/8}
        
        var renderImg = [
            {path:"./assets/images/template_QR_mobile.png",x: getWidthMiddlePosition(bgSize.w) ,y: 0,width: bgSize.w, height: bgSize.h},
            {path: _qrCodeData.qrCodeBase64, x: getWidthMiddlePosition(qrSize) ,y: getHeightMiddlePosition(qrSize), width: qrSize,height: qrSize},
            {path:"./assets/images/Thai_QR_Payment_Logo_tranparent.png",x:getWidthMiddlePosition(logoSize.w),y: getHeightMiddlePosition(logoSize.h),width: logoSize.w, height: logoSize.h}
        ];

        var renderTxt = [
            {text:_qrCodeData.accountName,lineWidth: 4,font: getTextSize(1.2/10),x: canvas.width / 2,y: (bgSize.h / 2) + (qrSize / 2) + (1.2/10*qrSize)},
            {text:_qrCodeData.amount,lineWidth: 4,font:getTextSize(1.2/10),x: canvas.width / 2,y: (bgSize.h / 2) + (qrSize / 2) +  2.5*(1.2/10*qrSize) + 15},
            {text:_translate.instant("label.bahtTHEN"),lineWidth: 2,font: getTextSize(1.2/10),x: canvas.width / 2,y: (bgSize.h / 2) + (qrSize / 2) +  3*(1.2/10*qrSize)+20}
        ];

        function getWidthMiddlePosition(objSize: number){
            return (canvas.width / 2) - (objSize / 2);
        }

        function getHeightMiddlePosition(objSize: number){
            return (bgSize.h / 2) - (objSize / 2);
        }

        function getTextSize(ratio: number){
            return (ratio*qrSize).toFixed(0)+ 'px RSU_BOLD';
        }

        // function getHightTextPosition(ratio: number){
        //     return (bgSize.h / 2) + (qrSize / 2) + getTextSize(ratio);
        // }

        var LoadedImgs=[];
        var count=0;
        startLoadingAllImages(imagesAreNowLoaded);

        function startLoadingAllImages(callback){

            for (var i=0; i<renderImg.length; i++) {
                var img = new Image(renderImg[i].width,renderImg[i].height);
                LoadedImgs.push(img);
                img.onload = function(){ 
                    count++; 
                    if (count>=renderImg.length ) {
                    callback();
                    }
                };
                img.onerror=function(){};
                img.src = renderImg[i].path;
            }      
        }

        function imagesAreNowLoaded(){
            for(var i = 0; i< renderImg.length; i++){
                ctx.drawImage(LoadedImgs[i],renderImg[i].x,renderImg[i].y,renderImg[i].width,renderImg[i].height);
            }

            var renderTxtLength = (_qrCodeData.amount != null)? renderTxt.length:1;
            var heightAccountNameTxt = (_qrCodeData.amount != null)? 15:20
            for(var j = 0; j< renderTxtLength; j++){
                ctx.lineWidth=renderTxt[j].lineWidth;
                ctx.fillStyle="#000000";
                ctx.font=renderTxt[j].font;
                ctx.textAlign = "center";

                renderTxt[0].y = renderTxt[0].y + heightAccountNameTxt;
                ctx.fillText(renderTxt[j].text,renderTxt[j].x,renderTxt[j].y);
            }
        }

       this.captureScreen();
    }

    ngOnDestroy() {
        window['angularComponent'] = null;
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    captureScreen(){
        setTimeout(() => {
            let userAgentInfo = this.getinformationService.userAgentInfo || {};
            this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'capturescreen');
        }, 500);
    }
    
    publicFunc() {
        this.ngZone.run(() => this.dismiss());
    }

}



