export class CardInfoBean {
    cardType: string;
    cardId: string;
    referenceCode: string;
    constructor (cardType: string, cardId: string, referenceCode: string) {
        this.cardType = cardType;
        this.cardId = cardId;
        this.referenceCode = referenceCode;
    }
}


