import { Component, OnInit, OnDestroy } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { BillPaymentService } from '../../bill-payment.service';
import { BillPayment } from './pib-mobile-bill-payment';
@Component({
    selector: 'pib-mobile-bill-payment-step2',
    templateUrl: './pib-mobile-bill-payment-step2.html'
})

export class PIBMobileBillPaymentStep2 extends BillPayment implements OnInit, OnDestroy {

    constructor(public constants: Constants, public billPaymentService: BillPaymentService) {
        super(constants,billPaymentService);
    }

    ngOnInit(): void {
        this.init(1);
    }

    ngOnDestroy(): void {
        this.billPaymentService.setBillPaymentObj(null);
    }
}


