import { Component, OnInit, AfterViewInit, NgZone, OnDestroy } from '@angular/core';
import { Constants } from '../../../service/constants';
import { TransferBean } from '../../../bean/transfer-bean';
import { TransferDecisionBean } from '../../../bean/transfer-decision-bean';
import { BankCodeDataService } from '../../../service/bankcode-data.service';
import { NavParams, ViewController } from 'ionic-angular';
// import { TranslateService } from 'ng2-translate';
import { GetinformationService } from '../../../service/getInformation.service';
import { UtilService } from '../../../service/util.service';

declare var BUILD_NUM;

@Component({
    selector: 'transaction-detail',
    templateUrl: './transaction-detail.html'
})

export class TransactionDetail implements OnInit, AfterViewInit, OnDestroy {
    public BUILD_NUM = BUILD_NUM;
    private transferObj: TransferBean = null;
    private decision: TransferDecisionBean = new TransferDecisionBean();

    constructor(
        public constants: Constants,
        public params: NavParams,
        public viewCtrl: ViewController,
        public bankCodeDataService: BankCodeDataService,
        private ngZone: NgZone,
        private getinformationService: GetinformationService
        // private translateService: TranslateService,
        , private utilService: UtilService
    ) {
        //window['angularComponentRef'] = {component: this, zone: ngZone}; 
    }

    ngOnInit(): void {
        this.transferObj = this.params.get('transfer') || new TransferBean();
        let transferDecisionList = this.transferObj.transferDecisionList;
        

        // Assign an initial value to fee detail radio button by default.
        if (transferDecisionList != null && transferDecisionList.length > 0) {
            this.decision = transferDecisionList[0];
        }

        // create outside the app
        window['angularComponentRef'] = {
            publicFunc: this.publicFunc.bind(this)
        };
    }

    ngAfterViewInit(){
        this.captureScreen();
    }

    ngOnDestroy() {
        window['angularComponent'] = null;
    }

    dismiss() {
        // let alert = this.alertCtrl.create({
        //     title: this.translateService.instant('label.Success'),
        //     subTitle: this.translateService.instant('label.screenShotSuccessMsg'),
        //     enableBackdropDismiss: false,
        //     buttons: [
        //         {
        //             text: this.translateService.instant('button.ok'),
        //             role: 'cancel',
        //             handler: () => {
        //                 this.viewCtrl.dismiss();
        //             }
        //         }
        //     ]
        // });
        // alert.setCssClass('z-twentythousand');
        // alert.present();
        this.viewCtrl.dismiss();
        
    }

    captureScreen(){
        setTimeout(() => {
            let userAgentInfo = this.getinformationService.userAgentInfo || {};
            this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'capturescreen');
        }, 500);
    }
    
    publicFunc() {
        this.ngZone.run(() => this.dismiss());
    }

}



