import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { KKProductAndServiceDetail } from '../../kk-product-and-service-detail';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { UtilService } from '../../../../share/service/util.service';
import { GetinformationService } from '../../../../share/service/getInformation.service';

declare var BUILD_NUM;

@Component({
  selector: 'pib-mobile-kk-product-and-service-detail',
  templateUrl: './kk-product-and-service-detail.html',
})
export class PIBMobileKKProductAndServiceDetail extends KKProductAndServiceDetail implements OnInit {
  public BUILD_NUM = BUILD_NUM;
  constructor(
    public constants: Constants, 
    public permissionChangeRoute: PermissionChangeRoute,
    public utilService: UtilService,
    public getinformationService: GetinformationService) {
    super(constants, permissionChangeRoute, getinformationService);
  }

  ngOnInit(): void {
    this.init();
  }

  public openTab(): void {
    let userAgentInfo = this.getinformationService.userAgentInfo || {};
    this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'openSafari', { command: 'openSafari', url: this.promptPayUrl });
  }
}


