import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../service/constants';
import { TransferBean } from '../../../bean/transfer-bean';
import { TransferTypeBean } from '../../../bean/transfer-type-bean';
import { TranslateService } from "ng2-translate/src/translate.service";
import { TransferDecisionBean } from '../../../bean/transfer-decision-bean';
import { FundTransferService } from '../../../service/fund-transfer.service';
import { BankCodeDataService } from '../../../service/bankcode-data.service';
import { ModalController, NavController, AlertController, ViewController } from 'ionic-angular';
import { DecisionList } from '../../../component/decision-list/mobile/decision-list.component';
import { FundTransferStep3 } from '../../../../pages/fund-transfer/mobile/fund-transfer-step3';
import {DateFormatterPipe} from "../../../pipe/date-formatter.pipe";

@Component({
    selector: 'confirm',
    templateUrl: './confirm.html',
    providers: [DateFormatterPipe]
})

export class Confirm implements OnInit {

    protected model: any = {
        transferMethod: "",
        otpPin: "",
        verifyOTP: null
    };

    protected verifyOTPStyle: string;
    protected verifyOTPAction: string;
    public submitted: boolean = false;
    private transferObj: TransferBean = new TransferBean();
    private decision: TransferDecisionBean = new TransferDecisionBean();

    public isRequestOTP: boolean = false;
    public isRequireOTP: boolean = false;
    protected responseCodeMainService: string = '';
    public showSmartColor: boolean = false;

    constructor(
        public constants: Constants,
        public navCtrl: NavController,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        public viewCtrl: ViewController,
        public translate: TranslateService,
        public bankCodeDataService: BankCodeDataService,
        public fundTransferService: FundTransferService,
        public dateFormatterPipe: DateFormatterPipe,
        // private permissionChangeRoute: PermissionChangeRoute
    ) {
    }

    ngOnInit(): void {
        this.verifyOTPStyle = this.constants.STYLE_PIB_MOBILE;
        this.verifyOTPAction = this.constants.ACTION_CODE_FUND_TRANSFER;

        this.transferObj = this.fundTransferService.getTransferObj();
        let transferDecisionList = this.transferObj.transferDecisionList;

        if(this.transferObj != null){
            this.isRequireOTP = this.transferObj.isRequireOtp;
            if (!this.isRequireOTP) {
                this.isRequestOTP = true;
            }
        }
        // Assign an initial value to fee detail radio button by default.
        if (transferDecisionList != null && transferDecisionList.length > 0) {
            this.decision = transferDecisionList[0];
            //
            this.model.transferMethod = this.decision.RIBTransferTypeCode;

            //show smart color
            this.isShowSmartColor(this.decision.creditTime.code);
        }
    }

    openModal(action: string) {
        if (action == 'DECISION') {
            let modal = this.modalCtrl.create(DecisionList, { decisions: this.transferObj.transferDecisionList });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {
                    //
                    this.decision = data;

                    //show smart color
                    this.isShowSmartColor(this.decision.creditTime.code);
                }
            });

            modal.present();
        }
    }

    onRequestOTP(result) {
        //

        this.isRequestOTP = true;

        if (typeof result.responseCode === "undefined") {
            this.model.verifyOTP = result;
        } else {
            let messageAlert = result.errorMessage;
            this.showAlert(messageAlert);
        }
    }

    onOtpChanged(arg) {
        //
        this.model.otpPin = arg;
    }

    beforeGoToNextStep(data) {
        let msg = this.translate.instant('lbl.fund.confirm.warn.description');
        let currentDate = this.dateFormatterPipe.transform(this.decision.getCreditTimeDisplay(), []);
        let displayMoment = this.decision.creditTime.name;
        msg = msg + currentDate + ', ' + displayMoment;
        if (this.decision.creditTime.code != this.constants.TRANSFER_TIME_IMMEDIATE) {
            this.showConfirm(data, msg);
        }else {
            this.goToNextStep(data);
        }
    }

    goToNextStep(data): void {
        //

        this.responseCodeMainService = '';

        let promise = null;
        this.transferObj.transferType = new TransferTypeBean();
        this.transferObj.transferType.code = this.decision.RIBTransferTypeCode;

        if (this.model.verifyOTP != null) {
            this.transferObj.verifyOTP = {
                otpRefcode: this.model.verifyOTP.otpRefcode,
                otpPin: this.model.otpPin,
                tokenOtp: this.model.verifyOTP.tokenOtp
            };
        }

        if (this.transferObj.rtpReferenceNo !== '' || this.transferObj.editType == "") {
            promise = this.fundTransferService.confirmFundTransfer(this.transferObj);
        } else {
            promise = this.fundTransferService.confirmEditFundTransfer(this.transferObj);
        }

        if (promise != null) {
            promise.then((result: any) => {
                if (typeof result.responseCode === "undefined") {
                    this.fundTransferService.setTransferObj(result);
                    // this.permissionChangeRoute.changeRoute("FUND_TRANSFER.COMPLETE");

                    this.navCtrl
                        .push(FundTransferStep3)
                        .then(() => {
                            // Find index of confirm.component
                            const index = this.viewCtrl.index;

                            // Then remove confirm.component from the navigation stack
                            this.navCtrl.remove(index);
                        });
                } else {

                    if (result.responseCode === this.constants.RESP_CODE_REQ_OTP_AGAIN) {
                        this.isRequestOTP = false;
                    }
                    this.responseCodeMainService = result.responseCode;
                    this.model.otpPin = '';
                    let messageAlert = result.errorMessage;
                    this.showAlert(messageAlert);
                }
            });
        }
    }

    showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    private isShowSmartColor(creditTimeCode) {
        switch (creditTimeCode) {
            case this.constants.TRANSFER_TIME_IMMEDIATE:
                this.showSmartColor = false;
                break;
            case this.constants.TRANSFER_TIME_MORNING:
                this.showSmartColor = true;
                break;
            case this.constants.TRANSFER_TIME_EVENING:
                this.showSmartColor = true;
                break;
            default:
                this.showSmartColor = true;
                break;
        }
    }

    private showConfirm(data, msg) {
        let confirm = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            message: msg,
            buttons: [{
                text: this.translate.instant('btn.cancel'),
                handler: () => {

                }
            },
                {
                    text: this.translate.instant('button.ok'),
                    handler: () => {
                        this.goToNextStep(data);
                    }
                }]
        });

        confirm.present();
    }
}



