import { Component, NgZone, OnDestroy, OnInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { PibMobileList } from './main-menu.list';
import { Constants } from '../../../../share/service/constants';
import { MainMenuPibMobileService } from '../../../../share/service/main-menu.service';
import { PermissionService, PermissionChangeRoute } from '../../../../share/service/permission.service';
import { TranslateService, LangChangeEvent } from 'ng2-translate';
import { NotificationService } from "../../../../share/service/notification.service";
import { Subscription } from "rxjs";
import { ModalContentPage } from "../../../landing-page/mobile/pib/landing-page-mobile";
import { GetinformationService } from "../../../../share/service/getInformation.service";

declare var BUILD_NUM;

@Component({
  selector: 'pages-pib-mobile-main-menu',
  templateUrl: 'main-menu.html',
  providers: [PibMobileList, MainMenuPibMobileService]
})

export class PIBMobileMenuRoot implements OnInit, OnDestroy {
  public BUILD_NUM = BUILD_NUM;
  public menuList;
  private submenu;
  public menus: any[];
  public notificationList: any[];
  private lang: any;
  private langSubscription: Subscription;
  private permissionSubscription: Subscription;
  private notificationSubscription: Subscription;
  private isFailChallengeSubscription: Subscription;
  public isDesktop = true;


    public notificationListReverse = [];
    public badgeMenu = "0";
    public notificationTemp = [];
  constructor(public navCtrl: NavController,
    private pibMobileList: PibMobileList,
    private mainMenuPibMobileService: MainMenuPibMobileService,
    private permissionService: PermissionService,
    private permissionChangeRoute: PermissionChangeRoute,
    private zone: NgZone,
    private constants: Constants,
    private translate: TranslateService,
    private navParams:NavParams,
    private notificationService:NotificationService,
    private navController: NavController,
    private getinformationService: GetinformationService
  ) {
    this.isDesktop = this.getinformationService.isDesktop();
    this.mainMenuPibMobileService.setRouterData(this.pibMobileList.getMenuList());
    this.mainMenuPibMobileService.setMenuList();
    this.permissionChangeRoute.setRouteStyle(navCtrl);
    this.permissionChangeRoute.setChannelRoute(this.constants.MENU_MOBILE);

    // this.notificationService.notificationEvent.subscribe(user => this.notificationUpdateEvent(user));

    // this.notificationService.notificationHistoryEvent.subscribe(noti => this.notificationHistory(noti));

    // this.notificationService.badgeUpdateEvent.subscribe(badge => this.badgeUpdateEvent(badge));
  }

  ngOnInit(): void {
    this.permissionSubscription = this.permissionService.PermssionChangeEvent
      .subscribe(user => this.selectedUserEventHandler(user));

    // this.menuRender()
    this.isFailChallengeSubscription = this.permissionService.getIsFailChallenge().subscribe(returnChallenge=>{
      // if ("RIB-E-LOG012" == returnChallenge.responseCode || "RIB-E-LOG011" == returnChallenge.responseCode || "RIB-E-LOG007" == returnChallenge.responseCode) {
        this.navController.push(ModalContentPage);
      // }
    })
    this.lang = this.translate.currentLang;
    this.langSubscription = this.translate.onLangChange
    .subscribe((event: LangChangeEvent) => {
      this.lang = this.translate.currentLang;
    });
    this.submenu = this.navParams.get("children");


    if(this.submenu !== undefined){
      this.submenuSet(this.submenu);
    }
    this.notificationSubscription = this.notificationService.getBadgeMenuListStream()
    .subscribe((badgemenuList)=>{
      this.notificationList = badgemenuList;
    })

    //  this.notificationService.getAllNotification(0);

  }

  ngOnDestroy(): void{
    this.langSubscription.unsubscribe();
    this.permissionSubscription.unsubscribe();
    this.notificationSubscription.unsubscribe();
    this.isFailChallengeSubscription.unsubscribe();
  }


  private submenuSet(submenu){
    this.notificationList = this.notificationService.getBadgeMenuList();
    this.menus = submenu;
  }

  // Just Prototype
  private menuRender() {
    this.zone.run(() => {
        this.menus = this.mainMenuPibMobileService.getMainMenuAfterLogin();
    });
    this.notificationService.updateBadgeMenuCount();
  }

  public isShowNotification(menuCode:string){
    let isShow = false;
    const countStr = this.notificationService.getBadgeMenuCountStr(menuCode);
    if(!this.notificationList||(countStr === '')){
      return false;
    }
    this.notificationList.forEach((item)=>{
      if(item.menuCode === menuCode){
        isShow = true;
      }
    });

    if(menuCode === 'RTP'){
      isShow = true;
    }
    return isShow;
  }

  public getNotificationCount(menuCode:string){
    return this.notificationService.getBadgeMenuCountStr(menuCode);
  }

  public navigate(component,children) {



    if(children === undefined || children.length === 0){
 
      if (component == "FUND_TRANSFER") {
        this.permissionChangeRoute.targetAction = "NEW";
      }

      this.permissionChangeRoute.changeRoute(component);
    }else{



              
        this.navCtrl.push(PIBMobileMenuRoot,{'children':children});
     //   this.permissionChangeRoute.pushRoute("PIBMobileMenuRoot");
   

    }
  }


  // private checkIsParent(children){
  //     if(children === undefined || children.length === 0){

  //       return true;
  //     }


  // }

  selectedUserEventHandler(user: any) {
    let isLogin = this.permissionService.getIsLogin();
    let isTermAndCond = this.permissionService.getIsTermAndCon();
    if (!isTermAndCond) {
      this.navController.push(ModalContentPage);
      //  this.menuList = this._MainMenuPibMobileService.getMainMenuList();
    } else {
      this.menuRender();
    }
  }

  public trackByFn(index, item) {
      return index;
  }

  private navigateToNotification(){

    this.notificationService.setNotiList(this.notificationListReverse);
    this.permissionChangeRoute.changeRoute('NOTI');
  }


  
    notificationUpdateEvent(notification) {
        this.notificationTemp = [notification, ...this.notificationTemp];
        this.notificationListReverse = this.notificationTemp;

        //this.notificationService.setNotiList(this.notificationListReverse);
    }

    notificationHistory(notification) {
        
        this.notificationTemp.push(notification);
        this.notificationListReverse = this.notificationTemp;

       // this.notificationService.setNotiList(this.notificationListReverse);
    }

    badgeUpdateEvent(badge) {
        this.badgeMenu = badge;

    }
}



