import { Component, OnInit, style, state, transition, trigger, animate } from '@angular/core';
import { ViewController, AlertController, ModalController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { BillPaymentService } from '../../bill-payment.service';
import { BillPaymentRequestToPayService } from '../../../../pages/bill-payment-request-to-pay/bill-payment-request-to-pay.service';
import { BillerBean } from '../../../../share/bean/biller-bean';
import { TranslateService } from "ng2-translate/src/translate.service";
import { ToBillerList } from '../../web/rib/bill/component/to-biller-list.component';
import { UtilService } from '../../../../share/service/util.service';
import { ManageBillerAddSearchModal } from '../../../../pages/bill-payment-request-to-pay/manage-biller-add-search-modal/manage-biller-add-search-modal';
import { BillerProfileBean } from '../../../../share/bean/biller-profile-bean';
import { StrFormatPipe } from '../../../../share/pipe/string-format.pipe';
import { BillerRefNoBean } from '../../../../share/bean/biller-ref-no-bean';
@Component({
    selector: 'pib-mobile-to-biller-list',
    templateUrl: './pib-mobile-to-biller-list.html',
    animations: [
        trigger('accordingState', [
            state('collapsed, void', style({
                height: '0'
            })),
            state('expanded', style({
                height: '*'
            })),
            transition('collapsed <=> expanded', [animate(250)])
        ])
    ]
})

export class PibMobileToBillerList extends ToBillerList implements OnInit {
    private action: string = "list";
    private selectedBillerProfile = new BillerProfileBean;
    constructor(public constants: Constants,
        public billPaymentService: BillPaymentService,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected translate: TranslateService,
        protected viewCtrl: ViewController,
        public utilService: UtilService,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        protected strFormat: StrFormatPipe
    ) {
        super(constants, billPaymentService, billPaymentRequestToPayService, translate, utilService, alertCtrl);
    }

    ngOnInit() {
        this.init();
        
    }

    public onSelectBillerProfile(selectBiller: BillerBean){
        this.viewCtrl.dismiss(selectBiller);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    openModal() {
        let modal = this.modalCtrl.create(ManageBillerAddSearchModal);
        modal.onDidDismiss(data => {
            
            if (data !== undefined && data !== null) {
                console.log(data)
                //if category 26 then not show ref2
                // if(this.isEDonationCategory(data.categoryId)) {
                //     if(data.refInfos.length >= 2) {
                //         //remove ref 2 from showing
                //         data.refInfos[1].value = '0';
                //     }
                // }
                if(this.isEDonationCategory(data.categoryId)) {
                    for (let i = 0; i < data.refInfos.length; i++) {
                        switch (i) {
                            case 0:
                                break;
                            case 1:
                                data.refInfos[i].isHideRef = true;
                                data.refInfos[i].value = '0';
                                break;
                            default:
                                data.refInfos[i].isHideRef = true;
                                break;
                        }
                    }
                }
                this.selectedBillerProfile = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(data);
            }
        });
        modal.present();
    }

    onSubmit(billerForm) {
        console.log(billerForm)
        let validationResult = this.validateForm(billerForm);
        if (!validationResult.valid) {
            let messageAlert = this.strFormat.transform(validationResult.message, [validationResult.displayName]);
            this.showErrorAlert(messageAlert, this.translate.instant('label.warning'));
        } else {
            let parseToBillerBean =  new BillerBean();
            //always check trim refs
            if( this.selectedBillerProfile.refNoList){
            let refInfos = new Array<any>() ;
            this.selectedBillerProfile.refNoList.forEach((element: BillerRefNoBean) => {
                        let refInfo = {
                            no:     element.no,
                            value:  element.value.trim(),
                            textEn: element.refTextEn,
                            textTh: element.refTextTh
                        };
                        refInfos.push(refInfo);
                });
                this.selectedBillerProfile.refNoList = refInfos;
            }
            
            parseToBillerBean = this.billPaymentRequestToPayService.parseBillerProfileBeanToBillerBean(this.selectedBillerProfile);
            this.billPaymentRequestToPayService.setConfirmBillerProfileForAddNew(parseToBillerBean);
            
            
            parseToBillerBean.isAddNewToBiller = true;
            this.viewCtrl.dismiss(parseToBillerBean);
        }
    }

    protected validateForm(form: any): any {
        let validationResult = { valid: true, displayName: '', message: '' };
        if (validationResult.valid) {
            
            for (let billerRefNo of this.selectedBillerProfile.refNoList) {
                if (billerRefNo.value === '') {
                    validationResult = {
                        valid: false,
                        displayName: (this.translate.currentLang === 'en') ? billerRefNo.refTextEn : billerRefNo.refTextTh,
                        message: 'valErr.required'
                    };
                    break;
                }
            }
        }

        return validationResult;
    }

    public showErrorAlert(messageAlert, title) {
        let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    public isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }
}


