import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController } from 'ionic-angular';
import { Constants } from '../../../../../share/service/constants';
import { OtherAccountService } from '../../../other-account.service';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from "ng2-translate/src/translate.service";
import { UtilService } from '../../../../../share/service/util.service';
import { PermissionAction } from '../../../../../share/service/permission.service';
import { DropdownDataService } from '../../../../../share/service/dropdown.service';
import { ValidationService } from '../../../../../share/service/validation.service';
import { MasterDataService } from '../../../../../share/service/master-data.service';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { MobileOtherAccountAddEdit } from '../../../../../pages/other-account/mobile/other-account-add-edit';

@Component({
    selector: 'edit-other-account',
    templateUrl: '../../other-account-add-edit.html',
})
export class PIBMobileEditOtherAccountComponent extends MobileOtherAccountAddEdit implements OnInit {

    editOtherAcc: boolean;
    public catName: any;
    public bankName: any;
    public messageAlert: string;
    disabledAccountNo: boolean = true;
    configAnyIDType: Object = { valueLength: Number, ValueType: Boolean };

    constructor(public constants: Constants,
        public otherAccountService: OtherAccountService,
        public permissionChangeRoute: PermissionChangeRoute,
        public alertCtrl: AlertController,
        public translateService: TranslateService,
        public fb: FormBuilder,
        public permissionAction: PermissionAction,
        public modalCtrl: ModalController,
        public dropdownDataService: DropdownDataService,
        public masterDataService: MasterDataService,
        public utilService: UtilService) {
        super(constants, otherAccountService, permissionChangeRoute, alertCtrl, translateService, fb, permissionAction, modalCtrl, dropdownDataService, masterDataService, utilService);
    }

    ngOnInit(): void {
        this.disabledAccountNo = true;
        this.editOtherAcc = true;
        this.titlePage = 'label.editAcc';
        this.init();
        this.otherAccountService.isAccountAfterTransfer = false;

        if (this.otherAccountService.selectAccountDetailData != undefined) {
            this.selectItemAcc = this.otherAccountService.selectAccountDetailData;
            this.otherAccountFormGroup = new FormGroup({
                accountAliasname: new FormControl({ value: this.selectItemAcc.acctAliasName, disabled: false }),
                email: new FormControl({ value: this.selectItemAcc.email == null ? '' : this.selectItemAcc.email, disabled: false }, [ValidationService.emailValidator]),
                clientUUID: new FormControl({ value: "", disabled: false }),
                anyIDType: new FormControl({ value: this.selectItemAcc.anyIDType, disabled: false }),
                accountNo: new FormControl({ value: this.selectItemAcc.acctNo, disabled: false }),
                mobile: new FormControl({ value: this.selectItemAcc.mobile == null ? '' : this.selectItemAcc.mobile, disabled: false }, [ValidationService.mobileNoValidator]),
                msgLang: new FormControl({ value: this.selectItemAcc.msgLang, disabled: false }),
                bankCode: new FormControl({ value: this.selectItemAcc.bankCode, disabled: false }),
                accountName: new FormControl({ value: this.selectItemAcc.acctName == null ? '' : this.selectItemAcc.acctName, disabled: false }),
                category: new FormControl({ value: this.selectItemAcc.catId, disabled: false }),
                categoryName: new FormControl({ value: this.selectItemAcc.categoryName, disabled: false }),
                acctAliasName: new FormControl({ value: this.selectItemAcc.acctAliasName, disabled: false }),
                exAcctId: new FormControl({ value: this.selectItemAcc.exAcctId, disabled: false }),
                isFavourite: new FormControl({ value: this.selectItemAcc.isFavourite, disabled: false })
            });


        }

        this.getAnyIDTypeList();
        if (this.inputMenuSwiper != undefined) {
            for (let i = 0; i < this.inputMenuSwiper.length; i++) {
                if (this.inputMenuSwiper[i].type === this.selectItemAcc.anyIDType) {
                    this.swiperOptions = { lock: true, index: i };
                }
            }
        }
    }

    public getBankList(): void {
        this.masterDataService.getAllBanks().then((result) => {
            this.bankLists = result;
            this.masterDataService.setBankList(this.bankLists);

            if (this.otherAccountFormGroup.value.anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) {
                let bankObj = this.bankLists.find(x => x.bankCode == this.selectItemAcc.bankCode);
                if (bankObj) {
                    this.bankName = bankObj.bankName;
                    this.checkAccountInfo(bankObj.bankCode);
                }
            }
        });
    }

    public getCatagories(): void {
        this.masterDataService.getAllCategories().then((result) => {
            this.accCategoryList = result;
            this.masterDataService.setCategoryList(this.accCategoryList);

            let category = this.accCategoryList.find(x => x.catId == this.selectItemAcc.catId);
            if (category) {
                this.catName = category.catName;
                this.otherAccountFormGroup.patchValue({ categoryName: this.catName });
            }
        });
    }

    // swiperChange(event) {
    //     this.Onchange('anyIDType', event);
    //     this.descriptionLabel = event.name;
    // }

    public Onchange(param, event): void {
        if (event.type != this.otherAccountFormGroup.value.anyIDType) {
            this.otherAccountFormGroup.patchValue({ anyIDType: event.type });
        }
    }

    isOpenModal(param, event) {
        this.openModal(param, event);
    }
}



