import { NavController } from 'ionic-angular';
import { FundTransfer } from './fund-transfer';
import { Constants } from '../../../share/service/constants';
import { Component, OnInit } from '@angular/core';
import { TransferBean } from '../../../share/bean/transfer-bean';
import { PermissionChangeRoute, PermissionService } from '../../../share/service/permission.service';
import { FundTransferService } from '../../../share/service/fund-transfer.service';
import { MyAccountService } from '../../../pages/my-account/my-account.service'; 
@Component({
    selector: 'fund-transfer-step3',
    templateUrl: './fund-transfer-step3.html'
})

export class FundTransferStep3 extends FundTransfer implements OnInit {

    public transferObj: TransferBean = new TransferBean();

    constructor(public constants: Constants,
        public navCtrl: NavController,
        public fundTransferService: FundTransferService,
        private permissionChangeRoute: PermissionChangeRoute,
        private permissionService: PermissionService,
        private myAccountService: MyAccountService) {
        super(constants, fundTransferService);
    }

    ngOnInit(): void {
        this.init(2);
        this.transferObj = this.fundTransferService.getTransferObj();
    }

    addToAccount(transferObj) {
        let destAccount = transferObj.destAccount;
        destAccount.email = transferObj.notifyEmail;
        destAccount.mobileNo = transferObj.notifyMobileNo;
        destAccount.notifyLang = transferObj.notifyLang;
        destAccount.txnId = transferObj.txnId;
        destAccount.refTxnId = transferObj.refTxnId;

        this.fundTransferService.newAccountAfterFund = destAccount;

        if (transferObj.addAccountType === 'OTHER_ACCOUNT') {
            this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.add1');
        } else {
            this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD');
        }
    }
 
    goToMainMenu() {
        this.navCtrl.popToRoot();
    }
}



