import { Component, OnInit } from '@angular/core';
import { MyAccountAddEdit } from '../../../my-account-add-edit';
import { MyAccountService } from '../../../../../pages/my-account/my-account.service';
import { PermissionAction } from '../../../../../share/service/permission.service';
import { Constants } from '../../../../../share/service/constants';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { FormBuilder, Validators } from '@angular/forms';
import { AlertController } from 'ionic-angular';
import { TranslateService } from 'ng2-translate';
import { FundTransferService } from '../../../../../share/service/fund-transfer.service';
import { ValidationService } from '../../../../../share/service/validation.service';

@Component({
    selector: 'my-account-add',
    templateUrl: '../my-account-add-edit.html'
})
export class MyAccountAddComponent extends MyAccountAddEdit implements OnInit {

    public myAccountAddEditForm: any;

    constructor(
        public fb: FormBuilder,
        public permissionChangeRoute: PermissionChangeRoute,
        public myAccountService: MyAccountService,
        public permissionAction: PermissionAction,
        public constants:Constants,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public fundTransferService: FundTransferService) {
            super(permissionChangeRoute,myAccountService,permissionAction,constants);
    }

    ngOnInit() {
        super.setStyle(this.constants.STYLE_PIB_MOBILE);
        super.setCurrentStepWizard(0);
        super.setMyAccountAddEditTitle('my.account.add.title');
        this.isAdd = true;
        super.init();
        this.buildForm();

        if(typeof this.myAccountService.newMyAccountData !== 'undefined'){
            this.myAccountAddEditForm.patchValue({
                myAccountNumber: this.myAccountService.newMyAccountData.myAccountNumber, 
                myAccountAliasName: this.myAccountService.newMyAccountData.myAccountAliasName
            });
        }


        // add account after transferd
        this.initNewAccountAfterFund();
    }

    ngOnDestroy(){
        this.fundTransferService.newAccountAfterFund = null;
    }

    onSubmitMyAccount() {
        if(this.newAccountAfterFund !== undefined){
            this.confirmNewAccountAfterFund();
        } else {
            let myAccount = this.myAccountAddEditForm.value;
            if (this.validateInputMyAccount(myAccount)) {
                let objMyAccount: any = {
                    myCustomerAccount: {
                        myAccountNumber: myAccount.myAccountNumber,
                        myAccountAliasName: myAccount.myAccountAliasName
                    }
                }
                this.verifyAddMyAccount(objMyAccount);
            }
        }
        
    }

    validateInputMyAccount(myAccount: any) : boolean {
        if (myAccount.myAccountAliasName === '' || myAccount.myAccountAliasName == null) {
            let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: this.translate.instant('label.missingaccAliasNameMsg'),
                buttons: [this.translate.instant('button.ok')]
            });
            alert.present();
            return false;
        }
        if (myAccount.myAccountNumber === '' || myAccount.myAccountNumber == null) {
            let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: this.translate.instant('label.missingaccNoMsg'),
                buttons: [this.translate.instant('button.ok')]
            });
            alert.present();
            return false;
        }
        return true;
    }

    //Override function for main class
    verifyAddMyAccount(obj: any) {
        this.myAccountService.invokeServices(this.constants.REQ_ACTION_CODE.MY_ACCOUNT_ADD, this.constants.REQ_PROCEDURE_NAME.MY_ACCOUNT_ADD, obj).then((result : any) => {
            if (result.responseJSON.result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                let data = result.responseJSON.result.value;
                this.myAccountService.newMyAccountData = data;
                // this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD_CONFIRM');

                this.showToAskNewAccountNumber(data);
            } else {
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.warning'),
                    subTitle: this.translate.instant(result.responseJSON.result.responseStatus.errorMessage),
                    buttons: [this.translate.instant('button.ok')]
                });
                alert.present();

            }
        }, function (error) {

        });

    }

    private buildForm(): void {
        this.myAccountAddEditForm = this.fb.group({
            'myAccountAliasName':['', [
                ValidationService.requiredValidator,
                Validators.maxLength(30)
                ]
            ],
            'myAccountNumber':['', [
                ValidationService.requiredValidator,
                Validators.maxLength(30),
                ValidationService.nonStringValidator
                ]
            ]
        });
    }

    private newAccountAfterFund;
    private initNewAccountAfterFund(){
        if(this.fundTransferService.newAccountAfterFund){
            this.newAccountAfterFund = {
                myAccountAliasName: this.fundTransferService.newAccountAfterFund.aliasName !== null? this.fundTransferService.newAccountAfterFund.aliasName:'',
                myAccountNumber: this.fundTransferService.newAccountAfterFund.accNo,
                myAccountName: this.fundTransferService.newAccountAfterFund.accName,
                txnId: this.fundTransferService.newAccountAfterFund.txnId,
                refTxnId: this.fundTransferService.newAccountAfterFund.refTxnId
            };

            this.myAccountAddEditForm.patchValue({
                myAccountNumber: this.newAccountAfterFund.myAccountNumber, 
                myAccountAliasName: this.newAccountAfterFund.myAccountAliasName
            });
            
            let myAccountNumberCtrl = this.myAccountAddEditForm.get('myAccountNumber');
            myAccountNumberCtrl.disable();
        }
    }
    private confirmNewAccountAfterFund(){
        this.myAccountService.newMyAccountData = {
            myAccountAliasName: this.myAccountAddEditForm.value.myAccountAliasName.trim(),
            myAccountNumber: this.newAccountAfterFund.myAccountNumber.trim(),
            myAccountName: this.newAccountAfterFund.myAccountName.trim(),
            refTxnId: this.newAccountAfterFund.refTxnId,
            txnId: this.newAccountAfterFund.txnId
        };
        
        let objMyAccount: any = {
            myCustomerAccount: {
                myAccountAliasName: this.myAccountAddEditForm.value.myAccountAliasName.trim(),
                myAccountNumber: this.newAccountAfterFund.myAccountNumber.trim(),
                refTxnId: this.newAccountAfterFund.refTxnId,
                txnId: this.newAccountAfterFund.txnId
            }
        };

        this.myAccountService.isAccountAfterTransfer = true;
        this.fundTransferService.newAccountAfterFund = null;

        super.verifyAddMyAccount(objMyAccount);
    }

    private showToAskNewAccountNumber(account: any){
         if(!account.myAccountNumberOld){
            this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD_CONFIRM');
        } else {
            let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: account.myAccountNumberOld + ' ' + this.translate.instant('lbl.askNewAccountNumber') + ' ' + account.myAccountNumber,
                buttons: [{
                    text: this.translate.instant('button.ok'),
                    handler: () => {
                        this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD_CONFIRM');
                    }
                }]
            });
            alert.present();
        }
        
    }
}


