import { Platform } from 'ionic-angular';
import { Component, OnInit } from '@angular/core';
import { StatusBar } from '@ionic-native/status-bar';
import { Keyboard } from '@ionic-native/keyboard';
import { PIBMobileMenuRoot } from '../pages/main-layout/mobile/pib/main-menu';
import { PreloadService, ILoader } from "../share/service/preload.service";
import { GetinformationService } from "../share/service/getInformation.service";
import { Keepalive } from '@ng-idle/keepalive';
import { Idle, DEFAULT_INTERRUPTSOURCES } from '@ng-idle/core';
import { UtilService } from "../share/service/util.service";
import { IsLoginService } from "../share/service/islogin.service";
@Component({
  template: `
          <div *ngIf="loader.isLoading" class="loading-container">
            <div class="loading-backdrop">
              <div class="loading-position">
                <div class="sk-circle">
                  <div class="sk-circle1 sk-child"></div>
                  <div class="sk-circle2 sk-child"></div>
                  <div class="sk-circle3 sk-child"></div>
                  <div class="sk-circle4 sk-child"></div>
                  <div class="sk-circle5 sk-child"></div>
                  <div class="sk-circle6 sk-child"></div>
                  <div class="sk-circle7 sk-child"></div>
                  <div class="sk-circle8 sk-child"></div>
                  <div class="sk-circle9 sk-child"></div>
                  <div class="sk-circle10 sk-child"></div>
                  <div class="sk-circle11 sk-child"></div>
                  <div class="sk-circle12 sk-child"></div>
                </div>
              </div>
            </div>
          </div>
          <p *ngIf="lastPing"><small>Last keepalive ping <strong>{{lastPing | amTimeAgo}}</strong></small></p>
          <ion-nav [root]="rootPage"  id="content" #content><router-outlet>LOADING...</router-outlet></ion-nav>
          <userAgentInfo (info)="thisUserAgent($event)"></userAgentInfo>`
})
export class PIBMobileComponent implements OnInit {
  private rootPage;
  loader: ILoader;
  lastPing?: Date = null;

  constructor(private platform: Platform,
    private preload: PreloadService,
    private getinformationService: GetinformationService,
    private statusbar : StatusBar,
    private keyboard: Keyboard,
    private keepalive: Keepalive,
    private idle: Idle,
    private utilService: UtilService,
    private isLoginService: IsLoginService
  ) {
    platform.ready().then(() => {
      statusbar.styleDefault();

      /*
       * MODIFY BOOTSTRAP CODE BELOW
       * Disable the Ionic Keyboard Plugin scroll for iOS only
       * https://github.com/driftyco/ionic/issues/5571
       */
      if (this.platform.is('ios')) {
        keyboard.disableScroll(true);
      }

    });
    
    // sets an idle timeout of 300 seconds(5 minutes), for testing purposes.
    idle.setIdle(60);
    // sets a timeout period of 5 seconds. after 300 seconds(5 minutes) of inactivity, the user will be considered timed out.
    idle.setTimeout(false);
    // sets the default interrupts, in this case, things like clicks, scrolls, touches to the document
    idle.setInterrupts(DEFAULT_INTERRUPTSOURCES);

    // sets the ping interval to 5 seconds
    keepalive.interval(5);
    keepalive.onPing.subscribe(() => this.keepAlive());

    idle.watch();
  }

  ngOnInit(): void {
    this.loader = this.preload.loader;
    this.rootPage = PIBMobileMenuRoot;
    this.isLoginService.multipleLoginEvent.subscribe(status => this.logOutMultipleLogin(status));
  }

  logOutMultipleLogin(status){
    
    let userAgentInfo = this.getinformationService.userAgentInfo || {};
    this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'checkUserCredential');
  }

  thisUserAgent(event) {
    this.getinformationService.userAgentInfo = event;
    
  }

  keepAlive(){
    let userAgentInfo = this.getinformationService.userAgentInfo || {};
    this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'updateIdleTime');
    
  }
}




