import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { TranslateService } from 'ng2-translate';
import { ModalController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { StepWizard } from '../../../../share/component/step-wizard/step-wizard.component';
import { PromptPayRegisterServiceMain } from '../../../../pages/prompt-pay/prompt-pay-register.service';
import { FromAccountList } from '../../../../share/component/from-account-list/mobile/from-account-list.component';
import { termAndConditionModal } from './pib-mobile-prompt-pay-register.component';
import { AccountBean } from '../../../../share/bean/account-bean';

@Component({
    selector: 'my-kk-prompt-pay-edit',
    templateUrl: './my-kk-prompt-pay-edit.html'
})

export class MyKKPromptPayEditComponent {

     public stepWizard: StepWizard;
     public EditPromptPayForm: any;
     public currentRegistedPromptPay: any;

    constructor(
        public promptPayRegisterServiceMain: PromptPayRegisterServiceMain,
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public translate: TranslateService,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController) {
        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }, { name: '3', label: 'stepWizard.complete' }],
                step: 0,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
    }

    ngOnInit(): void {
        //init form
        this.EditPromptPayForm = {
            account: new AccountBean,
            isAgree: false
        }
        //get proxy type form kk promptpay detail
        this.currentRegistedPromptPay = this.promptPayRegisterServiceMain.selectedAccount;
    }

    openAccountModal(selected: any) {
        let modal = this.modalCtrl.create(FromAccountList, { accTypeFilter: this.constants.ACCOUNT_TYPE_TD , accountNoFilter: this.currentRegistedPromptPay.accountNo});
        modal.onDidDismiss(data => {
            if (data !== undefined && data !== null) {
                this.EditPromptPayForm.account = data;
            }
        });
        modal.present();
    }

    opentermAndCondModal() {
        this.modalCtrl.create(termAndConditionModal).present();
    }

    toggleTermAndCond() {
        this.EditPromptPayForm.isAgree = !this.EditPromptPayForm.isAgree;
    }

    private showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    public verifyEditPromptPay(): void {
        if(this.EditPromptPayForm.account.accNo != undefined && this.EditPromptPayForm.isAgree != false){
           //submit
           let param = {
                anyIDType: this.currentRegistedPromptPay.anyIDType.anyIDType,
                anyIDValue: this.currentRegistedPromptPay.anyIDValue,
                fromAccountNo: this.currentRegistedPromptPay.accountNo,
                toAccountName: this.EditPromptPayForm.account.accName,
                toAccountNo: this.EditPromptPayForm.account.accNo
            };
            this.promptPayRegisterServiceMain.verifyPromptPayEditAccountService(param).then((objResponse: any) => {
                let result = objResponse.responseJSON.result;

                if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                    this.promptPayRegisterServiceMain.EditAnyIDData = result.value;
                    this.promptPayRegisterServiceMain.EditAnyIDData.selectedBankAccountObj = this.EditPromptPayForm.account.bank;
                    
                    this.permissionChangeRoute.changeRoute('MY_KK_PROMPTPAY.EDIT-CONFIRM');
                } else {
                    this.showAlert(result.responseStatus.errorMessage);
                }
            }, function (error) {
                
            });
           
        }else{
             this.showAlert(this.EditPromptPayForm.account.accNo == undefined ? this.translate.instant('label.warningSelectAccount') : this.translate.instant('label.warningAcceptTermAndCond'));
        }
    }

}



