import { Component, OnInit, ViewChild } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { NavController } from 'ionic-angular';
import { MyAccountService } from '../../../../pages/my-account/my-account.service';
import { PermissionChangeRoute, PermissionService} from '../../../../share/service/permission.service';
import { MyAccountListPage } from '../../my-account-list-page';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PIBMobileMyAccountListComponent } from './my-account-list.component';

@Component({
  selector: 'my-account-list-page',
  templateUrl: './my-account-list-page.html' 
})
export class MyAccountListPageComponent extends MyAccountListPage implements OnInit {
    alertConfig: {title: string, type: string,message: string, show: boolean, option: any};
    @ViewChild('listItem') public listItem: PIBMobileMyAccountListComponent;

    constructor(public navCtrl: NavController, 
    public permissionChangeRoute: PermissionChangeRoute, 
    public constants: Constants, 
    public myAccountService: MyAccountService, 
    public translateService: TranslateService,
    public _PermissionService:PermissionService) {
      super(permissionChangeRoute, constants, myAccountService, translateService, _PermissionService);
    }

    ngOnInit(){
      this.alertConfig = {
        title: '',
        type: 'danger',
        message: '', 
        show: false,
        option: {}
      };
    }

    ongetMyAccount(data: any){
        //
        this.myAccountService.selectAccountDetailData = data;
        //this.navCtrl.push(MyAccountDetailPageComponent);
        this.permissionChangeRoute.changeRoute('MY_DEPOSITS.DETAIL');
    }

    gotoAddAccountPage() {
      this.listItem.hideItemSliding();
      this.myAccountService.newMyAccountData = undefined;
      this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD');
    }
}


 


  




