import { FundTransfer } from './fund-transfer';
import { BankBean } from '../../../share/bean/bank-bean';
import { Constants } from '../../../share/service/constants';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { AccountBean } from '../../../share/bean/account-bean';
import { TransferBean } from '../../../share/bean/transfer-bean';
import { UtilService } from '../../../share/service/util.service';
import { OtherAccountService } from '../../other-account/other-account.service';
import { MyAccountService } from '../../../pages/my-account/my-account.service';
import { FundTransferService } from '../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../share/service/bankcode-data.service';
import { PermissionService, PermissionChangeRoute } from '../../../share/service/permission.service';

@Component({
    selector: 'fund-transfer-step1',
    templateUrl: './fund-transfer-step1.html'
})

export class FundTransferStep1 extends FundTransfer implements OnInit, OnDestroy {

   public module: string = "";
   public transferObj: TransferBean = new TransferBean();

    constructor(public constants: Constants,
        public myAccountService: MyAccountService,
        public fundTransferService: FundTransferService,
        public otherAccountService: OtherAccountService,
        public bankCodeDataService: BankCodeDataService,
        public permissionChangeRoute: PermissionChangeRoute,
        public utilService: UtilService,
        public permissionService: PermissionService) {
        super(constants, fundTransferService);
    }

    ngOnInit(): void {
        this.preLoadEvent();
    }

    ngOnDestroy(): void {
        this.permissionChangeRoute.targetAction = null;
    }

    public preLoadEvent() {
        if (this.permissionChangeRoute.targetAction == "NEW") {
            this.fundTransferService.setTransferObj(null);
        } else {

            // Pass data from my account/ other account to fund transfer
            if (this.utilService.arrayObjectIndexOf(['MY_ACCOUNT'], this.permissionChangeRoute.targetAction) !== -1
                && this.myAccountService.selectAccountDetailData != null
                && this.myAccountService.selectAccountDetailData.statusCode !== '2'
                && this.myAccountService.selectAccountDetailData.accountType !== this.constants.ACCOUNT_TYPE_TD) {

                this.module = "MY_ACCOUNT";
                this.fundTransferService.setTransferObj(null);
            } else if (this.utilService.arrayObjectIndexOf(['OTHER_ACCOUNT'], this.permissionChangeRoute.targetAction) !== -1
                && this.otherAccountService.selectAccountDetailData != null) {

                this.module = "OTHER_ACCOUNT";
                this.fundTransferService.setTransferObj(null);
            } else {

            }
        }

        // Load fund transfer form
        this.onLoadEvent();
    }

    public onLoadEvent() {
        this.init(0);
        this.permissionChangeRoute.prevUrl = null;
        this.permissionChangeRoute.targetAction = null;

        this.transferObj = this.fundTransferService.getTransferObj();


        if (this.transferObj == null) {
            if (this.module == "MY_ACCOUNT") {
                let srcAccountObj = new AccountBean();

                let bank = new BankBean();
                bank.bankCode = this.myAccountService.selectAccountDetailData.bankCode;
                bank.bankName = this.myAccountService.selectAccountDetailData.bankName;
                bank.shortName = this.myAccountService.selectAccountDetailData.shortName;
                bank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(bank.bankCode);

                srcAccountObj.accId = this.myAccountService.selectAccountDetailData.myAccountID;
                srcAccountObj.accName = this.myAccountService.selectAccountDetailData.accName;
                srcAccountObj.accNo = this.myAccountService.selectAccountDetailData.myAccountNumber.substring(0,10);
                srcAccountObj.accountType = this.myAccountService.selectAccountDetailData.accountType;
                srcAccountObj.aliasName = this.myAccountService.selectAccountDetailData.myAccountAliasName;
                srcAccountObj.balance = this.myAccountService.selectAccountDetailData.myAvailableBalance;
                srcAccountObj.benefitAcc = this.myAccountService.selectAccountDetailData.myLedgerBalance;
                srcAccountObj.allowSchedule = this.permissionService.isProductAllow(this.myAccountService.selectAccountDetailData.productID,"allowSchedule");
                srcAccountObj.bank = bank;

                this.transferObj = new TransferBean();
                this.transferObj.srcAccount = srcAccountObj;
                this.transferObj.destAccount = new AccountBean();
                this.fundTransferService.setTransferObj(this.transferObj);
            }
            if (this.module == "OTHER_ACCOUNT") {
                this.transferObj = new TransferBean();
                this.transferObj.srcAccount = new AccountBean();
                let destAccountObj = this.otherAccountService.selectAccountDetailData.accountBean;
                this.transferObj.destAccount = destAccountObj;
                this.transferObj.notifyEmail = destAccountObj.email;
                this.transferObj.notifyMobileNo = destAccountObj.mobileNo || "";

                if(this.transferObj.notifyMobileNo != "") {
                    this.transferObj.notifyLang = this.constants.CULTURE_SHORTNAME_ENGLISH;
                }
                
                this.fundTransferService.setTransferObj(this.transferObj);
            }
        }
    }
}



