import { ModalDirective } from 'ng2-bootstrap';
import { Constants } from '../../service/constants';
import { Dateservice } from '../../service/date.service';
import { TimelineMapFields } from './timeline-mapfields';
import { UtilService } from '../../service/util.service';
import { Component, Input, ViewChild, OnInit } from '@angular/core';
import { TranslateService } from "ng2-translate/src/translate.service";
import { BankCodeDataService } from '../../service/bankcode-data.service';
import { FundTransferService } from '../../service/fund-transfer.service';
import { CurrencyFormatterPipe } from '../../pipe/currency-formatter.pipe';
import { BillPaymentRequestToPayService } from '../../../pages/bill-payment-request-to-pay/bill-payment-request-to-pay.service';
@Component({
    selector: 'timeline-modal-message',
    template: `
    <div bsModal #timelineModalMessage="bs-modal" class="modal fade" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" aria-hidden="true" [config]="config">
        <div class="modal-dialog modal-{{optionalSize}}">
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title pull-left">{{ selectTitle | translate }}</h4>
                    <button type="button" class="close pull-right" aria-label="Close" (click)="hide()">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="table-scrollable table-scrollable-borderless">
                        <table class="table table-light">
                            <tbody>
                                <tr *ngIf="selectType !== 'RTP'">
                                    <td>{{ 'lbl.transferFrom' | translate }}</td>
                                    <td>
                                        <table>
                                            <tbody>
                                                <tr>
                                                    <td class="fit">
                                                        <div class="icon-circle" [style.background-color]="bankCodeDataService.getBankCodeImagePropertiesByField(modalData.fromBankCode,'color')">
                                                            <img class="user-pic rounded" src="{{'assets/' + bankCodeDataService.getBankCodeImagePropertiesByField(modalData.fromBankCode,'url')}}">
                                                        </div>
                                                    </td>
                                                    <td>
                                                        <div class="text-wrap"><a href="javascript:;">{{ modalData.accountFromName }}</a></div>
                                                        <div *ngIf="modalData.fromBankName" class="text-wrap">{{ modalData.fromBankName }}</div>
                                                        <div *ngIf="modalData.accountFromNumber">({{ modalData.accountFromNumber }})</div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </td>
                                </tr>
                                <tr  *ngIf="selectType !== 'RTP'">
                                    <td *ngIf="selectType === 'fundtransfer'">{{ 'lbl.transferTo' | translate }}</td>
                                    <td *ngIf="selectType === 'biller'">{{ 'lbl.toBiller' | translate }}</td>
                                    <td>
                                        <table>
                                            <tbody>
                                                <tr>
                                                    <td class="fit" *ngIf="selectType === 'fundtransfer'">
                                                        <div class="icon-circle" *ngIf="modalData.anyIDType === constants.ANYID_TYPE_BANK_ACCOUNT" [style.background-color]="bankCodeDataService.getBankCodeImagePropertiesByField(modalData.toBankCode,'color')">
                                                            <img class="user-pic rounded" onerror="this.style.display='none';" 
                                                                src="{{ 'assets/' + bankCodeDataService.getBankCodeImagePropertiesByField(modalData.toBankCode,'url') }}" />
                                                        </div>
                                                        <div class="icon-circle" *ngIf="modalData.anyIDType !== constants.ANYID_TYPE_BANK_ACCOUNT" [style.background-color]="anyIDTypeList[modalData.anyIDType]?.iconObj?.iconColor">
                                                            <img src="{{ anyIDTypeList[modalData.anyIDType]?.iconObj?.icon }}" onerror="this.style.display='none';" class="user-pic rounded" />
                                                        </div>
                                                    </td>
                                                    <td class="fit" *ngIf="selectType === 'biller'">
                                                        <div class="icon-circle"  [style.background-color]="bankCodeDataService.getBankCodeImagePropertiesByField('069','color')">
                                                            <img class="user-pic rounded" (error)="utilService.getDefaultBillerIcon($event)" src="{{ utilService.getBillerIcon(modalData) }}" />
                                                        </div>
                                                    </td>
                                                    <td *ngIf="selectType === 'fundtransfer'">
                                                        <div *ngIf="modalData.accountToName" class="text-wrap"><a href="javascript:;">{{ modalData.accountToName }}</a></div>
                                                        <div *ngIf="anyIDFromDesc" class="text-wrap">{{ anyIDFromDesc }}</div>
                                                        <div *ngIf="modalData.accountToNumber">({{ modalData.accountToNumber }})</div>
                                                    </td>
                                                    <td *ngIf="selectType === 'biller'">
                                                        <a>{{ modalData.paymentFor }}</a>
                                                        <div *ngIf="modalData.biller">{{ (modalData.biller) ? modalData.biller : "" }}</div>
                                                        <div *ngIf="modalData.ref1">{{ 'lbl.ref1' | translate }}: {{ modalData.ref1 }}</div>
                                                        <div *ngIf="modalData.ref2">{{ 'lbl.ref2' | translate }}: {{ modalData.ref2 }}</div>
                                                        <div *ngIf="modalData.ref3">{{ 'lbl.ref3' | translate }}: {{ modalData.ref3 }}</div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP'">
                                    <td>{{ 'lbl.RTPRefCode' | translate }}</td>
                                    <td>{{ modalData.rtpReferenceNo }}</td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP' && modalData.rtpMsgType === 'INCOMING'">
                                        <td>{{ 'lbl.RequestFrom' | translate}}</td>
                                        <td>{{ modalData.requesterAccountName }} <span *ngIf="modalData.requesterIdType!=='BILLERID'">({{modalData.requesterIdValue}})</span></td>
                                        <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP' && modalData.rtpMsgType === 'OUTGOING'">
                                        <td>{{ 'lbl.RequestTo' | translate}}</td>
                                        <td>{{ modalData.receiverAccountName }} <span *ngIf="modalData.receiverIdType!=='BILLERID'">({{modalData.receiverIdValue}})</span></td>
                                        <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP'">
                                    <td>{{ 'label.type' | translate }}</td>
                                    <td>{{ modalData.Type }} </td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td>{{ 'lbl.amount' | translate }}</td>
                                    <td>{{ modalData.amount }} <span *ngIf="modalData.amount">{{ 'lbl.baht' | translate }}</span></td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'biller'">
                                    <td>{{ 'lbl.fee' | translate }}</td>
                                    <td>{{ modalData.feeAmount }} <span *ngIf="modalData.feeAmount">{{ 'lbl.baht' | translate }}</span></td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType !== 'RTP'">
                                    <td>{{ 'lbl.referenceNo' | translate }}</td>
                                    <td>{{ modalData.referenceNumber }}</td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td>{{ 'lbl.transactionDate' | translate }}</td>
                                    <td>{{ modalData.transactionDate }}</td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP'">
                                    <td>{{ 'lbl.expiredDate' | translate }}</td>
                                    <td>{{ modalData.expiryDate }}</td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'RTP' && modalData.requesterIdType === 'BILLERID'">
                                     <td>{{ 'lbl.referenceNo' | translate }}</td>
                                     <td>
                                        <div *ngIf="modalData.ref1">{{ 'lbl.ref1' | translate }}: {{ modalData.ref1 }}</div>
                                        <div *ngIf="modalData.ref2">{{ 'lbl.ref2' | translate }}: {{ modalData.ref2 }}</div>
                                        <div *ngIf="modalData.ref3">{{ 'lbl.ref3' | translate }}: {{ modalData.ref3 }}</div>
                                     </td>
                                </tr>
                                <tr *ngIf="selectType === 'biller'">
                                    <td>{{ 'lbl.paymentDate' | translate }}</td>
                                    <td>{{ modalData.debitDate }}</td>
                                    <td></td>
                                </tr>  
                                <tr *ngIf="selectType !== 'RTP' && modalData.rtpReferenceNo != null">
                                    <td>{{ 'lbl.RTPRefCode' | translate }}</td>
                                    <td>{{ modalData.rtpReferenceNo }}</td>
                                    <td></td>
                                </tr>                                
                                <tr *ngIf="modalData.recurringType && selectKey.indexOf('schedule') > -1">
                                    <td *ngIf="selectType === 'fundtransfer'">{{ 'lbl.recurringType' | translate }}</td>
                                    <td *ngIf="selectType === 'biller'">{{ 'lbl.recurringType.paybill' | translate }}</td>
                                    <td>
                                        {{ modalData.recurringType }}{{ (modalData.recurringTimes) ? ', ' + modalData.recurringTimes : '' }}
                                        <span *ngIf="modalData.recurringTimes"> {{ (modalData.recurringTimes && modalData.recurringTimes > 1 ? 'lbl.times' : 'lbl.time') | translate }} </span>
                                    </td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'fundtransfer' && modalData.accountFromNumber != modalData.accountToNumber">
                                    <td>{{ 'lbl.notifyReceiver' | translate }}</td>
                                    <td>
                                        <div class="text-wrap" *ngIf="(modalData.toEmail || modalData.toMobileNumber || modalData.msgLanguage)">
                                            <span *ngIf="modalData.toEmail">{{ 'lbl.notifyEmail' | translate }} : {{ (modalData.toEmail) ? modalData.toEmail : '-' }}<br></span>
                                            <span *ngIf="modalData.toMobileNumber">{{ 'lbl.notifySMS' | translate }} : {{ (modalData.toMobileNumber) ? modalData.toMobileNumber : '-' }}<br></span>
                                            <span *ngIf="modalData.toMobileNumber">{{ 'lbl.notifyLanguage' | translate }} : {{ ((modalData.msgLanguage) ? ((modalData.msgLanguage === constants.CULTURE_SHORTNAME_THAI) ? 'lbl.thai' : 'lbl.english') : '-') | translate }}</span>
                                        </div>
                                        <div class="text-wrap" *ngIf="(!modalData.toEmail && !modalData.toMobileNumber && !modalData.msgLanguage)">
                                            <div class="text-wrap">-</div>
                                        </div>
                                    </td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td>{{ 'lbl.note' | translate }}</td>
                                    <td>
                                        <div class="text-wrap">{{ (modalData.note) ? modalData.note : '-' }}</div>
                                    </td>
                                    <td></td>
                                </tr>
                                <tr>
                                    <td>{{ 'lbl.status' | translate }}</td>
                                    <td *ngIf="modalData?.errorMsg" >{{ modalData.status + ' - ' + modalData?.errorMsg }}</td>
                                    <td *ngIf="!modalData?.errorMsg" >{{ modalData.status }}</td>
                                    <td></td>
                                </tr>
                                <tr *ngIf="selectType === 'fundtransfer'">
                                    <td>{{ 'lbl.feeAmount' | translate }}</td>
                                </tr>
                            </tbody>
                        </table>
                        <table *ngIf="selectType === 'fundtransfer'" class="table table-striped table-bordered table-advance table-hover">
                            <thead>
                                <tr>
                                    <th>{{'lbl.option' | translate}}</th>
                                    <th>{{'lbl.debitDate' | translate}}</th>
                                    <th>{{'lbl.transferDate' | translate}}</th>
                                    <th>{{'lbl.fee' | translate}}</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td><span></span>{{ modalData.fundTransferTypeDescToDisplay }}</td>
                                    <td>{{ (modalData.debitDate) ? modalData.debitDate : '-' }}</td>
                                    <td>{{ modalData.receivedDate }}<span *ngIf="modalData.transferTimeName">, {{ modalData.transferTimeName }}</span></td>
                                    <td>{{ (modalData.feeAmount) ? modalData.feeAmount : '-' }} <span *ngIf="modalData.feeAmount">{{ 'lbl.baht' | translate }}</span></td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" (click)="hide()">{{ 'btn.close' | translate }}</button>
                </div>
            </div>
        </div>
    </div>`
    , providers: [TimelineMapFields]
})
export class TimelineModalComponent implements OnInit {

    modalData: any;
    anyIDTypeList: any;
    anyIDFromDesc: string;

    @Input('config') config: any;
    @Input('key') selectKey: string;
    @Input('type') selectType: string;
    @Input('title') selectTitle: string;
    @Input('size') optionalSize: string;

    @ViewChild('timelineModalMessage') public timelineModalMessage: ModalDirective;

    constructor(public dateservice: Dateservice,
        public translateService: TranslateService,
        public bankCodeDataService: BankCodeDataService,
        public timelineMapFields: TimelineMapFields,
        public fundTransferService: FundTransferService,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        public currencyFormatter: CurrencyFormatterPipe,
        public constants: Constants,
        public utilService: UtilService) {
    }

    ngOnInit(): void {
        this.modalData = {
            fromBankCode: '',
            accountFromName: '',
            accountFromNumber: '',
            toBankCode: '',
            accountToName: '',
            accountToNumber: '',
            paymentFor: '',
            biller: '',
            ref1: '',
            ref2: '',
            amount: '',
            referenceNumber: '',
            receivedDate: '',
            transactionDate: '',
            recurringType: '',
            recurringTimes: '',
            toEmail: '',
            toMobileNumber: '',
            msgLanguage: '',
            note: '',
            fundTransferTypeDescToDisplay: '',
            transferTimeName: '',
            debitDate: '',
            anyIDType: this.constants.ANYID_TYPE_BANK_ACCOUNT,
            rtpReferenceNo: '',
            requesterAccountName: '',
            Type: '',
            requesterIdValue: '',
            expiryDate: '',
            ref3: '',
            requesterIdType: '',
            feeAmount: '',
            rtpMsgType: '',
            receiverAccountName: '',
            receiverIdValue: '',
            receiverIdTypeLabel: '',
            receiverIdType: '',
            profileCode: '',
            promptPayBillerId: '',
            transactionRef: '',
        };
    }

    private initialData(data: any) {
        if (!data) return;

        this.anyIDTypeList = this.config.data.anyIDTypeList || [];
        let mapfields = this.timelineMapFields.data[this.selectKey];
        let transactionDate = this.dateservice.parseDateTime(data[mapfields["transactionDate"]]);
        let txtTransactionDate = this.dateservice.formatDate(transactionDate, 'DD MMM YYYY HH:mm:ss', this.translateService.currentLang);
        let txtAmount = this.currencyFormatter.transform(data[mapfields["amount"]]) || '0.00';
        let receivedDate;
        let txtReceivedDate;
        let debitDate;
        let txtDebitDate;
        let Type;
        
        this.modalData = {
            fromBankCode: data[mapfields["fromBankCode"]] || this.constants.KKP_BANK_CODE,
            fromBankName: data[mapfields["fromBankName"]],
            accountFromName: data[mapfields["accountFromName"]],
            accountFromNumber: data[mapfields["accountFromNumber"]],
            toBankCode: data[mapfields["toBankCode"]] || this.constants.KKP_BANK_CODE,
            accountToName: data[mapfields["accountToName"]],
            accountToNumber: data[mapfields["accountToNumber"]],
            paymentFor: data[mapfields["paymentFor"]],
            biller: data[mapfields["biller"]],
            ref1: data[mapfields["ref1"]],
            ref2: data[mapfields["ref2"]],
            ref3: data[mapfields["ref3"]],
            referenceNumber: data[mapfields["referenceNumber"]],
            recurringType: data[mapfields["recurringType"]],
            recurringTimes: data[mapfields["recurringTimes"]],
            toEmail: data[mapfields["toEmail"]],
            toMobileNumber: data[mapfields["toMobileNumber"]],
            msgLanguage: data[mapfields["msgLanguage"]],
            note: data[mapfields["note"]],
            fundTransferTypeDescToDisplay: data[mapfields["fundTransferTypeDescToDisplay"]],
            transferTimeName: data[mapfields["transferTimeName"]],
            anyIDType: data[mapfields["anyIDType"]] || this.constants.ANYID_TYPE_BANK_ACCOUNT,
            toBankName: data[mapfields["bankName"]],
            status: data[mapfields["status"]],
            errorMsg: data[mapfields["errorMsg"]],
            //RTP
            rtpReferenceNo: data[mapfields["rtpReferenceNo"]],
            requesterAccountName: data[mapfields["requesterAccountName"]],
            Type: data[mapfields["Type"]],
            requesterIdValue: data[mapfields["requesterIdValue"]],
            expiryDate: data[mapfields["expiryDate"]],
            requesterIdType: data[mapfields["requesterIdType"]],
            rtpMsgType: data[mapfields["rtpMsgType"]],
            receiverAccountName: data[mapfields["receiverAccountName"]],
            receiverIdValue: data[mapfields["receiverIdValue"]],
            receiverIdTypeLabel: data[mapfields["receiverIdTypeLabel"]],
            requesterIdTypeLabel: data[mapfields["requesterIdTypeLabel"]],
            receiverIdType: data[mapfields["receiverIdType"]],
            transactionRef: data[mapfields["transactionRef"]]
        };

        if (this.selectType === 'fundtransfer') {
            receivedDate = this.dateservice.parseDate(data[mapfields["receivedDate"]]);
            txtReceivedDate = this.dateservice.formatDate(receivedDate, 'DD MMM YYYY', this.translateService.currentLang);
            debitDate = this.dateservice.parseDate(data[mapfields["debitDate"]]);
            txtDebitDate = this.dateservice.formatDate(debitDate, 'DD MMM YYYY', this.translateService.currentLang);
            // Get fee detail
            if (this.selectKey && this.selectKey.indexOf('schedule') > -1) {
                let objRequest = { transactionID: data["transactionID"] };
                
                this.fundTransferService.requestTransferFee(objRequest).then((objResponse: any) => {
                    let result = objResponse.responseJSON.result;
                    if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                        let feeAmount = result.value.fee;
                        this.modalData.feeAmount = this.currencyFormatter.transform(feeAmount);
                    }
                }, function (error) {
                    
                });
            } else {
                this.modalData.feeAmount = this.currencyFormatter.transform(data[mapfields["fee"]]) || '0.00';
            }
        } else if(this.selectType === 'biller') {
            debitDate = this.dateservice.parseDate(data[mapfields["debitDate"]]);
            txtDebitDate = this.dateservice.formatDate(debitDate, 'DD MMM YYYY', this.translateService.currentLang);
            if (this.selectKey && this.selectKey.indexOf('schedule') > -1) {
                let objRequest = { transactionID: data["transactionID"] };
                
                this.billPaymentRequestToPayService.requestBillPaymentFee(objRequest).then((objResponse: any) => {
                    let result = objResponse.responseJSON.result;
                    if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                        let feeAmount = result.value.feeAmount;
                        this.modalData.feeAmount = this.currencyFormatter.transform(feeAmount);
                    }
                }, function (error) {
                    
                });
            } else {
                this.modalData.feeAmount = this.currencyFormatter.transform(data[mapfields["feeAmount"]]) || '0.00';
            }
            this.modalData['promptPayBillerId'] = data['promptPayBillerId'];
            this.modalData['profileCode'] = data['profileCode'];
        } else if(this.selectType === 'RTP'){
            this.modalData.Type = this.modalData.rtpMsgType === 'INCOMING' ? this.modalData.requesterIdTypeLabel:this.modalData.receiverIdTypeLabel;
            this.modalData.feeAmount = this.currencyFormatter.transform(data[mapfields["fee"]]) || '0.00';
        }
        this.modalData.amount           = txtAmount;
        this.modalData.debitDate        = txtDebitDate;
        this.modalData.receivedDate     = txtReceivedDate;
        this.modalData.transactionDate  = txtTransactionDate;

        this.anyIDFromDesc = (this.modalData.anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) ? this.modalData.toBankName : this.anyIDTypeList[this.modalData.anyIDType].desc;
    }

    public show(data: any): void {
        this.initialData(data);
        this.timelineModalMessage.show();
        this.utilService.scrollToTop();
    }

    public hide(): void {
        this.timelineModalMessage.hide();
    }
}



