import { Component, ViewChild } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { KKProductAndServicePage } from '../../kk-product-and-service-page';
import { PromptPayRegisterServiceMain } from '../../../../pages/prompt-pay/prompt-pay-register.service';
import { NavController, Navbar } from 'ionic-angular';
@Component({
    selector: 'pib-mobile-kk-product-service',
    templateUrl: 'kk-product-and-service-page.html'
})
export class PIBMobileKKProductAndServicePage extends KKProductAndServicePage {
    @ViewChild(Navbar) navBar: Navbar;
    constructor(public constants: Constants,
        public promptPayRegisterServiceMain: PromptPayRegisterServiceMain,
        protected navCtrl: NavController) {
        super(constants);

        this.alertConfig = {
            title: '',
            type: 'danger',
            message: '',
            show: false,
            option: {}
        };

        this.product = {
            data: [],
            title: 'history',
            subtitle: 'newProduct'
        };

        this.inquiryCustomerAnyIDInformation();
    }

    ionViewDidLoad() {
        this.navBar.backButtonClick = (e:UIEvent)=>{
            this.navCtrl.popToRoot();
        }
    }

    public inquiryCustomerAnyIDInformation() {
        this.promptPayRegisterServiceMain.inquiryCustomerAnyIDInformation().then((objResponse: any) => {
            if (objResponse.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                let resultAnyIDInfo = objResponse.responseJSON.result.value;
                this.promptPayRegisterServiceMain.registerAnyIDInfo = {
                    resultAnyIDInfo: resultAnyIDInfo,
                    responseMsg: objResponse.responseJSON.result.responseStatus.responseCode
                };
            } else {
                this.promptPayRegisterServiceMain.registerAnyIDInfo = {
                    resultAnyIDInfo: null,
                    responseMsg: objResponse.responseJSON.result.responseStatus.errorMessage
                };
                
                //sth
            }
        }, function (error) {
            
        });
    }
}



