import { Component, OnInit } from '@angular/core';
import { NavController, Platform } from 'ionic-angular';
import { TranslateService } from "ng2-translate/src/translate.service";
import { StrFormatPipe } from '../../../../../share/pipe/string-format.pipe';
import { Constants } from '../../../../../share/service/constants';
import { ModalController, AlertController } from 'ionic-angular';
import { BillerRequestToPayMobileAddEdit } from '../../biller-request-to-pay-mobile-add-edit';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../../../bill-payment-request-to-pay.service';
import { UtilService } from '../../../../../share/service/util.service';

@Component({
    selector: 'biller-request-to-pay-edit',
    templateUrl: '../biller-request-to-pay-add-edit.html'

})
export class BillerRequestToPayEdit extends BillerRequestToPayMobileAddEdit implements OnInit {

    public titleBillPayment: string;

    constructor(
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected translate: TranslateService,
        private modalCtrl: ModalController,
        protected alertCtrl: AlertController,
        protected strFormat: StrFormatPipe,
        protected navController: NavController,
        public platform: Platform,
        public utilService: UtilService
    ) {
        super(constants, permissionChangeRoute, billPaymentRequestToPayService, translate, alertCtrl, strFormat, navController, platform);

        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }],
                step: 0,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
        this.channel = this.constants.CHANNEL_PIB_MOBILE;
        this.titleBillPayment = 'label.titleEditBiller';
    }

    ngOnInit() {
        this.isAdd = false;
        this.initMobile();
    }

    public isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }
}



