import { Constants } from '../../../service/constants';
import { OtpService } from '../../verify-otp/otp.service';
import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { TranslateService } from "ng2-translate/src/translate.service";
import { AcceptTermsAndConditionComponent } from '../accept-terms-and-conditions.component';
import { AlertController, ViewController, ModalController, NavController } from 'ionic-angular';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { TermsAndConditionsService } from '../../../../share/component/terms-and-conditions/terms-and-conditions.service';
import { UtilService } from '../../../service/util.service';
@Component({
    selector: 'pib-mobile-accept-terms-and-conditions',
    templateUrl: './accept-terms-and-conditions-modal.html'
})
export class PIBMobileAcceptTermsAndConditionComponent extends AcceptTermsAndConditionComponent implements OnInit {
    private verifyOTPStyle: string;
    private isAgreed: boolean= false;
    constructor(public constants: Constants,
        public otpService: OtpService,
        public viewCtrl: ViewController,
        public permissionService: PermissionService,
        public termsAndConditionsService: TermsAndConditionsService,
        public permissionChangeRoute: PermissionChangeRoute,
        public modalController: ModalController,
        public navController: NavController,
        public alertCtrl: AlertController,
        public translate: TranslateService,

        public cdRef: ChangeDetectorRef,
        public utilService: UtilService) {
        super(constants, otpService,utilService, cdRef)
    }

    ngOnInit(): void {
        this.screenName = this.constants.STYLE_PIB_MOBILE;
        this.termAction = 'pib_term_and_con';
        this.userData = this.permissionService.getUserData();
        this.verifyOTPStyle = this.constants.STYLE_PIB_MOBILE;

        this.unChecked = true;
    }

    // Override a base class method
    onRequestOTP(result) {
        this.isRequestOtp = true;

        if (typeof result.responseCode === "undefined") {
            this.responseOtp = result;
        } else {
            this.showAlertMessage(this.translate.instant('label.warning'), result.errorMessage);
        }
    }

    disAgree(): void {
        this.permissionService.setIsTermAndCon(false);
        this.viewCtrl.dismiss('disaree');
    }

    onSubmit(): void {
        if (this.userData && this.userData.newAccept && !this.isAgreed) {
            let objRequest: any = {
                params: {
                    referenceNO: this.responseOtp.otpRefcode,
                    otp: this.otpPass,
                    tokenOTPForCAA: this.responseOtp.tokenOtp
                }
            };

            this.otpService.setActionCode('ACT_VERIFY_OTP');
            this.otpService.setProcedure('verifyOTPProcedure');
            this.otpService.verifyOtp(objRequest).then((objResponse: any) => {
                let result = objResponse.responseJSON.result;
                if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                    this.saveTermsAndCons();
                    
                } else {
                    this.isAgreed = false;
                    this.showAlertMessage(this.translate.instant('label.warning'), result.responseStatus.errorMessage);
                }
            }, function (error) {

            });
        } else if(!this.isAgreed){
            this.saveTermsAndCons();
        }
    }

    private saveTermsAndCons(): void {
        this.isAgreed = true;
        this.termsAndConditionsService.setActionCode(this.constants.REQ_ACTION_CODE.UPDATE_TERM_AND_CONDITION);
        this.termsAndConditionsService.setTermAction('getTermAndConditionWithLoginProcedure');
        this.termsAndConditionsService.updateTermsAndConditionsService(this.permissionService.tokenId).then((objResponse: any) => {
            let result = objResponse.responseJSON.result;
            if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                this.isAgreed = true;
                if(this.userData.newAccept){
                    this.isGetRBACForFirstLogin();
                }else{
                    this.isAgreed = true;
                    this.permissionService.setIsTermAndCon(true);
                    this.permissionService.setIsLogin(true);
                    this.viewCtrl.dismiss('agree');
                }
            } else {
                this.isAgreed = false;
                this.showAlertMessage(this.translate.instant('label.warning'), result.responseStatus.errorMessage);
            }
        }, function (error) {
            this.isAgreed = false;

        });
    }

    private showAlertMessage(title, message) {
        let alert = this.alertCtrl.create({
            title: title,
            subTitle: message,
            buttons: [
                {
                    text: this.translate.instant('button.ok'),
                    role: 'cancel'
                }
            ]
        });

        alert.present();
    }

    public isGetRBACForFirstLogin(){
       this.termsAndConditionsService.getRBACMenuFirstLogin().then((objResponse: any) => {

            if( objResponse.responseJSON.result.responseStatus.errorMessage != undefined){
                 this.isAgreed = false;
                 this.showAlertMessage(this.translate.instant('label.warning'), objResponse.responseJSON.result.responseStatus.errorMessage);
            }else{
                this.isAgreed = true;
                this.permissionService.setIsTermAndCon(true);
                this.permissionService.setIsLogin(true);
                this.viewCtrl.dismiss('agree');
            }
        }, function (error) {

        });
    }
}



