import { Injectable } from '@angular/core';
import { MfpApi } from '../../share/mfp/mfp-api.service';
import { Constants } from '../../share/service/constants';

@Injectable()
export class DashBoardService {
    private _DashBoardDetails: any;
    private _selectDashBoardDetails: any = null;
    constructor(private _MfpApi: MfpApi,
    private _Constants:Constants) {
    };
    
    set DashBoardDetails(DashBoardDetails: any){
        this._DashBoardDetails = DashBoardDetails;
    }
    get DashBoardDetails(){
        return this._DashBoardDetails;
    }

    set selectDashBoardDetails(selectDashBoardDetails: any){
        this._selectDashBoardDetails = selectDashBoardDetails;
    }
    get selectDashBoardDetails(){
        return this._selectDashBoardDetails;
    }
}


