import { qrGeneratorService } from './qr-generator.service';
import { NavController, NavParams, ModalController } from 'ionic-angular';
import { CurrencyFormatterPipe } from '../../share/pipe/currency-formatter.pipe';
import { QRGeneratorSaveModalComponent } from './qr-generator-save-modal.component';

export class QRGeneratorCompleteBase{
    private qrCodeData: any;
    
    constructor(
    protected qrGeneratorService: qrGeneratorService,
    protected navCtrl: NavController,
    protected currencyFormatter: CurrencyFormatterPipe,
    protected modalCtrl: ModalController
    ) {
    
    }

   initial(){
      this.qrCodeData =  this.qrGeneratorService.getQRCodeData();
      this.qrCodeData.amount = this.qrCodeData.amount == '0.00'? null : this.currencyFormatter.transform(this.currencyFormatter.parse(parseFloat(this.qrCodeData.amount).toString()))
}

   goToMainMenu(){
       this.navCtrl.popToRoot();
   }

   getLogoPosition(){
      var QRLogo = document.getElementById('QRlogo').style;
      QRLogo.left = (window.screen.width/2)- (35/2) + "px";
   }

   

   save(){
        let prepareToSaveModal = this.qrCodeData;
        prepareToSaveModal.qrCodeBase64 = document.getElementsByTagName('qr-code')[0].getElementsByTagName('img')[0].src;
        let modal = this.modalCtrl.create(QRGeneratorSaveModalComponent, { qrCodeData: prepareToSaveModal });
        modal.present();
   }
   
}


