import { NavController } from 'ionic-angular';
import { Component, OnInit, AfterViewInit, } from '@angular/core';
import { MyAccountService } from '../../pages/my-account/my-account.service';
import { PermissionChangeRoute, PermissionService } from '../../share/service/permission.service';
import { DashBoardService } from './dashboard.service';
import { MyAccountStatementPageComponent } from '../../pages/my-account/mobile/pib/my-account-statement-page.component';
import { Constants } from '../../share/service/constants';
@Component({
  selector: 'dashboard-detail',
  templateUrl: './dashboard-detail.html'
})
// Component class
export class DashBoardDetailComponent implements OnInit {
  private permissionManage: any = {};
  private selectAccount: any;
  private pageType: String;
  constructor(private navCtrl: NavController,
    private myAccountService: MyAccountService,
    private permissionChangeRoute: PermissionChangeRoute,
    private permissionService: PermissionService,
    private dashBoardService: DashBoardService,
    private constants: Constants) {
  }

    ngOnInit(): void {
        this.selectAccount = this.dashBoardService.DashBoardDetails.data;
        this.pageType = this.dashBoardService.DashBoardDetails.Type;
        this.checkPermissionManage();
    }
  

    private checkIsAllow(productID, productAllow){

    let isProductAllow = this.permissionService.isProductAllow(productID, productAllow);
    let isAllow:boolean = true;
    let isClosed:boolean =  true;
    
    if(this.selectAccount.statusCode != 1){
      isClosed = false;
    }

    switch(productAllow) { 
      
          case 'allowTransferFrom': {
            isAllow = this.permissionService.getShortcutActionCodePermission('FUND_TRANSFER','FUND_TRANSFER') && isProductAllow && isClosed;
            break; 
          } 
          case 'allowStatement': { 
            isAllow = this.permissionService.getActionCode().VIEW_STATEMENT_MY_ACCOUNTS && isProductAllow;
            break; 
          } 

          case 'allowPayment': { 
            isAllow = isProductAllow && isClosed && this.permissionService.getShortcutActionCodePermission('PAY_BILL','PAY_BILL');
            break; 
          } 
          
          default: { 
            isAllow = true;
            break; 
          } 
      } 
    return isAllow;
  }

  private onTabsSelect(selected): void {
    this.myAccountService.selectAccountDetailData = this.selectAccount;
    switch(selected){
        case 'statement':
          this.navCtrl.push(MyAccountStatementPageComponent);
          break;
        case 'transfer':
          let accountStatus = this.selectAccount.statusCode;
          this.permissionChangeRoute.targetAction = accountStatus !== '2' ? 'MY_ACCOUNT' : 'NEW';
          this.permissionChangeRoute.changeRoute('FUND_TRANSFER');
          break;
        case 'schedule':
          this.permissionChangeRoute.changeRoute('SCHEDULE_FUND_TRANSFER');
          break;
        case 'billpayment':
          this.permissionChangeRoute.prevUrl='MY_ACCOUNTS.DETAIL';
          this.permissionChangeRoute.changeRoute('PAY_BILL');
          break;
        // case 'createRTP':
        //   this.dashBoardService.selectDashBoardDetails = this.selectAccount;
        //   this.permissionChangeRoute.prevUrl='MY_ACCOUNTS.DETAIL';
        //   this.permissionChangeRoute.changeRoute('MY_RTP.ADD');
        //   break;
        case 'edit':
          this.myAccountService.selectAccountDetailData = this.selectAccount;
          this.permissionChangeRoute.changeRoute('MY_DEPOSITS.EDIT');
          break;
        default:
          break;
      }
  }

  checkPermissionManage(){
     switch(this.pageType){
     case this.constants.DASHBOARD_MENU.MY_ACCOUNT:
          let productID = this.selectAccount.productID;
          this.permissionManage[this.constants.DASHBOARD_MENU.MY_ACCOUNT] = {
            transfer: this.checkIsAllow(productID, 'allowTransferFrom'),
            viewStatement: this.checkIsAllow(productID, 'allowStatement'),
            schedule: this.checkIsAllow(productID, 'allowSchedule') && this.permissionService.getShortcutActionCodePermission('SCHEDULE_FUND_TRANSFER','VIEW_TRANSFER_SCHEDULE'),
            edit: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS,
            payBill: this.checkIsAllow(productID, 'allowPayment')
          }
          
          break;
    //  case this.constants.DASHBOARD_MENU.MY_PROMPTPAY:
    //       this.permissionManage[this.constants.DASHBOARD_MENU.MY_PROMPTPAY] = {
    //         createRTP: true
    //       }
    //       
    //       break;
    }
  }
  
}



