import { Component } from '@angular/core';
import { ModalController, ViewController } from 'ionic-angular';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../../bill-payment-request-to-pay.service';

@Component({
    selector:   'biller-list',
    templateUrl: 'biller-list.html'
    
})
export class BillerRequestToPayListUserProfile {
    
    public actionCode;
    public iReloadData: number = 0;
    public addBillerPromptPayForm: any;
    
    constructor(public permissionChangeRoute: PermissionChangeRoute,
        public permissionService: PermissionService,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        public viewCtrl: ViewController,
        public modalCtrl: ModalController) {
            
    }


    ngOnInit() {
        
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    gotoAddBiller(){
        this.permissionChangeRoute.changeRoute('MANAGE_BILLER.add');
    }

    ionViewWillEnter() {
        
        if (this.permissionChangeRoute.prevUrl === 'BILLER_DETAIL') {
        //     this.permissionChangeRoute.prevUrl = '';
            this.iReloadData++;
        }
    }
}


