import { SchedulePage } from '../../schedule-page';
import { Component, OnInit, ViewChild } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { AccountService } from '../../../../share/service/account.service';
import { BillPaymentService } from '../../../bill-payment/bill-payment.service';
import { PermissionService } from '../../../../share/service/permission.service';
import { MasterDataService } from '../../../../share/service/master-data.service';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { FundTransferService } from '../../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { ScheduleFundTransferService } from '../../../../pages/schedule/schedule-fundtransfer.service';
import { UtilService } from "../../../../share/service/util.service";

@Component({
    selector: 'schedule-billpayment-page',
    templateUrl: './schedule-page.html',
    providers: [ScheduleFundTransferService]
})
export class SchedulePageBillPaymentComponent extends SchedulePage implements OnInit {
    schedData: any;
    massagePopup: any;
    massageTitlePopup: any;
    deleteScheduleLevel: any;
    selectOptions: { title: string; };
    alertConfig: { title: string, type: string, message: string, show: boolean, option: any };

    constructor(public constants: Constants,
        public translate: TranslateService,
        public fundTransferService: FundTransferService,
        public billPaymentService: BillPaymentService,
        public permissionChangeRoute: PermissionChangeRoute,
        public bankCodeDataService: BankCodeDataService,
        public masterDataService: MasterDataService,
        private navCtrl: NavController,
        private alertCtrl: AlertController,
        private scheduleService: ScheduleFundTransferService,
        public permissionService: PermissionService,
        public accountService : AccountService,
        public utilService: UtilService
    ) {
        super(constants, translate, fundTransferService, billPaymentService, bankCodeDataService, masterDataService, permissionService, accountService,utilService);
    }

    ngOnInit(): void {
        this.selectOptions = {
            title: 'Select Period'
        };

        this.dropdownListPeriodOption = {
            isopen: false
        };

        this.monthList = {
            options: [],
            selected: { label: "", value: "" }
        };

        this.scheduleData = {
            period: ''
        };

        this.timeline = {
            data: {
                rawData: [],
                isAdded: false,
                resetData: true
            },
            title: 'schedule',
            subtitle: 'biller'
        };

        // this.initialMonthList().then((result: any) => {
            // this.scheduleData.period = this.monthList.selected.value;
            this.getBillPaymentSchedule();
        // });
    }

    onActions(result: any) {

        if (result.name === 'info') {
            //let tmpdata = result.data;
            this.billPaymentService.setBillPaymentObj(result.data.rawData);
            this.permissionChangeRoute.changeRoute('SCHEDULE_BILL_PAYMENT.DETAIL');
        } else if (result.name === 'delete') {
            //let tmpdata = result.data;
            this.schedData = result.data;
            this.editSchedule();
        }
    }

    onChangePeriod(period: any) {
        this.switchScheduleType('biller');
    }

    editSchedule(): void {
        if (this.schedData.rawData.recurringTimes == 0 || this.schedData.rawData.scheduleType == 0) {
            this.showConfirmPopup();
        } else {
            this.showConfirmDialog();
        }
    }

    showConfirmDialog() {
        let alert = this.alertCtrl.create();
        alert.setTitle(this.translate.instant("lbl.deleteScheduleThistime"));

        alert.addInput({
            type: 'radio',
            label: this.translate.instant("lbl.thisTime"),
            value: this.constants.RECURRING_THIS_TIME,
            checked: true
        });

        alert.addInput({
            type: 'radio',
            label: this.translate.instant("lbl.allSchedule"),
            value: this.constants.RECURRING_ALL_SCHEDULE,
            checked: false
        });

        alert.addButton(this.translate.instant("btn.close"));
        alert.addButton({
            text: this.translate.instant("btn.delete"),
            handler: data => {
                if (data === this.constants.RECURRING_ALL_SCHEDULE) {
                    this.deleteBillPaymentSchedule(this.constants.RECURRING_ALL_SCHEDULE);
                } else if (data === this.constants.RECURRING_THIS_TIME) {
                    this.deleteBillPaymentSchedule(this.constants.RECURRING_THIS_TIME);
                } else {
                    this.deleteBillPaymentSchedule(this.constants.RECURRING_THIS_TIME);
                }
            }
        });

        alert.present();
    }

    showConfirmPopup() {
        let confirm = this.alertCtrl.create({
            title: this.translate.instant('lbl.deleteScheduleThistime'),

            buttons: [{
                text: this.translate.instant('button.cancel'),
                handler: () => {
                }
            },
            {
                text: this.translate.instant('button.confirm'),
                handler: () => {
                    this.deleteBillPaymentSchedule(this.constants.RECURRING_THIS_TIME);
                }
            }]
        });

        confirm.present();
    }

    showSuccessPopup() {
        let successPopup = this.alertCtrl.create({
            title: this.translate.instant(this.massageTitlePopup),
            subTitle: this.translate.instant(this.massagePopup),
            buttons: [{
                text: this.translate.instant('button.ok'),
                handler: () => {
                    this.navCtrl.popToRoot();
                }
            }]
        });
        successPopup.present();
    }

    deleteBillPaymentSchedule(param) {
        this.deleteScheduleLevel = param;

        if (this.deleteScheduleLevel === this.constants.RECURRING_THIS_TIME) {
            let params = {
                transactionID: this.schedData.rawData.transactionID
            };

            this.scheduleService.setParam(params);
            this.scheduleService.setActionCode(this.constants.REQ_ACTION_CODE.DELETE_BILL_SCHEDULE_ONETIME);
            this.scheduleService.setProcedure(this.constants.REQ_PROCEDURE_NAME.DELETE_BILL_SCHEDULE_ONETIME);
            this.scheduleService.invokeOptions = {};

            this.scheduleService.deleteSchedule().then((result: any) => {
                if (result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {

                    this.scheduleService.setScheduleRecurring = "";
                    this.massagePopup = 'label.removeScheduleSuccess';
                    this.massageTitlePopup = 'label.Success'
                    this.getBillPaymentSchedule();
                    this.showSuccessPopup();
                } else {
                    this.massagePopup = result.responseJSON.result.responseStatus;
                    this.massageTitlePopup = 'lbl.transferStatus.fail'
                    this.onError(this.massagePopup);
                    this.scheduleService.setScheduleRecurring = "";
                }

            }, function (error) {

            });

        } else if (this.deleteScheduleLevel === this.constants.RECURRING_ALL_SCHEDULE) {

            let params = {
                masterTransactionID: this.schedData.rawData.masterTransactionID,
            };

            this.scheduleService.setParam(params);
            this.scheduleService.setActionCode(this.constants.REQ_ACTION_CODE.DELETE_BILL_SCHEDULE_ALL);
            this.scheduleService.setProcedure(this.constants.REQ_PROCEDURE_NAME.DELETE_BILL_SCHEDULE_ALL);
            this.scheduleService.invokeOptions = {};
            
            this.scheduleService.deleteSchedule().then((result: any) => {
                if (result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {

                    this.scheduleService.setScheduleRecurring = "";
                    this.massagePopup = 'label.removeScheduleSuccess';
                    this.massageTitlePopup = 'label.Success'
                    this.getBillPaymentSchedule();
                    this.showSuccessPopup();
                } else {
                    this.massageTitlePopup = 'lbl.transferStatus.fail'
                    this.massagePopup = result.responseJSON.result.responseStatus;
                    this.onError(this.massagePopup);
                    this.scheduleService.setScheduleRecurring = "";
                }

            }, function (error) {

            });
        }
    }
}



