import {Component, OnDestroy, OnInit} from "@angular/core";
import {Constants} from "../../share/service/constants";
import {QrScannerService} from "../qr-scanner/qr-scanner.service";
import {TranslateService} from "ng2-translate";
import {BankCodeDataService} from "../../share/service/bankcode-data.service";
import {QRVerifyDetail} from "../../share/bean/qr-verify-detail";
import * as moment from 'moment';
import {UtilService} from "../../share/service/util.service";
import {MasterDataService} from "../../share/service/master-data.service";

@Component({
    selector: 'verify-slip',
    templateUrl: './verify-slip.component.html'
})

export class VerifySlipComponent implements OnInit {
    verifySlipObj: QRVerifyDetail;
    senderImgUrl: any;
    senderBank: { bankName: '' };
    receiverImgUrl: any;
    receiverBank: { bankName: '' };
    dateLabel: any;
    toLabel: any;
    transactionDate: any;
    bankList: any;
    anyIdList: any;
    title: any;

    constructor(
        private constants: Constants,
        private qrScannerService: QrScannerService,
        private translateService: TranslateService,
        private bankCodeDataService: BankCodeDataService,
        private utilService: UtilService,
    ) {

    }

    ngOnInit(): void {
        this.verifySlipObj = this.qrScannerService.selectVerifySlipDetailData;
        this.bankList = this.verifySlipObj.bankList;
        this.anyIdList = this.verifySlipObj.anyIdList;
        this.senderImgUrl = this.bankCodeDataService.getBankCodeImageProperty(this.verifySlipObj.sendingBank);
        console.log(this.senderImgUrl)
        let date = this.verifySlipObj.transDate + this.verifySlipObj.transTime;
        this.transactionDate = moment(date, 'YYYYMMDDHH:mm:ss').format('DD MMM YYYY HH:mm:ss');
        this.senderBank = this.bankList.find(bank => bank.bankCode === this.verifySlipObj.sendingBank);
        if (this.verifySlipObj.action === 'BILL') {
            this.title = this.translateService.instant('lbl.verifyQR.bill.title');
            this.verifySlipObj.pageTitle = this.translateService.instant('lbl.verifyQR.sub.title');
            this.dateLabel = this.translateService.instant('lbl.paymentDate');
            this.toLabel = this.translateService.instant('lbl.toBiller');

            if (this.isEDonationCategory()) {
                this.receiverImgUrl = this.utilService.getEDonateIcon({billerProfileId: this.verifySlipObj.receiver.account.value});
            }else {
                this.receiverImgUrl = this.utilService.getBillerIcon({billerProfileId: this.verifySlipObj.receiver.account.value});
            }
            console.log(this.receiverImgUrl)
        } else if (this.verifySlipObj.action === 'FUND') {
            this.title = this.translateService.instant('lbl.verifyQR.fund.title');
            this.verifySlipObj.pageTitle = this.translateService.instant('lbl.verifyQR.sub.title');
            this.dateLabel = this.translateService.instant('lbl.debitDate');
            this.toLabel = this.translateService.instant('lbl.transferTo');
            this.getReceiverImage();
        }

        console.log(this.verifySlipObj)
    }

    isVerifyBill() {
        return this.verifySlipObj.action === 'BILL';
    }

    isFundTransfer() {
        return this.verifySlipObj.action === 'FUND';
    }

    getReceiverImage() {
        if (this.verifySlipObj &&
            this.verifySlipObj.receiver &&
            this.verifySlipObj.receiver.account &&
            this.verifySlipObj.receiver.account.type) {
            if (this.verifySlipObj.receiver.account.type == this.constants.ANYID_TYPE_MOBILE ||
                this.verifySlipObj.receiver.account.type == this.constants.ANYID_TYPE_CITIZEN_ID ||
                this.verifySlipObj.receiver.account.type == this.constants.ANYID_TYPE_E_WALLET) {
                let receiverAnyIdType = this.verifySlipObj.receiver.account.type;
                let current = this.anyIdList[receiverAnyIdType];
                this.receiverImgUrl = {
                    url: current.iconObj.icon,
                    color: current.iconObj.iconColor,
                };
                this.receiverBank = {
                    bankName: ''
                };
            }else {
                this.receiverBank = this.bankList.find(bank => bank.bankCode === this.verifySlipObj.receivingBank);
                this.receiverImgUrl = this.bankCodeDataService.getBankCodeImageProperty(this.verifySlipObj.receivingBank);
            }
        }
    }

    isAnyIdType(type) {
        return type == this.constants.ANYID_TYPE_MOBILE ||
            type == this.constants.ANYID_TYPE_CITIZEN_ID ||
            type == this.constants.ANYID_TYPE_E_WALLET;
    }

    isEDonationCategory() {
        return this.verifySlipObj.categoryId == this.constants.E_DONATION_CATEGORY_ID;
    }


}


