import { Router } from '@angular/router';
import { NavController, AlertController, ItemSliding } from 'ionic-angular';
import { Constants } from '../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../share/service/master-data.service';
import { Component, trigger, animate, style, state, transition, Input, OnInit } from '@angular/core';
import { PermissionChangeRoute, PermissionService } from '../../share/service/permission.service';
import { MyAccountService } from '../../pages/my-account/my-account.service';
import { OrderByPipe } from '../../share/pipe/order-by.pipe';
import { RequestToPayService } from '../../pages/request-to-pay/request-to-pay.service';
import { DashBoardService } from './dashboard.service';
@Component({
  selector: 'dash-board',
  templateUrl: './dashboard.html',
  animations: [
    trigger('accordingState', [
      state('collapsed', style({
        height: '0px'
      })),
      state('expanded', style({
        height: '*'
      })),
      transition('collapsed <=> expanded', [animate(200)])
    ])
  ]
})
export class DashBoardComponent implements OnInit{

  private groupTypes: [{Type: string,headerLable: string,isOpen:string, Data:any}];
  private _itemSliding: any = null;
  private finishLoadMyAccount: boolean = false;
  //private finishLoadMyPromptPay: boolean = false;
  private permissionManage: any;
  constructor(
    private constants: Constants,
    private router: Router,
    private alertCtrl: AlertController,
    private navCtrl: NavController,
    private masterDataService: MasterDataService,
    private permissionChangeRoute: PermissionChangeRoute,
    private permissionService: PermissionService,
    private myAccountService: MyAccountService,
    private orderBy: OrderByPipe,
    private requestToPayService: RequestToPayService,
    private dashBoardService: DashBoardService,
    private translate: TranslateService,
    private NavCtrl: NavController
    ) {
    
  }

  ngOnInit(){
    //init first group Type as myaccount in Dashboard 
    this.groupTypes = [
       { 
            Type: this.constants.DASHBOARD_MENU.MY_ACCOUNT,
            headerLable: this.translate.instant('label.savingsaccount'),
            isOpen: 'expanded',
            Data: []
       }
      //  ,
      //  {
      //       Type: this.constants.DASHBOARD_MENU.MY_PROMPTPAY,
      //       headerLable: this.translate.instant('label.promptpayServiceHeader'),
      //       isOpen: 'collapsed',
      //       Data: []
      //  }
    ];
     this.permissionManage = {
      delete: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS,
      moreInfo: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS,
      add: this.permissionService.getActionCode().MANAGE_MY_ACCOUNTS
    };
    //prepare service
    this.getMyAccount();
    //this.getMyPromptPay();
  }

  private navigateToDashBoardDetailPage(data: any, slidingItem: ItemSliding, Type:String ): void {
    slidingItem.close();
    if(this.permissionManage.moreInfo){
      let selectAccountDetailData = {
        Type: Type,
        data: data
      }
      this.dashBoardService.DashBoardDetails = selectAccountDetailData;
      this.permissionChangeRoute.changeRoute('MY_DEPOSITS.DETAIL');
    }
  }

  private toggleState(selectState) {
    for (var key in this.groupTypes){
        if (this.groupTypes[key].Type === selectState){
            this.groupTypes[key].isOpen = 'expanded';
        } else {
            this.groupTypes[key].isOpen = 'collapsed';
        }
    }
  }

  private itemSliding(slidingItem: ItemSliding){
    this._itemSliding = slidingItem;
  }

  private getMyAccount(): void {
    this.myAccountService.requestDashboard().then((objResponse: any) => {
      let result = objResponse.responseJSON.result;
      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
            this.groupTypes[this.groupTypes
            .findIndex(x => x.Type == this.constants.DASHBOARD_MENU.MY_ACCOUNT)].Data = this.orderBy.aliasName(result.value, '');
      }
      // else{
      //       this.showAlert(objResponse.responseJSON.result.responseStatus.errorMessage,this.translate.instant('label.warning'));
      // }
      this.finishLoadMyAccount = true;
    });
  }

  // private getMyPromptPay(){
  //   this.masterDataService.getAllAnyIDTypes().then((result: any) => {
  //       let anyIDTypes = result;
  //       for(let i in anyIDTypes){
  //           if (anyIDTypes[i].anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) {
  //               delete anyIDTypes[i];
  //           }
  //       }
  //       this.requestToPayService.inquiryRequestToPayAnyIdMy(anyIDTypes).then((result: any) => {
          
  //         if(result.errorMessage === undefined && result.length > 0){
  //              this.groupTypes[this.groupTypes.findIndex(x => x.Type == this.constants.DASHBOARD_MENU.MY_PROMPTPAY)].Data = result;
              
  //         } else if(result.errorMessage !== undefined) {
  //             this.showAlert(result.errorMessage , this.translate.instant('label.warning'));
  //         }
  //         this.finishLoadMyPromptPay = true;
  //     });
  //   });
  // }

  private showConfirmDeleteMyAccount(myAccount: any, slidingItem: ItemSliding) {
    let confirm = this.alertCtrl.create({
      title: this.translate.instant('label.delete.title.myaccount'),
      message: this.translate.instant('lbl.deleteMyAccountMsg'),
      buttons: [{
        text: this.translate.instant('button.cancel'),
        handler: () => {

        }
      },
      {
        text: this.translate.instant('button.ok'),
        handler: () => {
          slidingItem.close();
          this.deleteMyAccount(myAccount);
        }
      }]
    });
    confirm.present();
  }

  private deleteMyAccount(myAccount: any) {
    this.myAccountService.deleteMyAccount(myAccount).then((objResponse: any) => {
      let result = objResponse.responseJSON.result;
      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {

        let alert = this.alertCtrl.create({
          title: this.translate.instant('label.Success'),
          subTitle: this.translate.instant('label.deleteMyAccountSuccess', { myAccountNumber: myAccount.myAccountNumber }),
          buttons: [{
            text: this.translate.instant('button.backToHome'),
            role: 'cancel',
            handler: () => {
              this.myAccountService.newMyAccountData = undefined;
              this.NavCtrl.popToRoot();
            }
          }]
        });

        alert.present();
      }
    });
  }

  private showAlert(messageAlert, title) {
      let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
  }

  gotoAddAccountPage() {
      this.myAccountService.newMyAccountData = undefined;
      this.permissionChangeRoute.changeRoute('MY_DEPOSITS.ADD');
  }
  
}



