import { AlertController } from 'ionic-angular';
import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../../share/service/constants';
import { OtherAccountService } from '../../../other-account.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { UtilService } from '../../../../../share/service/util.service';
import { PermissionAction } from '../../../../../share/service/permission.service';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { BankCodeDataService } from '../../../../../share/service/bankcode-data.service';
import { MobileOtherAccountAddEditConfirm } from '../../../../../pages/other-account/mobile/other-account-add-edit-confirm';
import { NavController } from 'ionic-angular';

@Component({
    selector: 'edit-other-account-confirm',
    templateUrl: '../../other-account-add-edit-confirm.html'
})
export class PIBMobileEditConfirmOtherAccountComponent extends MobileOtherAccountAddEditConfirm implements OnInit {
    public catName: any;
    public bankName: any;
    confirmAddEditExtAccountData: any;

    constructor(public permissionAction: PermissionAction,
        public constants: Constants,
        public otherAccountService: OtherAccountService,
        public permissionChangeRoute: PermissionChangeRoute,
        public alertCtrl: AlertController,
        public translateService: TranslateService,
        public bankCodeDataService: BankCodeDataService,
        public utilService: UtilService,
        public nav: NavController) {
        super(permissionAction, constants, otherAccountService, permissionChangeRoute, alertCtrl, translateService, bankCodeDataService, utilService,nav);
        this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }],
                step: 1,
                style: this.constants.STYLE_PIB_MOBILE
            }
        }
        this.verifyOTPStyle = this.constants.STYLE_PIB_MOBILE;
        this.verifyOTPAction = this.constants.ACTION_CODE_ADD_EXT_ACCOUNT;
        this.setMSGLang();
        this.language = this.translateService.currentLang;
    }

    ngOnInit(): void {
        this.editOtherAcc = true;
        this.titlePage = 'label.editAcc';
        this.confirmAddEditExtAccountData = this.otherAccountService.confirmEditExtAccountData;
        this.confirmAddEditExtAccountData.acctAliasName = this.confirmAddEditExtAccountData.accountAliasname;
        this.confirmAddEditExtAccountData.acctName = this.confirmAddEditExtAccountData.accountName;
    }

    onSubmitEditOtherAcc() {
        let params = {
            externalAccount: {
                acctAliasName: this.confirmAddEditExtAccountData.acctAliasName,
                acctName: this.confirmAddEditExtAccountData.accountName,
                acctNo: this.confirmAddEditExtAccountData.acctNo,
                anyIDType: this.confirmAddEditExtAccountData.anyIDType,
                bankCode: this.confirmAddEditExtAccountData.bankCode,
                bankName: this.confirmAddEditExtAccountData.bankName,
                catId: this.confirmAddEditExtAccountData.category,
                email: this.confirmAddEditExtAccountData.email,
                exAcctId: this.confirmAddEditExtAccountData.exAcctId,
                isFavourite: this.confirmAddEditExtAccountData.isFavourite,
                mobile: this.confirmAddEditExtAccountData.mobile,
                msgLang: this.confirmAddEditExtAccountData.mobile === '' ? '' : this.constants.CULTURE_SHORTNAME_ENGLISH,
                productId: '',
                status: ''
            }
        };

        this.otherAccountService.setParam(params);
        this.otherAccountService.setActionCode('ACT_EXTERNAL_ACCOUNT_EDIT');
        this.otherAccountService.setProcedure('editExternalAccountProcedure');
        this.otherAccountService.masterData().then((objResponse: any) => {
            let result = objResponse.responseJSON.result;

            if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                let alert = this.alertCtrl.create({
                    title: this.translateService.instant('label.Success'),
                    subTitle: this.translateService.instant('label.editOtherAccountSuccess'),
                    buttons: [
                        {
                            // text: this.translateService.instant('button.ok'),
                            text: this.translateService.instant('button.backToHome'),
                            role: 'cancel',
                            handler: () => {
                            	
//                                this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS');
                            	this.nav.popToRoot();
                            }
                        }
                    ]
                });

                alert.present();
            } else {
                let messageAlert = result.responseStatus.errorMessage;
                this.showAlert(messageAlert);
            }
        }, function (error) {
            
        });
    }
}



