import { Component, OnInit } from '@angular/core';
import { Dictionary } from '../../../../../share/bean/dictionary';
import { Constants } from '../../../../../share/service/constants';
import { StrFormatPipe } from '../../../../../share/pipe/string-format.pipe';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../../../share/service/master-data.service';
import { ModalController, Platform, NavParams, ViewController, AlertController } from 'ionic-angular';
import { RequestToPayService } from '../../../request-to-pay.service'
import { AnyIDTypeBean } from '../../../../../share/bean/anyid-type-bean';
@Component({
    selector: 'to-account-list-modal',
    templateUrl: './to-account-list-modal.html'
})

export class ToAccountListModal implements OnInit {

    private model: any = {
        anyIDType: this.constants.ANYID_TYPE_CITIZEN_ID,
        accountNo: '',
        accountName: ''
    };

    private dict: Dictionary = {
        'accountNo': '{0}',
        'accountName': 'lbl.accountName'
    };

    swiperOptions: any;
    isInitEdit: boolean;
    inputMenuSwiper: any;
    inputPattern: string;
    rangeLength: [number];
    private anyIDTypes: any = [];
    public submitted: boolean = false;
    private currentAccount: any;
    private displayNames: any;
    private accAnyIDType: any;
    private otherAccounts: any = [];
    public firstLoad: boolean = true;
    action: string = "list";

    constructor(
        private params: NavParams,
        private platform: Platform,
        public constants: Constants,
        private viewCtrl: ViewController,
        private strFormat: StrFormatPipe,
        public alertCtrl: AlertController,
        private modalCtrl: ModalController,
        private translate: TranslateService,
        private masterDataService: MasterDataService,
        private requestToPayService: RequestToPayService
    ) {
    }

    ngOnInit(): void {
        this.inputMenuSwiper = [];
        this.isInitEdit = true;
        this.swiperOptions = { lock: false, index: 0 }
        this.currentAccount = this.params.get('account') || {};

        this.displayNames = this.dict != null ? Object.keys(this.dict) : [];

        this.masterDataService.getAllAnyIDTypes().then((result: any) => {
            let index: number = 0
            this.anyIDTypes = result;
            this.accAnyIDType = this.anyIDTypes[this.constants.ANYID_TYPE_CITIZEN_ID];
            this.rangeLength = this.accAnyIDType.getRangeLength();
            this.inputPattern = this.accAnyIDType.getRegexPattern();
            for(let i in this.anyIDTypes){
                if (this.anyIDTypes[i].anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) {
                    delete this.anyIDTypes[i];
                }
            }
            this.getAnyIDTypes().forEach(type => {
                let anyIDType = this.anyIDTypes[type];
                    this.inputMenuSwiper.push({
                        srcImg: anyIDType.iconObj.icon,
                        type: anyIDType.anyIDType,
                        name: anyIDType.desc,
                        color: anyIDType.iconObj.iconColor,
                        index: index
                    });
                index++;
            });

            this.requestToPayService.getOtherAccounts(this.anyIDTypes).then((result: any) => {
                result.splice(0, 1); // Removes the "New Account" category from otherAccounts.
                this.otherAccounts = result;
                if(this.otherAccounts.length > 0) { this.toggleGroup(this.otherAccounts[0]); }
                this.firstLoad = false;
             });
        });
    }

    onSelectChanged(newValue) {
        this.viewCtrl.dismiss(newValue);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    toggleGroup(group) {
        group.show = !group.show;
    }

    isGroupShown(group) {
        return group.show;
    }

    getAnyIDTypes(): any {
        return this.anyIDTypes != null ? Object.keys(this.anyIDTypes) : [];
    }

    swiperChange(newValue) {
        if (!this.isInitEdit) {
            
            this.model.anyIDType = newValue.type;
            this.currentAccount = [];
            this.model.accountNo = "";
            this.model.accountName = "";
            this.submitted = false;
            this.accAnyIDType = this.anyIDTypes[newValue.type];
            this.rangeLength = this.accAnyIDType.getRangeLength();
            this.inputPattern = this.accAnyIDType.getRegexPattern();

            let txtAccountNo = document.getElementsByName("accountNo")[1];
            if (txtAccountNo !== undefined) {
                txtAccountNo.setAttribute('maxlength', this.rangeLength[1].toString());
                txtAccountNo.setAttribute('ng-reflect-range-length', this.rangeLength.toString());
            }
        }

        this.isInitEdit = false;
    }

    addAccount(accForm): void {
        let data = accForm.value;
        let anyIDType = this.anyIDTypes[data.anyIDType];
        let validationResult = this.validateAccountForm(accForm);

        if (!validationResult.valid) {
            this.showAlert(this.strFormat.transform(validationResult.message, [this.strFormat.transform(this.dict[validationResult.displayName], [anyIDType.label]), anyIDType.valueLength]));
        } else {
            this.submitted = true;
            let anyIDType = this.anyIDTypes[data.anyIDType] || new AnyIDTypeBean();

            let category = {
                catId : 999,
                catName : this.translate.instant('lbl.newAccount')
            };
            
            let accountObj = {
                accId : -1,
                accNo : data.accountNo,
                anyIDType : anyIDType,
                category : category,
                aliasName: ""
            };

            anyIDType.shortName = anyIDType.iconObj.shortName;

            this.viewCtrl.dismiss(accountObj);
        }
    }

    private showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    private validateAccountForm(form) {
        let validationResult = { valid: true, displayName: '', message: '' };

        this.displayNames.forEach(function (name, index) {
            if (validationResult.valid) {
                if (form.controls[name] != undefined && form.controls[name] != null && form.controls[name].errors != null) {
                    for (let err in form.controls[name].errors) {
                        if (['min', 'required'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                            break;
                        }
                        if (['pattern', 'rangeLength'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.invalid' };
                            break;
                        }
                    }
                }
            }
        });

        return validationResult;
    }
}


