import {BankBean} from "./bank-bean";
import {AnyIDTypeBean} from "./anyid-type-bean";

export class QRVerifyDetail {
    action: string;
    amount: string;
    countryCode: string;
    paidLocalAmount: string;
    paidLocalCurrency: boolean;
    receivingBank: boolean;
    sendingBank: boolean;
    ref1: string;
    ref2: string;
    ref3: string;
    transDate: any;
    transTime: any;
    transRef: any;
    receiver: Receiver;
    sender: Sender;
    pageTitle: string;
    bankList: Array<BankBean> = [];
    anyIdList: Array<AnyIDTypeBean> = [];
    ref1Label: string;
    ref2Label: string;
    ref3Label: string;

    ref1Value: string;
    ref2Value: string;
    ref3Value: string;

    refInfos: any;

    categoryId: any;
}

export class Account {
    value: string;
    type: string;
}

export class Receiver {
    displayName: string;
    name: string;
    proxy: string;
    account: Account;
}

export class Sender {
    displayName: string;
    name: string;
    proxy: string;
    account: Account;
}


