import { Component } from '@angular/core';
import { Constants } from '../../../../share/service/constants';

@Component({
    selector: 'term-and-condition',
    templateUrl: 'term-and-con.html',
})

export class TermAndConPIBMobile {
    public isError = true;
    termAction: string = 'pib_term_and_con';

    constructor(
        private constants: Constants,
    ) {
    }

    responseServiceChecked($event): void {
        if ($event == this.constants.RESP_CODE_SUCCESS) {
            this.isError = false;
        } else if ($event === 'error') {
        }
    }
}



