
import { ViewChild } from '@angular/core';
import { NavController, Navbar, Platform } from 'ionic-angular';
import { TranslateService } from "ng2-translate/src/translate.service";
import { StrFormatPipe } from '../../../share/pipe/string-format.pipe';
import { Constants } from '../../../share/service/constants';
import { AlertController } from 'ionic-angular';
import { PermissionChangeRoute } from '../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../bill-payment-request-to-pay.service';
import { BillerRequestToPayAddEdit } from '../biller-request-to-pay-add-edit';
import { BillerProfileBean } from '../../../share/bean/biller-profile-bean';

export class BillerRequestToPayMobileAddEdit extends BillerRequestToPayAddEdit {

    public isAndroid: boolean = false;
    @ViewChild('navbar') navBar: Navbar;

    constructor(
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected translate: TranslateService,
        protected alertCtrl: AlertController,
        protected strFormat: StrFormatPipe,
        public nav: NavController,
        public platform: Platform) {
        super(constants, permissionChangeRoute, billPaymentRequestToPayService, translate);
    }

    initMobile() {

        this.isAndroid = this.platform.is("android");
        this.initModel();
    }

    onSubmit(billerForm) {
        let validationResult = this.validateForm(billerForm);
        if (!validationResult.valid) {
            let messageAlert = this.strFormat.transform(validationResult.message, [validationResult.displayName]);
            this.showAlert(messageAlert, this.translate.instant('label.warning'));
        } else {
            this.selectedBillerProfile.aliasName = this.model.aliasName;
            if (this.isAdd) {
                this.billPaymentRequestToPayService.verifyAddBillerProfileDetail(this.selectedBillerProfile).then((result: any) => {
                    if (result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                        let billerProfile: BillerProfileBean = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(result.responseJSON.result.value);
                        this.billPaymentRequestToPayService.setConfirmBillerProfile(billerProfile);
                        this.permissionChangeRoute.changeRoute('MANAGE_BILLER.add-confirm');
                    } else {
                        let messageAlert = result.responseJSON.result.responseStatus.errorMessage;
                        this.showAlert(messageAlert, this.translate.instant('label.warning'));
                    }
                }, function (error) {

                });
            } else {
                this.billPaymentRequestToPayService.verifyEditBillerProfileDetail(this.selectedBillerProfile).then((result: any) => {
                    if (result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                        let billerProfile: BillerProfileBean = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(result.responseJSON.result.value);
                        this.billPaymentRequestToPayService.setConfirmBillerProfile(billerProfile);
                        this.permissionChangeRoute.changeRoute('MANAGE_BILLER.edit-confirm');
                    } else {
                        let messageAlert = result.responseJSON.result.responseStatus.errorMessage;
                        this.showAlert(messageAlert, this.translate.instant('label.warning'));
                    }
                }, function (error) {

                });
            }
        }
    }

    protected validateForm(form: any): any {
        let validationResult = { valid: true, displayName: '', message: '' };
        if (form.controls['aliasName'] != undefined && form.controls['aliasName'] != null && form.controls['aliasName'].errors != null) {
            for (let err in form.controls['aliasName'].errors) {
                if (['required'].indexOf(err) != -1) {
                    validationResult = { valid: false, displayName: "label.accountAliasName", message: 'valErr.required' };
                }
            }
        }
        if (validationResult.valid) {

            for (let billerRefNo of this.selectedBillerProfile.refNoList) {
                if (billerRefNo.value === '') {
                    validationResult = {
                        valid: false,
                        displayName: (this.translate.currentLang === 'en') ? billerRefNo.refTextEn : billerRefNo.refTextTh,
                        message: 'valErr.required'
                    };
                    break;
                }
            }
        }

        return validationResult;
    }

    protected showAlert(messageAlert, title) {
        let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    ionViewWillEnter() {
        this.navBar.backButtonClick = () => {

            this.selectedBillerProfile = new BillerProfileBean();
            this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
            this.nav.pop();
        };
    }
}



