import { NavController, Navbar } from 'ionic-angular';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { OtherAccountService } from '../../other-account.service';
import { UtilService } from '../../../../share/service/util.service';
import { OtherAccountListPage } from '../../other-account-list-page';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { PIBMobileOtherAccountAccordianComponent } from './other-account-accordian.component';

@Component({
    selector: 'other-account-list-page',
    templateUrl: './other-account-list-page.html'
})
export class PIBMobileOtherAccountListPageComponent extends OtherAccountListPage implements OnInit {
    permissionManage: any;
    @ViewChild('navbar') navBar: Navbar;
    @ViewChild('listItem') public listItem: PIBMobileOtherAccountAccordianComponent;
    constructor(public otherAccountService: OtherAccountService,
        public permissionChangeRoute: PermissionChangeRoute,
        public translateService: TranslateService,
        public constants: Constants,
        public permissionService: PermissionService,
        public nav: NavController,
        public utilService: UtilService) {
        super(otherAccountService, translateService, constants, permissionChangeRoute, utilService);
    }

    ngOnInit(): void {
        this.pageStyle = this.constants.STYLE_PIB_MOBILE;
        this.permissionService.getActionCode();
        this.permissionManage = {
            add: this.permissionService.getActionCode().MANAGE_OTHER_ACCOUNTS
        };
    }

    ngAfterViewInit(): void {
        if (typeof this.otherAccountService.alertConfig !== 'undefined') {
            this.alertConfig = this.otherAccountService.alertConfig;
        }
    }

    iReloadData: number = 0;
    ionViewWillEnter() {
        if (this.permissionChangeRoute.prevUrl === 'OTHER_ACCOUNT_DETAIL') {
            this.permissionChangeRoute.prevUrl = null;
            this.iReloadData++;
        }

        this.navBar.backButtonClick = () => {
            ///here you can do wathever you want to replace the backbutton event

            this.nav.popToRoot();
        };
    }

    gotoAddOtherAccountPage(): void {
        this.listItem.hideItemSliding();
        this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.add1');
    }
}


