import { TranslateService } from "ng2-translate/src/translate.service";
import { Constants } from '../../../share/service/constants';
import { AlertController, NavController } from 'ionic-angular';
import { PermissionChangeRoute } from '../../../share/service/permission.service';
import { BillPaymentRequestToPayService } from '../bill-payment-request-to-pay.service';
import { BillerRequestToPayAddEditConfirm } from '../biller-request-to-pay-add-edit-confirm';
import { StrFormatPipe } from '../../../share/pipe/string-format.pipe';
import { UtilService } from '../../../share/service/util.service';

export class BillerRequestToPayMobileAddEditConfirm extends BillerRequestToPayAddEditConfirm {
    isFromAddnewAfterPayBill: boolean;
    constructor(
        public constants: Constants,
        public permissionChangeRoute: PermissionChangeRoute,
        public billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected translate: TranslateService,
        protected alertCtrl: AlertController,
        protected strFormat: StrFormatPipe,
        protected navCtrl: NavController,
        public utilService: UtilService) {
            super(constants, permissionChangeRoute, billPaymentRequestToPayService, translate, utilService);
    }

    public onClickSubmit() : void {
        this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
        if (this.isAdd) {    
            if(this.isFromAddnewAfterPayBill !== true){
                this.confirmAddBillerProfileDetail();
            }else{
                this.confirmAddBillerProfileDetailWithOutOTP();
            }
            
        } else {
            this.billPaymentRequestToPayService.confirmEditBillerProfileDetail(this.confirmBillerProfile).then((result: any) => {
                if(result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS){
                    let alert = this.alertCtrl.create({
                        title: this.translate.instant('label.Success'),
                        subTitle: this.translate.instant('label.editBillerSuccess'),
                        buttons: [
                            {
                                text: this.translate.instant('button.backToHome'),
                                role: 'cancel',
                                handler: () => {
                                    this.onModalHidden();
                                }
                            }
                        ]
                    });
                    alert.present();
                } else {
                    let messageAlert = this.strFormat.transform('', result.responseJSON.result.responseStatus.errorMessage);
                    this.showAlert(messageAlert, this.translate.instant('label.warning'));
                }
                this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
            }, function(error) {
                
            });
        }
    }

    requestOTPChanged($event){
        this.isRequestOTP = true;
        
        if (typeof $event.responseCode === "undefined") {
            this.OTPData.otpRefcode = $event.otpRefcode;
            this.OTPData.tokenOtp = $event.tokenOtp;
            this.OTPData.pin = '';
        } else {
            let messageAlert = this.strFormat.transform('', $event.errorMessage);
            this.showAlert(messageAlert, this.translate.instant('label.warning'));
        }
    }

    protected showAlert(messageAlert, title) {
        let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    onModalHidden() {
        this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
        this.navCtrl.popToRoot();
    }

    confirmAddBillerProfileDetail(){
        
        if (this.OTPData.pin == '') {
            let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: this.translate.instant('error.label.please.input.otp'),
                buttons: [this.translate.instant('button.ok')]
            });
            alert.present();
            return;
        }
        this.billPaymentRequestToPayService.confirmAddBillerProfileDetail(this.confirmBillerProfile, this.OTPData).then((result: any) => {
            if(result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS){
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.Success'),
                    subTitle: this.translate.instant('label.addBillerSuccess'),
                    buttons: [
                        {
                            text: this.translate.instant('button.backToHome'),
                            role: 'cancel',
                            handler: () => {
                                this.onModalHidden();
                            }
                        }
                    ]
                });
                alert.present();
            } else {
                let messageAlert = result.responseJSON.result.responseStatus.errorMessage;
                this.showAlert(messageAlert, this.translate.instant('label.warning'));
            }
            this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
        }, function(error) {
            
        });
    }

    confirmAddBillerProfileDetailWithOutOTP(){
        this.billPaymentRequestToPayService.confirmAddBillerProfileDetailWithOutOTP(this.confirmBillerProfile).then((result: any) => {
            if(result.responseJSON.result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS){
                let alert = this.alertCtrl.create({
                    title: this.translate.instant('label.Success'),
                    subTitle: this.translate.instant('label.addBillerSuccess'),
                    buttons: [
                        {
                            text: this.translate.instant('button.backToHome'),
                            role: 'cancel',
                            handler: () => {
                                this.onModalHidden();
                            }
                        }
                    ]
                });
                alert.present();
            } else {
                let messageAlert = result.responseJSON.result.responseStatus.errorMessage;
                this.showAlert(messageAlert, this.translate.instant('label.warning'));
            }
            this.billPaymentRequestToPayService.setConfirmBillerProfile(null);
        }, function(error) {
            
        });
    }
}


