import { Component, OnInit, OnDestroy } from '@angular/core';
import { ContactUsService } from '../../contact-us.service';
import { LanguageSettingService } from '../../../../pages/main-layout/web/language-setting.service';
import { Subscription } from "rxjs";

@Component({
    selector: 'page-contact-us-mobile',
    templateUrl: 'contact-us.html',
    providers: [ContactUsService]
})
export class ContactUs implements OnInit, OnDestroy{
    private language;
    public actionCode;
    public result: any = '';
    public langObserver: any;
    private langObserverSubscription: Subscription;

    constructor(
        private contactUsService: ContactUsService, 
        public languageSettingService: LanguageSettingService) {
    }

    ngOnInit(): void {
        this.langObserver = this.languageSettingService.getLanguageObserver();
        this.langObserverSubscription = this.langObserver
        .subscribe(language => this.selectedLanguageEventHandler(language));

        this.contactUsService.getcontactUs('en').then((objResponse: any) => {
            this.result = objResponse.responseJSON.result.value.data;
        }, function (error) {

        });
    }
    
    ngOnDestroy(): void {
        this.langObserverSubscription.unsubscribe()
    }
  

    selectedLanguageEventHandler(language: any) {
        this.language = language;
    }
}



