import { Component, OnInit } from '@angular/core';
import { ModalController, AlertController } from 'ionic-angular';
import { Constants } from '../../../../../share/service/constants';
import { OtherAccountService } from '../../../other-account.service';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { TranslateService } from "ng2-translate/src/translate.service";
import { UtilService } from '../../../../../share/service/util.service';
import { PermissionAction } from '../../../../../share/service/permission.service';
import { DropdownDataService } from '../../../../../share/service/dropdown.service';
import { ValidationService } from '../../../../../share/service/validation.service';
import { MasterDataService } from '../../../../../share/service/master-data.service';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { FundTransferService } from '../../../../../share/service/fund-transfer.service';
import { StepWizard } from '../../../../../share/component/step-wizard/step-wizard.component';
import { MobileOtherAccountAddEdit } from '../../../../../pages/other-account/mobile/other-account-add-edit';

@Component({
  selector: 'pib-mobile-add-other-account',
  templateUrl: '../../other-account-add-edit.html'
})
export class PIBMobileAddOtherAccountPageComponent extends MobileOtherAccountAddEdit implements OnInit {
  swiperOptions: any;
  inputMenuSwiper = [];
  public type: string;
  public catName: any;
  public bankName: any;
  public message: string;
  public swiperIndex: any;
  // disabledAccountNo: boolean;
  public messageAlert: string;
  public stepWizard: StepWizard;
  public show: boolean = false;
  configAnyIDType: Object = { valueLength: Number, ValueType: Boolean };

  constructor(public constants: Constants,
    public otherAccountService: OtherAccountService,
    public permissionChangeRoute: PermissionChangeRoute,
    public alertCtrl: AlertController,
    public translateService: TranslateService,
    public fb: FormBuilder,
    public permissionAction: PermissionAction,
    public modalCtrl: ModalController,
    public fundTransferService: FundTransferService,
    public dropdownDataService: DropdownDataService,
    public masterDataService: MasterDataService,
    public utilService: UtilService) {
    super(constants, otherAccountService, permissionChangeRoute, alertCtrl, translateService, fb, permissionAction, modalCtrl, dropdownDataService, masterDataService, utilService);
  }

  ngOnInit(): void {
    this.stepWizard = {
      input: {
        data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }],
        step: 0,
        style: this.constants.STYLE_PIB_MOBILE
      }
    };

    this.titlePage = 'label.addAcc';
    this.getAnyIDTypeList();
    this.bankName = this.translateService.instant('lbl.drpSelect');
    this.catName = this.translateService.instant('lbl.drpSelect');
    this.editOtherAcc = false;

    if (this.fundTransferService.newAccountAfterFund) {
      let formValue = this.fundTransferService.newAccountAfterFund;
      
      this.otherAccountService.isAccountAfterTransfer = true;
      this.otherAccountFormGroup = new FormGroup({
        accountAliasname: new FormControl({ value: formValue.aliasName == null ? '' : formValue.aliasName, disabled: false }),
        email: new FormControl({ value: formValue.email == null ? '' : formValue.email, disabled: false }, [ValidationService.emailValidator]),
        clientUUID: new FormControl({ value: '', disabled: false }),
        anyIDType: new FormControl({ value: formValue.anyIDType.anyIDType, disabled: false }),
        accountNo: new FormControl({ value: formValue.accNo, disabled: false }),
        mobile: new FormControl({ value: formValue.mobileNo == null ? '' : formValue.mobileNo, disabled: false }, [ValidationService.mobileNoValidator]),
        msgLang: new FormControl({ value: '', disabled: false }),
        bankCode: new FormControl({ value: formValue.bank.bankCode, disabled: false }),
        accountName: new FormControl({ value: formValue.accName == null ? '' : formValue.accName, disabled: false }),
        category: new FormControl({ value: '', disabled: false }),
        categoryName: new FormControl({ value: '', disabled: false }),
        txnId: new FormControl({ value: formValue.txnId == null ? '' : formValue.txnId, disabled: false }),
        refTxnId: new FormControl({ value: formValue.refTxnId == null ? '' : formValue.refTxnId, disabled: false })
      });
      
      for (let i = 0; i < this.inputMenuSwiper.length; i++) {
        if (this.inputMenuSwiper[i].type === formValue.anyIDType.anyIDType) {
          this.swiperIndex = i;
        }
      }

      this.disabledAccountNo = true;
    } else {
      this.otherAccountFormGroup = this.fb.group({
        'clientUUID': '',
        'anyIDType': this.constants.ANYID_TYPE_BANK_ACCOUNT,
        'accountNo': ['', [
        ]
        ],
        'accountAliasname': ['', [
        ]
        ],
        'category': '',
        'email': ['', [
          ValidationService.emailValidator
        ]
        ],
        'mobile': ['', [
          ValidationService.mobileNoValidator
        ]
        ],
        msgLang: new FormControl(),
        'bankCode': '',
        'accountName': ''
      });

      this.swiperIndex = 0;
      this.otherAccountService.isAccountAfterTransfer = false;
      this.disabledAccountNo = false;

      this.getAnyIDTypes().forEach(type => {
        let anyIDType = this.lstOfAnyIDType[type];
        if (this.otherAccountFormGroup.value.anyIDType === anyIDType.anyIDType) {
          this.configAnyIDType = {
            valueLength: anyIDType.valueLength == 0 ? 100 : anyIDType.valueLength,
            valueType: anyIDType.valueType == 'S' ? false : true
          };
          this.otherAccountFormGroup.controls['accountNo'].setValidators(null);
          if (anyIDType.valueType == 'N') {
            this.otherAccountFormGroup.controls['accountNo'].setValidators([
              ValidationService.requiredValidator,
              ValidationService.nonStringValidator
            ]);
          } else {
            this.otherAccountFormGroup.controls['accountNo'].setValidators([
              ValidationService.requiredValidator,
            ]);
          }
          this.otherAccountFormGroup.controls['accountNo'].updateValueAndValidity();
        }
      });
    }

    this.swiperOptions = { lock: this.fundTransferService.newAccountAfterFund == null ? false : true, index: this.swiperIndex };
    this.init();
  }

  swiperChange(event) {
    this.Onchange('anyIDType', event);
    this.descriptionLabel = event.name;
  }

  public Onchange(param, event): void {
    if (event.type != this.otherAccountFormGroup.value.anyIDType) {
      this.otherAccountFormGroup.patchValue({ anyIDType: event.type });

      if (!this.fundTransferService.newAccountAfterFund) {
        this.otherAccountFormGroup.patchValue({ accountName: '' });

        this.otherAccountFormGroup.patchValue({ bankCode: '' });
        this.bankName = this.translateService.instant('lbl.drpSelect');

        this.otherAccountFormGroup.patchValue({ accountNo: '' });
      }

      this.getAnyIDTypes().forEach(type => {
        let anyIDType = this.lstOfAnyIDType[type];

        if (event.type === anyIDType.anyIDType) {
          this.configAnyIDType = {
            valueLength: anyIDType.valueLength == 0 ? 100 : anyIDType.valueLength,
            valueType: anyIDType.valueType == 'S' ? false : true
          };
          this.otherAccountFormGroup.controls['accountNo'].setValidators(null);
          if (anyIDType.valueType == 'N') {
            this.otherAccountFormGroup.controls['accountNo'].setValidators([
              ValidationService.requiredValidator,
              ValidationService.nonStringValidator
            ]);
          } else {
            this.otherAccountFormGroup.controls['accountNo'].setValidators([
              ValidationService.requiredValidator,
            ]);
          }
          this.otherAccountFormGroup.controls['accountNo'].updateValueAndValidity();
        }
      });
    }
  }

  isOpenModal(param, event) {
    if (!this.fundTransferService.newAccountAfterFund) {
      this.openModal(param, event);
    }
  }

  public getBankList(): void {
    this.masterDataService.getAllBanks().then((result) => {
      this.bankLists = result;
      this.masterDataService.setBankList(this.bankLists);

      if (this.otherAccountFormGroup.value.anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT && this.fundTransferService.newAccountAfterFund) {
        let bankObj = this.bankLists.find(x => x.bankCode == this.fundTransferService.newAccountAfterFund.bank.bankCode);
        if (bankObj) {
          this.bankName = bankObj.bankName;
          this.checkAccountInfo(bankObj.bankCode);
        }
      }
    });
  }

  ngOnDestroy(): void {
    this.fundTransferService.newAccountAfterFund = null;
  }
}



