import { CurrencyFormatterPipe } from '../../../../share/pipe/currency-formatter.pipe';
import * as moment from 'moment';
import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { Dateservice } from '../../../../share/service/date.service';
import { UtilService } from '../../../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionService } from '../../../../share/service/permission.service';
import { MasterDataService } from '../../../../share/service/master-data.service';
import { MyAccountStatementComponent } from '../../my-account-statement.component';
import { MyAccountService } from '../../../../pages/my-account/my-account.service';
import { AlertController } from 'ionic-angular';
import { GetinformationService } from "../../../../share/service/getInformation.service";
@Component({
    selector: 'my-account-statement-mobile',
    templateUrl: './my-account-statement.html'
})
// Component class
export class PIBMobileMyAccountStatementComponent extends MyAccountStatementComponent implements OnInit {
    constructor(public constants: Constants,
        public myAccountService: MyAccountService,
        public dateservice: Dateservice,
        public translate: TranslateService,
        public utilService: UtilService,
        public alertCtrl: AlertController,
        public masterDataService: MasterDataService,
        public permissionService: PermissionService,
        public getinformationService: GetinformationService,
        public currencyFormatter: CurrencyFormatterPipe
    ) {
        super(constants, myAccountService, dateservice, translate, utilService, masterDataService, permissionService,getinformationService,currencyFormatter);
    }
   
    selectedDate: string;
    arrStatementDate: Array<any>;
    selectOptions: { title: string; };
    maxPage: number;
    infiniteScroll: any;

    ngOnInit(): void {
        this.selectOptions = {
            title: this.translate.instant('label.selectPeriod')
        };

        this.pagination = {
            totalItem: 0,
            itemPerPage: 0,
            currentPage: 1,
            pageSize: [5, 10, 15, 20],
            pageSizeLabel: '10'
        };

        this.initialStatementDateTime().then((result: any) => {
            // initial dropdown date
            this.arrStatementDate = this.getDates(this.datepicker.fromMaxDate, this.datepicker.fromMinDate, 'MMMM YYYY');
            this.selectedDate = this.arrStatementDate[0].lastDay;
            
            // set date range
            this.datepicker.txtDateFrom = '01/' + this.arrStatementDate[0].lastDay.split('/')[1] + '/' + this.arrStatementDate[0].lastDay.split('/')[2];
            this.datepicker.txtDateTo = this.arrStatementDate[0].lastDay;

            this.initialStatementRequest();
        });
    }

    onChangeDate(date: string) {
        this.datepicker.txtDateFrom = '01/' + date.split('/')[1] + '/' + date.split('/')[2];
        this.datepicker.txtDateTo = date;
        this.pagination.currentPage = 1;
        this.statements = undefined;
        if(this.infiniteScroll) {
            this.infiniteScroll.enable(true);
        }
        this.initialStatementRequest();
    }

    private getDates(startDate, stopDate, formatString) {
        let dtLabel;
        let dtLastDay;
        let dateArray = new Array();
        let currentDate = startDate;

        while (currentDate >= stopDate) {
            let dt = new Date(currentDate);
            dtLastDay = this.findLastDayOfMonth((dt.getMonth() + 1), dt.getFullYear());
            dtLabel = this.dateservice.formatDate(dt, formatString);
            dateArray.push({ date: dt, label: dtLabel, lastDay: (dtLastDay + '/' + (dt.getMonth() + 1) + '/' + dt.getFullYear()) });
            currentDate = moment(dt).subtract(1, 'months').toDate();//new Date(dt.setMonth(dt.getMonth() - 1));
        }

        return dateArray;
    }

    private findLastDayOfMonth(selectedMonth, selectedYear) {
        let day;
        selectedMonth = parseInt(selectedMonth);
        if (selectedMonth === 2) {
            if (selectedYear % 4 === 0) {
                day = 29;
            } else {
                day = 28;
            }
        } else if (selectedMonth === 4 || selectedMonth === 6 || selectedMonth === 9 || selectedMonth === 11) {
            day = 30;
        } else {
            day = 31;
        }

        return day;
    }

    notZeroValue(value) {
        return value && value !== 0 && value !== 0.00 && value !== '0' && value !== '0.00';
    }

    loadData(infiniteScroll) {
        this.infiniteScroll = infiniteScroll;
        setTimeout(() => {
            this.pagination.currentPage = this.pagination.currentPage + 1;
            if (this.pagination.currentPage > this.maxPage) {
                this.infiniteScroll.enable(false);
            }else {
                this.initialStatementRequest();
            }
            this.infiniteScroll.complete();
        }, 500);
    }

    public initialStatementRequest() {
        this.myAccountStatement = {
            tokenID: '',
            paging: {
                page: this.pagination.currentPage,
                pageSize: '' + this.pagination.itemPerPage
            },
            inquiryAccountStatement: {
                myAcctId: this.myAcctId,
                statementDateFrom: this.datepicker.txtDateFrom,
                statementDateTo: this.datepicker.txtDateTo
                // statementDateFrom: "01/07/2019",
                // statementDateTo: "31/07/2019"
            }
        };

        if (this.myAcctType == this.constants.ACCOUNT_TYPE_TD ) {
                this.getTDStatement(this.myAccountStatement, this.statements)
                this.isNoData.subscribe(status => this.getNoDataTD(status));
            } else {
                this.getCASAStatement(this.myAccountStatement, this.statements);
                
            }
        }


     getNoDataTD(status){
            if(status === true){
                let alert = this.alertCtrl.create({
                title: this.translate.instant('label.warning'),
                subTitle: this.translate.instant('label.noDetailData'),
                buttons: [this.translate.instant('button.ok')]
            });
                alert.present();
            }    
    }

    public getCASAStatement(data: any, currentStatement) {
        let p = new Promise((resolve, reject) => {
            this.myAccountService.requestInquiryCASAStatement(data).then((objResponse: any) => {
                let result = objResponse.responseJSON.result;

                if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                    this.maxPage = result.value.statements.maxPage;
                    let list = result.value.statements.items;
                    this.statements = this.formatCASAStatement(list, currentStatement);
                }
            }, function (error) {
                    
            });
        });

        return p;
    }

    
}



