import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { UtilService } from '../../../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../../share/service/master-data.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { OtherAccountDetailPageComponent } from '../../other-account-detail-page-component';
import { OtherAccountService } from '../../../../pages/other-account/other-account.service';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';

@Component({
  selector: 'other-account-detail-page',
  templateUrl: './other-account-detail-page.html'
})
export class PIBMobileOtherAccountDetailPageComponent extends OtherAccountDetailPageComponent {
  anyIDTypeLists: any;
  permissionManage: any;

  constructor(public navCtrl: NavController,
    public router: Router,
    public constants: Constants,
    public otherAccountService: OtherAccountService,
    public bankCodeDataService: BankCodeDataService,
    public translateService: TranslateService,
    public permissionChangeRoute: PermissionChangeRoute,
    private masterDataService: MasterDataService,
    public permissionService: PermissionService,
    public utilService: UtilService) {
    super(router, constants, otherAccountService, bankCodeDataService, translateService, permissionChangeRoute, utilService);

    this.permissionChangeRoute.prevUrl = 'OTHER_ACCOUNT_DETAIL';
    if (this.otherAccountService.selectAccountDetailData !== null) {
      this.accountDetail = this.otherAccountService.selectAccountDetailData;
    }
  }

  ngOnInit(): void {
    let productID = this.accountDetail.productId;

    this.alertConfig = {
      type: 'danger',
      message: '',
      show: false
    };

    this.messageModalData = {
      title: 'label.msgDeleteExAccHead',
      accountDetail: this.accountDetail,
      size: 'md',
      config: { isShowCloseBtn: true, isShowDeleteBtn: true },
    };

    this.anyIDTypeLists = this.masterDataService.getAnyIDTypeList();
    this.permissionManage = {
      transfer: this.permissionService.getShortcutActionCodePermission('FUND_TRANSFER','FUND_TRANSFER') && this.permissionService.isProductAllow(productID, "allowTransferTo"),
      edit: this.permissionService.getActionCode().MANAGE_OTHER_ACCOUNTS,
      create_rtp: this.permissionService.getShortcutActionCodePermission('MY_RTP','CREATE_RTP')
    };
  }

  onTabsSelect(selected): void {
    
    if (selected === 'edit') {
      // this.navCtrl.push(PIBMobileEditOtherAccountComponent);
      this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.edit1');
    }
    if (selected === 'transfer') {
      this.permissionChangeRoute.targetAction = 'OTHER_ACCOUNT';
      this.permissionChangeRoute.changeRoute('FUND_TRANSFER');
    }
     if (selected === 'create_rtp') {
      this.permissionChangeRoute.targetAction = 'OTHER_ACCOUNT';
      this.permissionChangeRoute.changeRoute('MY_RTP.ADD');
    }
  }
}



