import { Constants } from '../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { Component, OnInit, NgZone, OnDestroy } from '@angular/core';
import { PermissionChangeRoute } from '../../share/service/permission.service';
import { AlertController} from 'ionic-angular';
import { GetinformationService } from '../../share/service/getInformation.service';
import { UtilService } from '../../share/service/util.service';
import { QrScannerService } from './qr-scanner.service';
import { FundTransferService } from '../../share/service/fund-transfer.service';
import { TransferBean } from '../../share/bean/transfer-bean';
import { CategoryBean } from '../../share/bean/category-bean';
import { AnyIDTypeBean } from '../../share/bean/anyid-type-bean';
import { AccountBean } from '../../share/bean/account-bean';
import { MasterDataService } from '../../share/service/master-data.service';
import { BillPaymentService } from '../../pages/bill-payment/bill-payment.service';
import { BillPaymentRequestToPayService } from '../../pages/bill-payment-request-to-pay/bill-payment-request-to-pay.service';
import { BillerBean } from '../../share/bean/biller-bean';
import {QRVerifyDetail} from "../../share/bean/qr-verify-detail";
@Component({
    selector: 'qr-scanner',
    templateUrl: './qr-scanner.html'
})

export class QRScannerComponent implements OnDestroy {
    private stepWizard: any;
    private anyIDTypeList: any;

    constructor(
    private translate: TranslateService,
    private constants: Constants,
    private permissionChangeRoute: PermissionChangeRoute,
    private alertCtrl: AlertController,
    private ngZone: NgZone,
    private getinformationService: GetinformationService,
    private utilService: UtilService,
    private qrScannerService: QrScannerService,
    private fundTransferService: FundTransferService,
    private masterDataService: MasterDataService,
    private billPaymentService: BillPaymentService,
    private billPaymentRequestToPayService: BillPaymentRequestToPayService,
    ){
        
    }

    ngOnInit() {
    }

    ionViewWillEnter() {
        window['angularComponentRef'] = {
            publicFunc: this.publicFunc.bind(this)
        };
    }

    publicFunc(event) {
        this.ngZone.run(() => this.verifyQRCode(event));
    }

    readBarcode(){
        let userAgentInfo = this.getinformationService.userAgentInfo || {};
        this.utilService.invokeNativeApi(userAgentInfo.mobile.name, 'readBarcode');
    }

    verifyQRCode(result){
        switch(result.responseCode) { 
          case 0: {
              //force scan only QR CODE like rib-mobile (can't scan barcode)
              result.actionCode = '';
              this.inquiryQRCodeInfo(result);
              // if(result.format === 'QR_CODE'){
              //     this.inquiryQRCodeInfo(result);
              // }else if (result.format === 'CODE_128'){
              //     this.inquiryBarCodeInfo(result);
              // }
            break; 
          } 
          case 1: { 
                
            break; 
          } 
          case -1: { 
                this.showAlert(this.translate.instant("lbl.scanPluginError"));
            break; 
          } 
      } 

    }

     private inquiryQRCodeInfo(Data){
        this.qrScannerService.inquiryDataByQRCodeAndBarCodeProcedure(Data).then((objResponse: any) => {
            if (objResponse.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                this.anyIDTypeList = this.masterDataService.getAllAnyIDTypes().then((result: any) => {
                this.anyIDTypeList = result;
                if(objResponse.value.actionCode == 'FUND_TRANSFER'){
                    this.permissionChangeRoute.targetAction = 'QR_READ';
                    this.fundTransferService.setTransferObj(this.populateTranferObj(objResponse.value));
                    this.permissionChangeRoute.changeRoute('FUND_TRANSFER');
                }else if(objResponse.value.actionCode == 'BILL_PAYMENT'){
                    this.permissionChangeRoute.prevUrl = 'QR_READ';
                    let populateBillPaymentObj =  this.qrScannerService.populateBillPaymentObj(objResponse.value)
                    this.billPaymentService.selectBillerDetailData = populateBillPaymentObj;

                    this.qrScannerService.QRScanFormat = Data.dataFormatType;
                    this.qrScannerService.dataFormatType = objResponse.value.dataFormatType;
                    this.billPaymentRequestToPayService.setConfirmBillerProfileForAddNew((populateBillPaymentObj).toBiller);
                    this.permissionChangeRoute.changeRoute('PAY_BILL');
                } else if (objResponse.value.actionCode == 'VER_SLIP') {
                    this.masterDataService.getAllBanks().then((bankList: any) => {
                        this.masterDataService.getAllAnyIDTypes().then((anyIdList: any) => {
                            this.masterDataService.setAnyIDTypeList(anyIdList);
                            this.masterDataService.setBankList(bankList);
                            this.permissionChangeRoute.prevUrl = 'QR_READ';
                            this.qrScannerService.inquiryQRVerifyTransaction(objResponse.value).then((inquiryResponse: any) => {
                                if (inquiryResponse.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                                    let refInfos = objResponse.value.refInfos || [];
                                    let categoryId = objResponse.value.categoryId || '';
                                    let populateQRVerifyDetailObj = this.populateQRVerifyDetailObj(inquiryResponse.value, bankList, anyIdList, refInfos, categoryId);
                                    this.qrScannerService.selectVerifySlipDetailData = populateQRVerifyDetailObj;
                                    this.permissionChangeRoute.changeRoute('VERIFY_SLIP');
                                } else {
                                    this.showAlert(inquiryResponse.responseStatus.errorMessage);
                                }
                            });
                        });

                    });

                }

                }, function (error) {
                    
                });
            } else {
                this.showAlert(objResponse.responseStatus.errorMessage);
            }
        }, function (error) {
            
        });
    }

    private inquiryBarCodeInfo(Data){
        this.qrScannerService.inquiryBarCodeInfo(Data).then((objResponse: any) => {
            if (objResponse.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                this.billPaymentService.selectBillerDetailData = this.qrScannerService.populateBillPaymentObj(objResponse.value);
                this.permissionChangeRoute.prevUrl = 'QR_READ';

                this.qrScannerService.QRScanFormat = Data.format;
                this.billPaymentRequestToPayService.setConfirmBillerProfileForAddNew(this.billPaymentRequestToPayService.parseObjToBillerProfileBean(objResponse.value));
                this.permissionChangeRoute.changeRoute('PAY_BILL');
            } else {
                this.showAlert(objResponse.responseStatus.errorMessage);
            }
        }, function (error) {
            
        });
    }

    protected populateTranferObj(data: any): TransferBean {
        
        let category = new CategoryBean();
        category.catId = -1;
        let anyIDType = this.anyIDTypeList[data.toAnyIdType] || new AnyIDTypeBean();

        let destAccount = new AccountBean();
        destAccount.accId = -1;
        destAccount.accNo = data.toAnyIdValue;
        destAccount.accName = data.toAccountAcctName || '';
        destAccount.aliasName = data.toAccountAliasName || '';
        destAccount.category = category;
        destAccount.anyIDType = anyIDType;

        let transferObj = new TransferBean();
        transferObj.srcAccount = new AccountBean();
        transferObj.destAccount = destAccount;
        transferObj.rtpReferenceNo = data.rtpreference;
        transferObj.transferAmount = data.amount;
        transferObj.recurringType = this.constants.RECURRING_TYPE_NO;
        transferObj.editType = "";
        transferObj.note = data.additionalNote;
        transferObj.scanFlag = 'Y';
        return transferObj;
    }

    
    private showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });
        alert.present();
    }

    ngOnDestroy() {
        window['angularComponent'] = null;
    }

    private populateQRVerifyDetailObj(data: any, bankList: any, anyIdList: any,refInfos: any, categoryId: any) {
        let qrVerifyDetailObject = new QRVerifyDetail();
        qrVerifyDetailObject = data;
        qrVerifyDetailObject.bankList = bankList;
        qrVerifyDetailObject.anyIdList = anyIdList;
        qrVerifyDetailObject.categoryId = categoryId;
       if(refInfos != undefined && refInfos.length > 0){
           refInfos.forEach((element) => {
            if (element.no == '1') {
                qrVerifyDetailObject.ref1Value = element.value;
                qrVerifyDetailObject.ref1Label = element.textEn || this.translate.instant('lbl.ref1');
            }
            if (element.no == '2') {
                qrVerifyDetailObject.ref2Value = element.value;
                qrVerifyDetailObject.ref2Label = element.textEn || this.translate.instant('lbl.ref2');
                if (qrVerifyDetailObject.categoryId == this.constants.E_DONATION_CATEGORY_ID) {
                    qrVerifyDetailObject.ref2Label = this.translate.instant('lbl.eDonation.card');
                }
            }
            if (element.no == '3') {
                qrVerifyDetailObject.ref3Value = element.value;
                qrVerifyDetailObject.ref3Label = element.textEn || this.translate.instant('lbl.ref3');
            }
        });
       }else if(qrVerifyDetailObject.ref1) {
           qrVerifyDetailObject.ref1Value = qrVerifyDetailObject.ref1;
           qrVerifyDetailObject.ref1Label = this.translate.instant('lbl.ref1');

           qrVerifyDetailObject.ref2Value = qrVerifyDetailObject.ref2;
           qrVerifyDetailObject.ref2Label = this.translate.instant('lbl.ref2');

           qrVerifyDetailObject.ref3Value = qrVerifyDetailObject.ref3;
           qrVerifyDetailObject.ref3Label = this.translate.instant('lbl.ref3');
       }
        

        return qrVerifyDetailObject;
    }

}



