import { Injectable } from '@angular/core';
import { MfpApi } from '../../share/mfp/mfp-api.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { Constants } from '../../share/service/constants';
import { BillPaymentBean } from '../../share/bean/bill-payment-bean';
import { BillerBean } from '../../share/bean/biller-bean';
import {QRVerifyDetail} from "../../share/bean/qr-verify-detail";

@Injectable()
export class QrScannerService {
    private actioncode: string;
    private procedure: string;
    private _verifySlipDetail: QRVerifyDetail;
    
    public QRScanFormat: string;
    private _dataFormatType: string;
    constructor(private _MfpApi: MfpApi,
    private _TranslateService: TranslateService,
    private _Constants: Constants) {

    };

    public inquiryQRCodeInfo(Data: any) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    qrCode: Data,
                    language: this._TranslateService.currentLang,
                },
                actionCode: this._Constants.REQ_ACTION_CODE.QR_INQUIRY_DATA,
                procedure: this._Constants.REQ_PROCEDURE_NAME.QR_INQUIRY_DATA
            };
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);  
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }

    public inquiryBarCodeInfo(Data: any) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    language: this._TranslateService.currentLang,
                    barcodeType: Data.format,
                    barcodeInfo: Data.text
                },
                actionCode: this._Constants.REQ_ACTION_CODE.BPS_INQUIRY_BILL_PAYMENT_BARCODE_QRCODE_INFO,
                procedure: this._Constants.REQ_PROCEDURE_NAME.BPS_INQUIRY_BILL_PAYMENT_BARCODE_QRCODE_INFO
            };
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }

    public inquiryDataByQRCodeAndBarCodeProcedure(data: any) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    actionCode: data.actionCode,
                    barcodeType: data.format,
                    barcodeInfo: data.text
                },
                actionCode: this._Constants.REQ_ACTION_CODE.INQUIRY_DATA_QR_CODE_BAR_CODE,
                procedure: this._Constants.REQ_PROCEDURE_NAME.INQUIRY_DATA_QR_CODE_BAR_CODE
            };
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }


    public populateBillPaymentObj(data: any){
        let billPaymentObj = new BillPaymentBean();
        let ToBiller = new BillerBean();

        // ToBiller.billerID =  null;
        ToBiller.billerName = data.billerName || ((this._TranslateService.currentLang === 'en')? data.billerNameEn : data.billerNameTh);
        ToBiller.profileCode= data.profileCode;
        // ToBiller.promptPayBillerId = data.billerId;
        ToBiller.billerProfileId= data.billerProfileId;
        ToBiller.serviceCode = data.serviceCode;
        ToBiller.companyCode = data.companyCode;
        ToBiller.categoryId = data.categoryId;

        if (data.refInfos != null && data.refInfos != undefined) {
            ToBiller.billerID =  null;
            ToBiller.promptPayBillerId = data.promptPayBillerId;
            data.refInfos.forEach(element => {
                if (element.no == '1') {
                    ToBiller.ref1 = element.value;
                    ToBiller.ref1TextEn = element.textEn;
                    ToBiller.ref1TextTh = element.textTh;
                    ToBiller.ref1ShowRef = element.showRef;
                }
                if (element.no == '2') {
                    ToBiller.ref2 = element.value;
                    ToBiller.ref2TextEn = element.textEn;
                    ToBiller.ref2TextTh = element.textTh;
                    ToBiller.ref2ShowRef = element.showRef;
                }
                if (element.no == '3') {
                    ToBiller.ref3 = element.value;
                    ToBiller.ref3TextEn = element.textEn;
                    ToBiller.ref3TextTh = element.textTh;
                    ToBiller.ref3ShowRef = element.showRef;
                }
            });
        }else{
            ToBiller.billerID = null;
            ToBiller.promptPayBillerId = data.billerId;
            
            ToBiller.ref1 = data.ref1 || "";
            ToBiller.ref2 = data.ref2 || "";
            ToBiller.ref3 = data.ref3 || "";

            ToBiller.ref1TextEn = ToBiller.ref1TextTh = this._TranslateService.instant('label.billRefNo1');
            ToBiller.ref2TextEn = ToBiller.ref2TextTh = this._TranslateService.instant('label.billRefNo2');
            ToBiller.ref3TextEn = ToBiller.ref3TextTh = this._TranslateService.instant('label.billRefNo3');

            if (data.ref3) {
                ToBiller.ref3ShowRef = true;
            }else {
                ToBiller.ref3ShowRef = false;
            }

        }

        billPaymentObj.toBiller = ToBiller;
        billPaymentObj.payAmount= data.amount;
        billPaymentObj.rtpReferenceNo = '';
        billPaymentObj.editType = '';
        billPaymentObj.flagAmountFix = data.flagAmountFix;

        return billPaymentObj;
    }

    public inquiryQrcodeBarCodeInfoForAdd(data) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    barcodeType: data.format,
                    barcodeInfo: data.text
                },
                actionCode: this._Constants.REQ_ACTION_CODE.BPS_INQUIRY_BILL_PAYMENT_BARCODE_QRCODE_INFO_FOR_ADD,
                procedure: this._Constants.REQ_PROCEDURE_NAME.BPS_INQUIRY_BILL_PAYMENT_BARCODE_QRCODE_INFO_FOR_ADD
            }
            
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);
            });
        });
        return promise;
    }

    public getQRScanFormat(QRScanFormat): string {
        return this.QRScanFormat;
    }
    public setQRScanFormat(QRScanFormat: string) {
        this.QRScanFormat = QRScanFormat;
    }

    public generateTransferQRVerifySlipProcedure(data: any) {
        let _invoke = {adapter: 'FundTransferAdapter'};
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    transactionRef: data.transactionRef
                },
                actionCode: this._Constants.REQ_ACTION_CODE.GENERATE_TRANSFER_QR_VERIFY_SLIP,
                procedure: this._Constants.REQ_PROCEDURE_NAME.GENERATE_TRANSFER_QR_VERIFY_SLIP
            };
            this._MfpApi.invokeProcedure(obj, _invoke).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }

    public generatePaymentQRVerifySlipProcedure(data: any) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    transactionRef: data.transactionRef
                },
                actionCode: this._Constants.REQ_ACTION_CODE.GENERATE_PAYMENT_QR_VERIFY_SLIP,
                procedure: this._Constants.REQ_PROCEDURE_NAME.GENERATE_PAYMENT_QR_VERIFY_SLIP
            };
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }

    public inquiryQRVerifyTransaction(data: any) {
        let promise = new Promise((resolve, reject) => {
            let obj = {
                params: {
                    transRef: data.transRef,
                    sendingBank: data.sendingBank,
                    actionCode: data.actionCode,
                },
                actionCode: this._Constants.REQ_ACTION_CODE.INQUIRY_QR_VERIFY_TRANSACTION,
                procedure: this._Constants.REQ_PROCEDURE_NAME.INQUIRY_QR_VERIFY_TRANSACTION
            };
            this._MfpApi.invokeProcedure(obj).then((result) => {
                let response = result.responseJSON.result;
                resolve(response);
            }, function (error) {
                reject(error);

            });
        });
        return promise;
    }

    set selectVerifySlipDetailData(selectVerifySlipDetailData: any) {
        this._verifySlipDetail = selectVerifySlipDetailData;
    }

    get selectVerifySlipDetailData() {
        return this._verifySlipDetail;
    }

    get dataFormatType(): string {
        return this._dataFormatType;
    }

    set dataFormatType(value: string) {
        this._dataFormatType = value;
    }

}


