import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { OtpService } from '../../../../share/component/verify-otp/otp.service';
import { PermissionAction } from '../../../../share/service/permission.service';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { StepWizard } from '../../../../share/component/step-wizard/step-wizard.component';
import { PromptPayRegisterConfirmComponent } from '../../prompt-pay-register-confirm.component';
import { PromptPayRegisterServiceMain } from '../../../../pages/prompt-pay/prompt-pay-register.service';

@Component({
  selector: 'pib-mobile-prompt-pay-register-confirm',
  templateUrl: './pib-mobile-prompt-pay-register-confirm.html'
})
export class PIBMobilePromptPayRegisterConfirmComponent extends PromptPayRegisterConfirmComponent {
  swiperOptions: any;
  inputMenuSwiper = [];
  selectedAcc: String;
  messageAlert: String;
  anyIDTypeValSelected: string;
  public stepWizard: StepWizard;

  constructor(public otpService: OtpService,
    public constants: Constants,
    public promptPayRegisterServiceMain: PromptPayRegisterServiceMain,
    public permissionAction: PermissionAction,
    public permissionChangeRoute: PermissionChangeRoute,
    public alertCtrl: AlertController,
    public translate: TranslateService) {
    super(otpService, constants, promptPayRegisterServiceMain, permissionAction, permissionChangeRoute, translate);

    this.stepwizardStyle = this.constants.STYLE_PIB_MOBILE;
    this.resultAnyIDConfirm = this.promptPayRegisterServiceMain.tempRegisterAnyIDData.resultAnyIDConfirm;
    let resultAnyIDInfo = this.promptPayRegisterServiceMain.registerAnyIDInfo.resultAnyIDInfo;
    
    for (let i = 0; i < resultAnyIDInfo.length; i++) {
      this.inputMenuSwiper.push({
        srcImg: resultAnyIDInfo[i].icon,
        type: resultAnyIDInfo[i].anyIDType,
        name: resultAnyIDInfo[i].descriptionEN,
        color: resultAnyIDInfo[i].iconColor,
        index: i
      });
    }
    this.swiperOptions = { lock: true, index: this.inputMenuSwiper.find(x => x.type == this.resultAnyIDConfirm.anyIDType).index };

    this.verifyOTPStyle = this.constants.STYLE_PIB_MOBILE;
    this.verifyOTPAction = this.constants.ACTION_CODE_REGISTER_ANYID;
    this.verifyTransactionId = this.promptPayRegisterServiceMain.verifyTransactionId;
    // this.selectedAcc = this.resultAnyIDConfirm.accountName + ' ' + this.resultAnyIDConfirm.accountNO;
    this.selectedAcc = this.promptPayRegisterServiceMain.tempRegisterAnyIDData.selectedAccount;
  }

  // Override a base class method
  requestOTPChanged(result) {
    this.isRequestOTP = true;

    if (typeof result.responseCode === "undefined") {
      this.OTPData.otpRefcode = result.otpRefcode;
      this.OTPData.tokenOtp = result.tokenOtp;
      this.OTPData.pin = '';
    } else {
      let messageAlert = result.errorMessage;
      this.showAlert(messageAlert);
    }
  }

  public verifyPromptPayRegisterStep() {
    this.messageAlert = this.translate.instant('label.warningRInputOTPPin');
    this.showAlert(this.messageAlert);
  }

  // Override method fo mobile
  gotoPromptPayRegisterSuccess() {
      this.responseCodeMainService = '';

      if (this.OTPData.pin != null && this.OTPData.pin != "") {
          let verifyTransactionID = this.verifyTransactionId;
          let verifyOTPRequest = {
              referenceNO: this.OTPData.otpRefcode,
              tokenOTPForCAA: this.OTPData.tokenOtp,
              otp: this.OTPData.pin
          };
          let param = { verifyTransactionID, verifyOTPRequest };

          this.promptPayRegisterServiceMain.registerAnyID(param).then((objResponse: any) => {
              let result = objResponse.responseJSON.result;

              if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                  this.promptPayRegisterServiceMain.tempRegisterAnyIDData = null;
                  this.promptPayRegisterServiceMain.confirmRegisterAnyIDData = objResponse;
                  this.permissionChangeRoute.changeRoute('KK_PRODUCT_SERVICE.REGISTER_KK_PROMPT_PAY_STEP3');
              } else {
                  if (result.responseStatus.responseCode === this.constants.RESP_CODE_REQ_OTP_AGAIN) {
                      this.isRequestOTP = false;
                  }
                  this.responseCodeMainService = result.responseStatus.responseCode;
                  this.OTPData.pin = '';
                  this.showAlert(result.responseStatus.errorMessage);
              }
          }, function (error) {
              
          });
      }
  }

  showAlert(messageAlert) {
    let alert = this.alertCtrl.create({
      title: this.translate.instant('label.warning'),
      subTitle: messageAlert,
      buttons: [this.translate.instant('btn.close')]
    });

    alert.present();
  }
}



