import { Constants } from '../../../../../share/service/constants';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { UtilService } from '../../../../../share/service/util.service';
import { PermissionChangeRoute } from '../../../../../share/service/permission.service';
import { ModalController, AlertController} from 'ionic-angular';
import { FromAccountListModal } from './from-account-list-modal.component';
import { ToAccountListModal } from './to-account-list-modal.component';
import { MasterDataService } from '../../../../../share/service/master-data.service';
import { Dateservice } from '../../../../../share/service/date.service';
import { CurrencyFormatterPipe } from '../../../../../share/pipe/currency-formatter.pipe';
import { Dictionary } from '../../../../../share/bean/dictionary';
import { TranslateService } from "ng2-translate/src/translate.service";
import { RequestToPayService } from '../../../request-to-pay.service';
import { StrFormatPipe } from '../../../../../share/pipe/string-format.pipe';
import { AnyIDTypeBean } from '../../../../../share/bean/anyid-type-bean';
import { OtherAccountService } from '../../../../other-account/other-account.service';
import { DashBoardService } from '../../../../dashboard/dashboard.service';
@Component({
    selector: 'pib-mobile-add-request-to-pay-step1',
    templateUrl: 'add-request-to-pay-step1.html'
})

export class AddRequestToPayStep1 implements OnInit, OnDestroy {

     private model: any = {
        amount: "0.00",
        transferDate: "",
        note: "",
        srcAccount: {},
        destAccount: {}
    };
    private stepWizard: any;
    private fromAccount = {
        aliasName : '',
        accNo : '',
        anyIDValue : '',
        anyIDType : new AnyIDTypeBean
    };
    private toAccount = {
        accNo: '',
        aliasName: '',
        anyIDType: new AnyIDTypeBean,
        catId: ''
    };
    public dt: Date;
    private today: any;
    private minDate: Date;
    private maxDate: Date;
    private dateOptions: any;
    private submitted: boolean = false;
    private displayNames: any;
    private dict: Dictionary = {
        'fromAccount': 'lbl.RequestFrom',
        'toAccount': 'lbl.myPromptPay',
        'amount': 'lbl.amount',
        'transferDate': 'lbl.transferDate',
        'note': 'lbl.note'
    };
    nextStep: string = 'MY_RTP.ADD_CONFIRM';
    amountText:string = "0.00";
    hiddenInputText = false;

    constructor(private constants: Constants,
        private permissionChangeRoute: PermissionChangeRoute,
        private utilService: UtilService,
        private modalCtrl: ModalController,
        private masterDataService: MasterDataService,
        private dateservice: Dateservice,
        private currencyFormatter: CurrencyFormatterPipe,
        private alertCtrl: AlertController,
        private translate: TranslateService,
        private strFormat: StrFormatPipe,
        private requestToPayService: RequestToPayService,
        private otherAccountService: OtherAccountService,
        private dashBoardService: DashBoardService) {
            this.stepWizard = {
            input: {
                data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }, { name: '3', label: 'stepWizard.complete' }],
                step: 0,
                style: this.constants.STYLE_PIB_MOBILE
            }
        };
    }

    ngOnDestroy(): void {
        this.permissionChangeRoute.targetAction = null;
    }

    ngOnInit(): void {


        if(this.utilService.arrayObjectIndexOf(['OTHER_ACCOUNT'], this.permissionChangeRoute.targetAction) !== -1
                && this.otherAccountService.selectAccountDetailData != null){
            let selectAccountDetailData = this.otherAccountService.selectAccountDetailData.accountBean;
            this.toAccount.accNo = selectAccountDetailData.accNo;
            this.toAccount.anyIDType = selectAccountDetailData.anyIDType;

            this.otherAccountService.selectAccountDetailData = null;
        }else if(this.utilService.arrayObjectIndexOf(['MY_ACCOUNTS.DETAIL'], this.permissionChangeRoute.prevUrl) !== -1
                && this.dashBoardService.selectDashBoardDetails != null){

            let selectMyAccountDetailData = this.dashBoardService.selectDashBoardDetails;
            this.fromAccount = {
                aliasName : selectMyAccountDetailData.accountAliasName,
                accNo : selectMyAccountDetailData.accountNo,
                anyIDValue : selectMyAccountDetailData.anyIDValue,
                anyIDType : selectMyAccountDetailData.anyIDType
            };

            this.dashBoardService.selectDashBoardDetails = null;

        }

        this.dateOptions = this.dateservice.getMasterDates();
        this.displayNames = this.dict != null ? Object.keys(this.dict) : [];
        this.masterDataService.getCurrentDate().then((result: any) => {
            let dt = result;
            // dt.setMinutes(dt.getMinutes() + 480);
            this.today = this.dateservice.formatDate(dt, 'DD/MM/YYYY');
            let bkkISODate = this.dateservice.dtBKKISOSting(dt);
            
            this.minDate = new Date(bkkISODate);
            this.maxDate = new Date(bkkISODate);
            this.dt = dt;
            this.model.transferDate = bkkISODate;
        });
    }

    openModal(action: string, selected: any) {
        if (action == 'FROM_ACCOUNT_LIST') {
             let modal = this.modalCtrl.create(FromAccountListModal, { account: selected });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {

                    this.fromAccount = {
                    aliasName : data.accountAliasName,
                    accNo : data.accountNo,
                    anyIDValue : data.anyIDValue,
                    anyIDType : data.anyIDType
                    };
                }
            });

            modal.present();
        }
        if (action == 'TO_ACCOUNT_LIST') {
             let modal = this.modalCtrl.create(ToAccountListModal, { account: selected });

            modal.onDidDismiss(data => {
                if (data !== undefined && data !== null) {

                    this.toAccount = {
                    accNo: data.accNo,
                    aliasName: data.aliasName || data.anyIDType.label,
                    anyIDType: data.anyIDType,
                    catId: data.category.catId
                    }
                }
            });

            modal.present();
        }
    }

    goToNextStep(Form): void {
        let data = Form.value;
        let valid = Form.valid;
        let validationResult = this.validateTransferForm(Form);
        if (!validationResult.valid) {
            this.showAlert(this.strFormat.transform(validationResult.message, [this.dict[validationResult.displayName]]));
        }else if(Number(this.model.amount) <= 0.0 ){
            this.showAlert(this.strFormat.transform('valErr.required', [this.dict['amount']]));
        }else if(this.utilService.validSpecialChar(data.note) || this.utilService.validNewline(data.note)){
            this.showAlert(this.strFormat.transform('valErr.validSpecialChar', []));
        }else {
            this.submitted = true;


            if (this.fromAccount.accNo !== '' && this.toAccount.accNo !== '' && valid) {

                let promise = null;
    
                let createRTPObj = {
                    fromAnyIdType:  this.fromAccount.anyIDType,
                    fromAnyIdValue:  this.fromAccount.anyIDValue,
                    fromAccountNo:  this.fromAccount.accNo,
                    toAnyIdType:  this.toAccount.anyIDType,
                    toAnyIdValue:  this.toAccount.accNo,
                    amount: this.model.amount,
                    memo:  data.note,
                    isAddNew: (Number(this.toAccount.catId) === 999)
                };
               
                  promise = this.requestToPayService.prepareCreateRTP(createRTPObj);

                if (promise != null) {
                    promise.then((result: any) => {
                        if (result.verifyTransactionId !== undefined) {
                            this.requestToPayService.setcreateRTPObj(result);
                            this.permissionChangeRoute.changeRoute(this.nextStep);
                        } else {
                            let messageAlert = result.errorMessage;
                            this.showAlert(messageAlert);
                        }
                    });
                }
            }
        }
    }

    protected showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    protected validateTransferForm(form: any): any {
        let fromAccount = this.fromAccount;
        let toAccount = this.toAccount;
        let validationResult = { valid: true, displayName: '', message: '' };

        this.displayNames.forEach(function (name, index) {
            if (validationResult.valid) {
                if (form.controls[name] != undefined && form.controls[name] != null && form.controls[name].errors != null) {
                    for (let err in form.controls[name].errors) {
                        if (['pattern'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.invalid' };
                            break;
                        }
                    }
                }
                if (name == 'fromAccount' && fromAccount.accNo === '') {
                    validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                }
                if (name == 'toAccount' && fromAccount.accNo !== '' && toAccount.accNo === '') {
                    validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                }
            }
        });

        return validationResult;
    }

    onFocus(event) {
        this.hiddenInputText = true;
        const value = this.currencyFormatter.parse(event.value);
        this.model.amount = value;
        this.amountText = value;
    }

    onBlur(event) {
        this.hiddenInputText = false;
        this.model.amount = this.currencyFormatter.transform(event.value,false);
        this.amountText = this.currencyFormatter.transform(event.value,true);
    }
}



