import { Router } from '@angular/router';
import { NavController, AlertController, ItemSliding } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { UtilService } from '../../../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../../share/service/master-data.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { OtherAccountAccordianComponent } from '../../other-account-accordian.component';
import { OtherAccountService } from '../../../../pages/other-account/other-account.service';
import { Component, trigger, animate, style, state, transition, Input } from '@angular/core';
// import { PIBMobileOtherAccountDetailPageComponent } from './other-account-detail-page.component'
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';

@Component({
  selector: 'other-account-accordian-mobile',
  templateUrl: './other-account-accordian.html',
  animations: [
    trigger('accordingState', [
      state('collapsed', style({
        height: '0px'
      })),
      state('expanded', style({
        height: '*'
      })),
      transition('collapsed <=> expanded', [animate(200)])
    ])
  ]
})

// Component class
export class PIBMobileOtherAccountAccordianComponent extends OtherAccountAccordianComponent {

  @Input('reload') iReloadData: number;

  constructor(public constants: Constants,
    public otherAccountService: OtherAccountService,
    public bankCodeDataService: BankCodeDataService,
    public router: Router,
    public alertCtrl: AlertController,
    public navCtrl: NavController,
    public masterDataService: MasterDataService,
    public permissionChangeRoute: PermissionChangeRoute,
    public translate: TranslateService,
    public permissionService: PermissionService,
    public utilService: UtilService) {
    super(constants, otherAccountService, bankCodeDataService, router, masterDataService, permissionChangeRoute, translate, permissionService, utilService);
  }

  ngOnChanges(changed: any): void {
    //
    if (changed.iReloadData && changed.iReloadData.currentValue) {
      this.firstLoad = true;
      this.getExternalAccount();
    }
  }

  showDeleteExAccModal(selectedAccount, slidingItem: ItemSliding) {
    let confirm = this.alertCtrl.create({
      title: this.translate.instant('label.deleteOtherAccountHead'),
      message: this.translate.instant('label.deleteOtherAccountBody'),
      buttons: [{
        text: this.translate.instant('btn.close'),
        handler: () => {
          
        }
      },
      {
        text: this.translate.instant('button.ok'),
        handler: () => {
          slidingItem.close();
          this.deleteOtherAccount(selectedAccount);
        }
      }]
    });

    confirm.present();
  }

  public deleteOtherAccount(account: any) {
    this.otherAccountService.removeExternalAccount(account.exAcctId).then((objResponse: any) => {
      let result = objResponse.responseJSON.result;

      if (result.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
        let alertMsg = this.alertCtrl.create({
          title: this.translate.instant('label.Success'),
          message: this.translate.instant('label.deleteOtherAccountSuccess'),
          buttons: [{
            // text: this.translate.instant('button.ok'),
            text: this.translate.instant('button.backToHome'),
            role: 'cancel',
            handler: () => {
              this.onModalHidden();
            }
          }]
        });

        alertMsg.present();
      }
    }, function (error) {
      
    });
  }

  onModalHidden() {
    this.otherAccountService.selectAccountDetailData = undefined;
    this.navCtrl.popToRoot();
  }

  navigateToExAccDetailPage(data: any, slidingItem: ItemSliding): void {
    slidingItem.close();
    this.otherAccountService.selectAccountDetailData = data;
    //this.navCtrl.push(PIBMobileOtherAccountDetailPageComponent);
    this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.DETAIL');
  }

  toggleState(selectState) {
      // all collapsed
      let state = '';
      for (var key in this.accordian.state){
          if (this.accordian.state.hasOwnProperty(key)){
            if(key === selectState){
              state = 'expanded';
            } else {
              state = 'collapsed';
            }
            this.accordian.state[key] = state;
          }
      }
      //this.accordian.state[selectState] = (this.accordian.state[selectState] === 'expanded') ? 'collapsed' : 'expanded';
  }

  private _itemSliding: any = null;
  itemSliding(slidingItem: ItemSliding){
    
    this._itemSliding = slidingItem;
  }

  hideItemSliding(){
    
    if(this._itemSliding !== null){
      this._itemSliding.close();
    }
  }
}



