import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { Dateservice } from '../../../../share/service/date.service';
import { TranslateService } from 'ng2-translate/src/translate.service';
import { MasterDataService } from '../../../../share/service/master-data.service';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { FundTransferService } from '../../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { RequestToPayService } from '../../request-to-pay.service'
@Component({
    selector: 'history-request-to-pay-timeline-detail',
    templateUrl: './history-request-to-pay-timeline-detail.html'
})
export class HistoryRequestToPayTimelineDetailPageComponent implements OnInit {
    detailData: any;
    anyIDTypeList: any;
    toBankCode: string;
    fromBankCode: string;
    anyIDFromDesc: string;

    constructor(
        public constants: Constants,
        public fundTransferService: FundTransferService,
        public translateService: TranslateService,
        private permissionChangeRoute: PermissionChangeRoute,
        public bankCodeDataService: BankCodeDataService,
        public dateservice: Dateservice,
        public masterDataService: MasterDataService,
        public requestToPayService:RequestToPayService) {
    }

    ngOnInit(): void {
        this.detailData = this.requestToPayService.SelectedRTPIncomingData;
        this.anyIDTypeList = this.masterDataService.getAnyIDTypeList();
    }
}


