import { Observable } from 'rxjs/Observable';
import { animate, Component, OnInit, state, style, transition, trigger } from '@angular/core';
import { Constants } from '../../../share/service/constants';
import { BillPaymentRequestToPayService } from '../bill-payment-request-to-pay.service';
import { ViewController } from 'ionic-angular';
import { Subject } from 'rxjs/Rx';

import { TranslateService } from "ng2-translate/src/translate.service";
import { OnDestroy } from '@angular/core/src/metadata/lifecycle_hooks';
import { UtilService } from '../../../share/service/util.service';

@Component({
    selector: 'manage-biller-add-search-modal',
    templateUrl: 'manage-biller-add-search-modal.html',
    animations: [
        trigger('accordingState', [
            state('collapsed', style({
                height: '0px'
            })),
            state('expanded', style({
                height: '*'
            })),
            transition('collapsed <=> expanded', [animate(200)])
        ])
    ]
})
export class ManageBillerAddSearchModal implements OnInit, OnDestroy {
    
    // biller list format
    // [
    //     {
    //         category: {categoryId:'xx',categoryName:'xx'},
    //         expanded: true,
    //         items:[{...},{...}]
    //     },
    //     ...
    // ]
    public billerList = [];
    private search$: Subject<string> = new Subject();
    public msgDataNotFound = '';
    public loading: boolean = false;
    public allCategories = [];
    public searchInput = '';

    constructor(
        private constants: Constants,
        private billPaymentRequestToPayService: BillPaymentRequestToPayService,
        public viewCtrl: ViewController,
        public translate: TranslateService,
        public utilService: UtilService
    ) {

    }

    ngOnInit() {
        this.searchInit();
        this.categoriesInit();
    }

    ngOnDestroy() {
        this.search$.unsubscribe();
    }

    private categoriesInit() {
        this.billPaymentRequestToPayService
            .getBillerCategories()
            .switchMap((categories) => {
                this.allCategories = categories;
                return Observable.of(this.createEmptyBillerListByCategory(this.allCategories));
            })
            .subscribe((resp) => {
                this.billerList = resp;
            })
    }
    private searchInit() {
        this.search$.asObservable()
            .switchMap((token: string) => {
                // this.loading = true;
                this.msgDataNotFound = '';
                if (token && token.trim() != '') {
                    return this.billPaymentRequestToPayService
                        .getBillerByToken(token)
                        .do((resp) => {
                            if (resp.length === 0) {
                                this.msgDataNotFound = this.translate.instant('label.billerNotFoundSearchAgain');
                                this.billerList = [];
                            } else {
                                this.billerList = this.mapBillerList(resp);
                            }
                        })
                        .catch((err) => {
                            this.msgDataNotFound = this.translate.instant('label.billerNotFoundSearchAgain');
                            this.billerList = [];
                            return Observable.of([]);
                        })
                } else {
                    return Observable
                        .of(this.createEmptyBillerListByCategory(this.allCategories))
                        .do((resp) => {
                            this.billerList = resp;
                        })
                }
            })
            .do((resp) => {
                this.loading = false;
            })
            .finally(() => {
                this.loading = false;
            })
            .subscribe(
                (resp) => {
                },
                (err) => {
                },
                () => {
                }
            );
    }

    public createEmptyBillerListByCategory(categories) {
        const emptyBiller = categories.map((value) => {
            const { categoryTh, categoryEn } = value;
            const categoryName = this.translate.currentLang === 'en' ? categoryEn : categoryTh;
            return {
                category: Object.assign({}, value, { categoryName, expanded: false }),
                items: []
            }
        })

        return emptyBiller;
    }
    public mapBillerList(billerList) {
        if (!billerList) {
            billerList = [];
        }
        const result = billerList
            .reduce((prev, item) => {
                const find = prev.filter((value) => value.categoryId === item.categoryId)
                if (find.length === 0) {
                    const { categoryId, categoryTh, categoryEn } = item;
                    const categoryName = this.translate.currentLang === 'en' ? categoryEn : categoryTh;
                    prev.push({ categoryId, categoryName, expanded: true })
                }
                return prev;
            }, [])
            .map((category) => {
                return {
                    category,
                    items: billerList.filter((value) => {
                        return value.categoryId === category.categoryId;
                    })
                }
            });
        return result;
    }

    public onSearchInput(ev: any) {
        this.search$.next(ev.target.value);
    }

    public onSelectBillerProfile(selectBillerProfile) {
        this.viewCtrl.dismiss(selectBillerProfile)
    }

    public toggleCategory(categoryId) {
        if (this.searchInput === '') {
            const find = this.billerList.find((group) => group.category.categoryId === categoryId)
            if(find && find.category.expanded){
                find.category.expanded = !find.category.expanded;
                return;
            }

            this.loading = true;
            const category = [];
            category.push(categoryId);
            this.billPaymentRequestToPayService
                .getBillerByTokenAndCategories('',category)
                .catch((err) => {
                    this.billerList = [];
                    return Observable.of([]);
                })
                .do((resp)=>{
                    this.loading = false;
                })
                .finally(() => {
                    this.loading = false;
                })
                .subscribe((resp) => {
                    this.billerList = this.createEmptyBillerListByCategory(this.allCategories)
                        .map((item) => {
                            if (item.category.categoryId === categoryId) {
                                item.items = resp;
                                item.category.expanded = true;
                            }
                            return item;
                        })
                })
        } else {
            const find = this.billerList.find((group) => group.category.categoryId === categoryId)
            if (find) {
                find.category.expanded = !find.category.expanded;
            }
        }

    }
    public dismiss() {
        this.viewCtrl.dismiss();
    }
}


