import { FormBuilder } from '@angular/forms';
import { AlertController } from 'ionic-angular';
import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../share/service/constants';
import { OtherAccountService } from '../other-account.service';
import { OtherAccountAddEdit } from '../other-account-add-edit';
import { UtilService } from '../../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionAction } from '../../../share/service/permission.service';
import { DropdownDataService } from '../../../share/service/dropdown.service';
import { MasterDataService } from '../../../share/service/master-data.service';
import { PermissionChangeRoute } from '../../../share/service/permission.service';
import { ModalController, Platform, NavParams, ViewController } from 'ionic-angular';
import { StepWizard } from '../../../share/component/step-wizard/step-wizard.component';

@Component({
  selector: 'add-edit-other-account',
  templateUrl: './other-account-add-edit.html'
})
// Component class
export class MobileOtherAccountAddEdit extends OtherAccountAddEdit implements OnInit {

  swiperOptions: any;
  inputMenuSwiper = [];
  public type: string;
  public catName: any;
  public bankName: any;
  public message: string;
  public selectItemAcc: any;
  public messageAlert: string;
  public show: boolean = false;
  public stepWizard: StepWizard;
  public descriptionLabel: String;
  disabledAccountNo: boolean;

  constructor(public constants: Constants,
    public otherAccountService: OtherAccountService,
    public permissionChangeRoute: PermissionChangeRoute,
    public alertCtrl: AlertController,
    public translateService: TranslateService,
    public fb: FormBuilder,
    public permissionAction: PermissionAction,
    public modalCtrl: ModalController,
    public dropdownDataService: DropdownDataService,
    public masterDataService: MasterDataService,
    public utilService: UtilService
  ) {
    super(constants, otherAccountService, translateService, fb, permissionAction, permissionChangeRoute, dropdownDataService, masterDataService, utilService);

    this.stepWizard = {
      input: {
        data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }],
        step: 0,
        style: this.constants.STYLE_PIB_MOBILE
      }
    }

    this.bankName = this.translateService.instant('lbl.drpSelect');
    this.catName = this.translateService.instant('lbl.drpSelect');
  }

  ngOnInit(): void {
  }

  public getAnyIDTypeList(): void {
    this.lstOfAnyIDType = this.masterDataService.getAnyIDTypeList();

    this.getAnyIDTypes().forEach(type => {
      let anyIDType = this.lstOfAnyIDType[type];
      this.inputMenuSwiper.push({
        srcImg: anyIDType.iconObj.icon,
        type: anyIDType.anyIDType,
        name: anyIDType.label,
        color: anyIDType.iconObj.iconColor
      });
    });


    if (this.editOtherAcc == true) {
      this.descriptionLabel = this.lstOfAnyIDType[this.otherAccountFormGroup.value.anyIDType].label;
    }
  }

  getAnyIDTypes(): any {
    return this.lstOfAnyIDType != null ? Object.keys(this.lstOfAnyIDType) : [];
  }

  public showAlert(messageAlert) {
    let alert = this.alertCtrl.create({
      title: this.translateService.instant('label.warning'),
      subTitle: messageAlert,
      buttons: [this.translateService.instant('btn.close')]
    });

    alert.present();
  }

  public onSubmit(): void {
    this.validateSubmitForm();
  }

  private validateSubmitForm() {
    this.messageAlert = '';
    let anyIDType = this.lstOfAnyIDType[this.otherAccountFormGroup.value.anyIDType].label;

    if (this.otherAccountFormGroup.value.accountAliasname == '') {
      this.messageAlert = this.translateService.instant('label.missingaccAliasNameMsg');
    } else if (this.otherAccountFormGroup.value.anyIDType == this.constants.ANYID_TYPE_BANK_ACCOUNT) {
      if (this.otherAccountFormGroup.value.bankCode == '') {
        this.messageAlert = this.translateService.instant('label.missingaccBankMsg');
      } else if (this.otherAccountFormGroup.value.accountNo == '') {
        this.messageAlert = this.translateService.instant('label.missingaccNoMsg');
      } else if (this.otherAccountFormGroup.value.accountNo != '' && this.otherAccountFormGroup.controls.accountNo.status != 'VALID') {
        this.messageAlert = this.translateService.instant('valErr.invalidAccNo', { anyIDType: anyIDType });
      } else if (this.showAcctName == true && this.otherAccountFormGroup.value.accountName == '') {
        this.messageAlert = this.translateService.instant('label.missingaccNameMsg');
      }
    } else if (this.otherAccountFormGroup.value.accountNo == '') {
      if (anyIDType) {
        this.messageAlert = this.translateService.instant('lbl.missingAlrt', { anyIDType: anyIDType });
      }
      
    } else if (this.otherAccountFormGroup.controls.accountNo.status != 'VALID') {
      
      this.messageAlert = this.translateService.instant('valErr.invalidAlert', { anyIDType: anyIDType });
    }
    if (this.messageAlert != '') {
      this.showAlert(this.messageAlert);
    } else {
      if (this.otherAccountFormGroup.value.category == '') {
        this.messageAlert = this.translateService.instant('label.missingaccCategoryMsg');
        this.showAlert(this.messageAlert);
      } else if (this.otherAccountFormGroup.controls.mobile.status != 'VALID') {
        this.messageAlert = this.translateService.instant('label.invalidMobileNoMsg');
        this.showAlert(this.messageAlert);
      } else if (this.otherAccountFormGroup.controls.email.status != 'VALID') {
        this.messageAlert = this.translateService.instant('label.invalidEmailMsg');
        this.showAlert(this.messageAlert);
      } else if (this.messageAlert == '') {
        this.prepareAddEditExtConfirmScreen();
      }
    }
  }

  public prepareAddEditExtConfirmScreen(): void {
    let confirmAddEditExtAccountData;
    let extAccData = this.otherAccountFormGroup.value;
    
    if (this.editOtherAcc == false) {
      this.otherAccountService.setParam(extAccData);
      this.otherAccountService.setActionCode('ACT_EXTERNAL_ACCOUNT_ADD');
      this.otherAccountService.setProcedure('addExternalAccountProcedure');
      this.otherAccountService.masterData().then((objResponse: any) => {
        let result = objResponse.responseJSON.result;

        if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
          let data = result.value;

          confirmAddEditExtAccountData = {
            bankCode: data.bankCode,
            bankName: data.bankName,
            acctNo: data.acctNo,
            acctName: data.acctName,
            acctAliasName: data.acctAliasName,
            catId: data.catId,
            categoryName: data.categoryName,
            mobile: data.mobile,
            email: data.email,
            anyIDType: data.anyIDType,
            msgLang: this.otherAccountFormGroup.value.mobile === '' ? '' : this.constants.CULTURE_SHORTNAME_ENGLISH,
            anyIDTypeLabel: this.currentLang == this.constants.CULTURE_SHORTNAME_ENGLISH ? data.labelEn : data.labelTh,
            anyIDTypeDesc: this.currentLang == this.constants.CULTURE_SHORTNAME_ENGLISH ? data.descriptionEN : data.descriptionTH
          };
          
          this.otherAccountService.confirmAddExtAccountData = confirmAddEditExtAccountData;
          this.otherAccountService.isRequireOtp = data.requireOtp;
          this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.add2');
        } else {
          let messageAlert = result.responseStatus.errorMessage;
          this.showAlert(messageAlert);
        }
      }, function (error) {
        
      });
    } else {
      confirmAddEditExtAccountData = Object.assign({}, extAccData);

      let anyIDTypeObj = this.lstOfAnyIDType[extAccData.anyIDType];
      confirmAddEditExtAccountData.anyIDTypeLabel = anyIDTypeObj.label;
      confirmAddEditExtAccountData.anyIDTypeDesc = anyIDTypeObj.desc;

      if (typeof extAccData.bankCode != 'undefined' && extAccData.bankCode != '') {
        let bankObj = this.bankLists.find(x => x.bankCode == extAccData.bankCode);
        confirmAddEditExtAccountData.bankName = bankObj.bankName;
      }

      this.otherAccountService.tempEditExtAccountData = extAccData;
      this.otherAccountService.confirmEditExtAccountData = confirmAddEditExtAccountData;
      
      this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS.edit2');
    }
  }

  public openModal(param, event) {
    if (param === 'bankLists' && this.editOtherAcc == false) {
      let modal = this.modalCtrl.create(ModalbankListPage, { bankLists: event });
      modal.onDidDismiss(data => {
        if (data !== undefined && data !== null) {
          this.bankName = data.bankName;
          this.checkAccountInfo(data.bankCode);
        }
      });
      modal.present();
    }

    if (param === 'accCategoryList') {
      let modal = this.modalCtrl.create(ModalCategoryPage, { accCategoryList: event });
      modal.onDidDismiss(data => {
        if (data !== undefined && data !== null) {
          this.catName = data.catName;
          this.otherAccountFormGroup.patchValue({ category: data.catId });
          if (this.editOtherAcc == true) {
            this.otherAccountFormGroup.patchValue({ categoryName: this.catName });
          }
        }
      });
      modal.present();
    }
  }

  swiperChange(event) {
      this.Onchange('anyIDType', event);
      this.descriptionLabel = event.name;
  }

}

@Component({
  template: `
              <ion-header>
                <ion-navbar>
                  <ion-title>
                    Select Bank List
                  </ion-title>
                  <ion-buttons end>
                    <button ion-button icon-only (click)="dismiss()">
                      <ion-icon class="icon-large" name="close"></ion-icon>
                    </button>
                  </ion-buttons>
                </ion-navbar>
              </ion-header>
              <ion-content>
                <ion-list>
                    <ion-item *ngFor="let bankList of bankLists; let i = index" (click)="emitSelectedBank(bankList)">
                      {{ bankList.bankName }}
                    </ion-item>
                </ion-list>
              </ion-content>
            `
})
export class ModalbankListPage {
  name: string;
  bankLists: any;
  accCategoryList: any;

  constructor(
    public platform: Platform,
    public params: NavParams,
    public viewCtrl: ViewController
  ) {
    this.bankLists = this.params.get('bankLists');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  emitSelectedBank(value) {
    this.viewCtrl.dismiss(value);
  }

  emitSelectedCategory(value) {
  }
}

@Component({
  template: `
              <ion-header>
                <ion-navbar>
                  <ion-title>
                    Select Category List
                  </ion-title>
                  <ion-buttons end>
                    <button ion-button icon-only (click)="dismiss()">
                      <ion-icon class="icon-large" name="close"></ion-icon>
                    </button>
                  </ion-buttons>
                </ion-navbar>
              </ion-header>
              <ion-content>
                <ion-list>
                    <ion-item *ngFor="let accCategory of accCategoryList; let i = index" (click)=" emitSelectedCategory(accCategory)">
                      {{ accCategory.catName }}
                    </ion-item>
                </ion-list>
              </ion-content>
            `
})

export class ModalCategoryPage {
  name: string;
  bankLists: any;
  accCategoryList: any;

  constructor(
    public platform: Platform,
    public params: NavParams,
    public viewCtrl: ViewController
  ) {
    this.accCategoryList = this.params.get('accCategoryList');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

  emitSelectedCategory(value) {
    this.viewCtrl.dismiss(value);
  }
}


