import { Component, trigger, animate, style, state, transition, Input } from '@angular/core';
import { Router } from '@angular/router';

import { TranslateService } from "ng2-translate/src/translate.service";
import { AlertController, NavController } from 'ionic-angular';

import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { BillerAccordianRequestToPay } from '../../biller-accordian';
import { BillPaymentRequestToPayService } from '../../bill-payment-request-to-pay.service';
import { BillerProfileBean } from '../../../../share/bean/biller-profile-bean';
import { Constants } from '../../../../share/service/constants';
import { BillPaymentService } from '../../../../pages/bill-payment/bill-payment.service';
import { UtilService } from '../../../../share/service/util.service';

declare var BUILD_NUM;

@Component({
	selector: 'biller-request-to-pay-accordian-mobile',
	templateUrl: './biller-accordian.html',
	animations: [
		trigger('accordingState', [
			state('collapsed', style({
				height: '0px'
			})),
			state('expanded', style({
				height: '*'
			})),
			transition('collapsed <=> expanded', [animate(200)])
		])
	]
})
export class BillerAccordianRequestToPayMobile extends BillerAccordianRequestToPay {

    public BUILD_NUM = BUILD_NUM;
    @Input('reload') iReloadData: number;

    constructor(
        protected constants: Constants,
        protected permissionChangeRoute: PermissionChangeRoute,
        protected router: Router,
        protected billPaymentRequestToPayService: BillPaymentRequestToPayService,
        protected billPaymentService: BillPaymentService,
        protected translate: TranslateService,
        protected alertCtrl: AlertController,
        protected navCtrl: NavController,
        public utilService: UtilService) {
        super(constants, permissionChangeRoute, router, billPaymentRequestToPayService, translate, billPaymentService, utilService);
    }

    ngOnChanges(changed: any): void {
        //
        if (changed.iReloadData && changed.iReloadData.currentValue) {
            setTimeout(() => this.getUserBillerProfile(), 0);
        }
    }

    public navigateToBillerProfileDetail(data: BillerProfileBean) {

        this.billPaymentRequestToPayService.setSelectBillerProfileDetail(data);
        this.permissionChangeRoute.changeRoute('MANAGE_BILLER.detail');
    }

    public showDeleteBillerProfileModal(item) {
        let confirm = this.alertCtrl.create({
            title: this.translate.instant('label.delete.title.biller'),
            message: this.translate.instant('label.delete.message.biller'),
            buttons: [{
                text: this.translate.instant('button.cancel'),
                handler: () => {

                }
            },
            {
                text: this.translate.instant('button.ok'),
                handler: () => {
                this.deleteBiller(item);
                }
            }]
        });

        confirm.present();
    }

    public deleteBiller(biller: any) {
        let billerProfile : BillerProfileBean = this.billPaymentRequestToPayService.parseObjToBillerProfileBean(biller);
        this.billPaymentRequestToPayService.deleteBillerProfileDetail(billerProfile).then((result: any) => {
            let tmpresult = result.responseJSON.result;
            if(tmpresult.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS){
                let alert = this.alertCtrl.create({
                title: this.translate.instant('label.Success'),
                subTitle: this.translate.instant('label.deleteBillerSuccess'),
                buttons: [{
                    text: this.translate.instant('button.backToHome'),
                    role: 'cancel',
                    handler: () => {
                    this.onModalHidden();
                    }
                }]
                });
                
                alert.present();
            }else{
            
            }
        }, function(error) {

        });
    }

    onModalHidden() {
        this.billPaymentRequestToPayService.setSelectBillerProfileDetail(null);
        // this.permissionChangeRoute.changeRoute('MY_ACCOUNTS');
        this.navCtrl.popToRoot();
    }

    public isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }
}


