import { Component, OnInit } from '@angular/core';
import { BankBean } from '../../../bean/bank-bean';
import { Dictionary } from '../../../bean/dictionary';
import { Constants } from '../../../service/constants';
import { AccountBean } from '../../../bean/account-bean';
import { CategoryBean } from '../../../bean/category-bean';
import { AnyIDTypeBean } from '../../../bean/anyid-type-bean';
import { StrFormatPipe } from '../../../pipe/string-format.pipe';
import { AccountService } from '../../../service/account.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../service/master-data.service';
import { BankCodeDataService } from '../../../service/bankcode-data.service';
import { BankList } from '../../../component/bank-list/mobile/bank-list.component';
import { ModalController, NavParams, ViewController, AlertController, Platform } from 'ionic-angular';

@Component({
    selector: 'to-account-list',
    templateUrl: './to-account-list.html'
})

export class ToAccountList implements OnInit {

    private model: any = {
        anyIDType: this.constants.ANYID_TYPE_BANK_ACCOUNT,
        accountNo: '',
        accountName: ''
    };

    private dict: Dictionary = {
        'bankCode': 'lbl.bankName',
        'accountNo': '{0}',
        'accountName': 'lbl.accountName'
    };

    swiperOptions: any;
    isInitEdit: boolean;
    inputMenuSwiper: any;
    inputPattern: string;
    rangeLength: [number];
    action: string = "list";
    private displayNames: any;
    private accAnyIDType: any;
    private anyIDTypes: any = [];
    private otherAccounts: any = [];
    public firstLoad: boolean = true;
    public submitted: boolean = false;
    public isAndroid: boolean = false;
    private currentAccount: AccountBean;
    public isAccNameRequired: boolean = false;
    private bankAccount: BankBean = new BankBean();

    constructor(
        private platform: Platform,
        private params: NavParams,
        public constants: Constants,
        private viewCtrl: ViewController,
        private strFormat: StrFormatPipe,
        public alertCtrl: AlertController,
        private modalCtrl: ModalController,
        private translate: TranslateService,
        private accountService: AccountService,
        private masterDataService: MasterDataService,
        public bankCodeDataService: BankCodeDataService
    ) {
        this.isAndroid = this.platform.is("android");
    }

    ngOnInit(): void {
        this.inputMenuSwiper = [];
        this.isInitEdit = true;
        this.swiperOptions = { lock: false, index: 0 }
        this.currentAccount = this.params.get('account') || new AccountBean();
        this.model.valueType = true;
        this.displayNames = this.dict != null ? Object.keys(this.dict) : [];

        this.masterDataService.getAllAnyIDTypes().then((result: any) => {
            let index: number = 0
            this.anyIDTypes = result;
            this.accAnyIDType = this.anyIDTypes[this.constants.ANYID_TYPE_BANK_ACCOUNT];
            this.rangeLength = this.accAnyIDType.getRangeLength();
            this.inputPattern = this.accAnyIDType.getRegexPattern();

            this.getAnyIDTypes().forEach(type => {
                let anyIDType = this.anyIDTypes[type];

                this.inputMenuSwiper.push({
                    srcImg: anyIDType.iconObj.icon,
                    type: anyIDType.anyIDType,
                    name: anyIDType.desc,
                    color: anyIDType.iconObj.iconColor,
                    index: index
                });

                index++;
            });

            this.accountService.getOtherAccounts(this.anyIDTypes).then((result: any) => {
                result.splice(0, 1); // Removes the "New Account" category from otherAccounts.
                this.otherAccounts = result;
                if(this.otherAccounts.length > 0) { this.toggleGroup(this.otherAccounts[0]); }
                this.firstLoad = false;
             });

            if (typeof this.currentAccount.accNo !== "undefined" && this.currentAccount.category.catId === 999) {
                this.action = "add";
                this.bankAccount = this.currentAccount.bank;
                this.model.anyIDType = this.currentAccount.anyIDType.anyIDType;
                this.model.valueType = this.currentAccount.anyIDType.valueType === 'N'?true:false;
                this.model.accountNo = this.currentAccount.accNo;
                this.accAnyIDType = this.anyIDTypes[this.model.anyIDType];
                this.rangeLength = this.accAnyIDType.getRangeLength();
                this.inputPattern = this.accAnyIDType.getRegexPattern();
                this.swiperOptions = { lock: false, index: this.inputMenuSwiper.find(x => x.type == this.accAnyIDType.anyIDType).index }

                if (this.model.anyIDType == this.constants.ANYID_TYPE_BANK_ACCOUNT) {
                    this.isAccNameRequired = !this.bankAccount.isORFT;
                }
                if (this.isAccNameRequired) {
                    this.model.accountName = this.currentAccount.accName;
                }
            } else {
                this.currentAccount = new AccountBean();
            }
        });
    }

    openModal(action: string, selected: any) {
        let modal = this.modalCtrl.create(BankList, { bank: selected });

        modal.onDidDismiss(data => {
            if (data !== undefined && data !== null) {
                this.bankAccount = data;
                this.isAccNameRequired = !this.bankAccount.isORFT;
            }
        });

        modal.present();
    }

    onSelectChanged(newValue) {
        this.viewCtrl.dismiss(newValue);
    }

    dismiss() {
        this.viewCtrl.dismiss();
    }

    toggleGroup(group) {
        group.show = !group.show;
    }

    isGroupShown(group) {
        return group.show;
    }

    getAnyIDTypes(): any {
        return this.anyIDTypes != null ? Object.keys(this.anyIDTypes) : [];
    }

    swiperChange(newValue) {
        if (!this.isInitEdit) {
            
            this.model.anyIDType = newValue.type;
            this.model.valueType = this.anyIDTypes[newValue.type].valueType === 'N'? true:false;
            this.bankAccount = new BankBean();
            this.currentAccount = new AccountBean();
            this.isAccNameRequired = false;

            this.model.accountNo = "";
            this.model.accountName = "";
            this.submitted = false;
            this.accAnyIDType = this.anyIDTypes[newValue.type];
            this.rangeLength = this.accAnyIDType.getRangeLength();
            this.inputPattern = this.accAnyIDType.getRegexPattern();

            let txtAccountNo = document.getElementsByName("accountNo")[1];
            if (txtAccountNo !== undefined) {
                txtAccountNo.setAttribute('maxlength', this.rangeLength[1].toString());
                txtAccountNo.setAttribute('ng-reflect-range-length', this.rangeLength.toString());
            }
        }

        this.isInitEdit = false;
    }

    addAccount(accForm): void {
        let data = accForm.value;
        let anyIDType = this.anyIDTypes[data.anyIDType];
        let validationResult = this.validateAccountForm(accForm);

        if (!validationResult.valid) {
            this.showAlert(this.strFormat.transform(validationResult.message, [this.strFormat.transform(this.dict[validationResult.displayName], [anyIDType.label]), anyIDType.valueLength]));
        } else {
            this.submitted = true;

            let accountObj = new AccountBean();
            anyIDType = anyIDType || new AnyIDTypeBean();

            if (this.constants.ANYID_TYPE_BANK_ACCOUNT == data.anyIDType) {
                let bank = new BankBean();
                bank.bankCode = this.bankAccount.bankCode;
                bank.bankName = this.bankAccount.bankName;
                bank.shortName = this.bankAccount.shortName;
                bank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(bank.bankCode);
                bank.isORFT = this.bankAccount.isORFT;
                accountObj.bank = bank;
            } else {
                anyIDType.shortName = anyIDType.iconObj.shortName;
            }

            let category = new CategoryBean();
            category.catId = 999;
            category.catName = this.translate.instant('lbl.newAccount');

            accountObj.accId = -1;
            accountObj.accNo = data.accountNo;
            accountObj.accName = data.accountName ? data.accountName.toUpperCase() : '';
            accountObj.aliasName = "";
            accountObj.anyIDType = anyIDType;
            accountObj.category = category;

            this.viewCtrl.dismiss(accountObj);
        }
    }

    private showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
    }

    private validateAccountForm(form) {
        let data = form.value;
        let bankAccount = this.bankAccount;
        let bankAnyIDType = this.constants.ANYID_TYPE_BANK_ACCOUNT;
        let validationResult = { valid: true, displayName: '', message: '' };

        this.displayNames.forEach(function (name, index) {
            if (validationResult.valid) {
                if (form.controls[name] != undefined && form.controls[name] != null && form.controls[name].errors != null) {
                    for (let err in form.controls[name].errors) {
                        if (['min', 'required'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                            break;
                        }
                        if (['pattern', 'rangeLength'].indexOf(err) != -1) {
                            validationResult = { valid: false, displayName: name, message: 'valErr.invalid' };
                            break;
                        }
                    }
                }
                if (data.anyIDType == bankAnyIDType && name == 'bankCode' && bankAccount.bankCode === undefined) {
                    validationResult = { valid: false, displayName: name, message: 'valErr.required' };
                }
            }
        });

        return validationResult;
    }
}



