import { AlertController, ItemSliding } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { MasterDataService } from '../../../../share/service/master-data.service';
import { Component, OnInit } from '@angular/core';
import { RequestToPayService } from '../../../../pages/request-to-pay/request-to-pay.service';
import { UtilService } from '../../../../share/service/util.service';
@Component({
  selector: 'request-to-pay-block-list',
  templateUrl: './request-to-pay-block-list.html'
})
export class RequestToPayBlockListComponent implements OnInit{
 private _itemSliding: any = null;
 private accountList: any = [];
 
  constructor(
    private constants: Constants,
    private alertCtrl: AlertController,
    private masterDataService: MasterDataService,
    private requestToPayService: RequestToPayService,
    private translate: TranslateService,
    private utilService: UtilService
    ) {
    
  }

  ngOnInit(){
    this.inquiryRequestToPayBlockList();
  }

  private itemSliding(slidingItem: ItemSliding){
    this._itemSliding = slidingItem;
  }

  private inquiryRequestToPayBlockList(){
      this.masterDataService.getAllAnyIDTypes().then((result: any) => {
              let anyIDTypes = result;
              for(let i in anyIDTypes){
                  if (anyIDTypes[i].anyIDType === this.constants.ANYID_TYPE_BANK_ACCOUNT) {
                      delete anyIDTypes[i];
                  }
              }
              this.requestToPayService.inquiryRequestToPayAnyIdBlocklist(anyIDTypes).then((result: any) => {
              
              if(result.errorMessage === undefined){
                  this.accountList = result;
              }else {
                  this.showAlert(result.errorMessage, this.translate.instant('label.warning'));
              }
          });
      });
      
  }

  private showConfirmUnBlockAccount(account: any, slidingItem: ItemSliding) {
      let confirm = this.alertCtrl.create({
        title: this.translate.instant('title.confirmUnblockAccMsg'),
        message: this.translate.instant('body.confirmUnblockAccMsg'),
        buttons: [{
          text: this.translate.instant('button.cancel'),
          handler: () => {

          }
        },
        {
          text: this.translate.instant('button.ok'),
          handler: () => {
            slidingItem.close();
            this.unBlockAccount(account);
          }
        }]
      });
      confirm.present();
  }

  private unBlockAccount(account: any) {
      let deleteRequestToPayData = {
                type: account.anyIDType.anyIDType || account.anyIDType,
                name: account.accountAliasName,
                value: account.anyIDValue
      }
      this.requestToPayService.deleteRequestToPayAnyIdBlocklist(deleteRequestToPayData).then((result: any) => {
          if(result.errorMessage === undefined){
              this.inquiryRequestToPayBlockList();
              this.showAlert('Unblocked successfully.', this.translate.instant('label.Success'));
          } else {
              this.showAlert(result.errorMessage , this.translate.instant('label.warning'));
          }
      });
  }

  private showAlert(messageAlert, title) {
      let alert = this.alertCtrl.create({
            title: title,
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });

        alert.present();
  }
  
}



