import {Component, OnInit} from '@angular/core';
import {Constants} from '../../../../../share/service/constants';
import {PermissionChangeRoute, PermissionService} from '../../../../../share/service/permission.service';
import {BankCodeDataService} from '../../../../../share/service/bankcode-data.service';
import {BillPaymentService} from '../../../../bill-payment/bill-payment.service';
import {PIBMobileTransactionDetail} from '../../../../bill-payment/mobile/pib/pib-mobile-transaction-detail.component'
import {AlertController, ModalController} from 'ionic-angular';
import {UtilService} from '../../../../../share/service/util.service';
import {CurrencyFormatterPipe} from '../../../../../share/pipe/currency-formatter.pipe';
import {Dateservice} from '../../../../../share/service/date.service';
import {BillPaymentBean} from '../../../../../share/bean/bill-payment-bean';
import {MasterDataService} from '../../../../../share/service/master-data.service';
import {AccountBean} from '../../../../../share/bean/account-bean';
import {BankBean} from '../../../../../share/bean/bank-bean';
import {BillerBean} from '../../../../../share/bean/biller-bean';
import {QrScannerService} from "../../../../qr-scanner/qr-scanner.service";
import {TranslateService} from "ng2-translate";

@Component({
    selector: 'bill-pay-transaction-history-detail-page',
    templateUrl: './transaction-history-detail-page.html'
})
export class BillPayTransactionHistoryDetailPageComponent implements OnInit {
    detailData: any;
    fromBankCode: string;
    authorized: any;
    recurringTimes: any;
    scheduleTypes: any;
    today: any;
    qrData = null;

    constructor(
        public constants: Constants,
        private permissionChangeRoute: PermissionChangeRoute,
        public billPaymentService: BillPaymentService,
        public bankCodeDataService: BankCodeDataService,
        public permissionService: PermissionService,
        public modalCtrl: ModalController,
        public utilService: UtilService,
        public currencyFormatterPipe: CurrencyFormatterPipe,
        public dateservice: Dateservice,
        public masterDataService: MasterDataService,
        public qrScannerService: QrScannerService,
        public translateService: TranslateService) {
    }

    ngOnInit(): void {
        this.masterDataService.getRecurringTimeList().then((result: any) => {
            this.recurringTimes = result;
        });
        this.masterDataService.getAllScheduleList().then((result: any) => {
            this.scheduleTypes = result;
        });
        this.masterDataService.getCurrentDate().then((result: any) => {
            let dt = result;
            this.today = this.dateservice.formatDate(dt, 'DD/MM/YYYY');
        });

        this.authorized = {
            printSlip: this.permissionService.getShortcutActionCodePermission('PAY_BILL', 'PRINT_SLIP_PAY_BILL'),
        };
        this.detailData = this.billPaymentService.getBillPaymentObj();
        this.detailData.debitDate = this.detailData.debitDate.split(' ')[0];
        this.fromBankCode = (this.detailData.fromBankCode) ? this.detailData.fromBankCode : this.constants.KKP_BANK_CODE;

        //clear billpayment Obj
        this.billPaymentService.setBillPaymentObj(null);
        console.log(this.detailData)
        if (this.detailData.refInfos) {
            this.extractRefInfos(this.detailData.refInfos);
        }
    }

    beforeOpenModal(action: string, selected: any) {
        if (this.detailData.transactionRef && this.detailData.canGenQrSlip) {
            this.qrScannerService.generatePaymentQRVerifySlipProcedure(this.detailData).then((objResponse: any) => {
                if (objResponse.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                    this.qrData = objResponse.value;
                }
                this.openModal(action, selected);
            });
        } else {
            //not show qr Data
            this.openModal(action, selected);
        }
    }

    openModal(action: string, selected: any) {
        console.log('selected', selected)
        if (action == 'VIEW_TRANSACTION_DETAIL') {
            let scheduleType = selected.scheduleType || 0;
            let recurringTime = selected.recurringTimes || 0;

            let fromAccount = new AccountBean();
            let bank = new BankBean();
            bank.bankCode = selected.fromBankCode;
            bank.bankName = selected.fromBankName;
            bank.shortName = '';
            bank.imgObj = this.bankCodeDataService.getBankCodeImageProperty(bank.bankCode);

            fromAccount.accNo = selected.fromAccountNumber;
            fromAccount.aliasName = selected.fromAliasName;
            fromAccount.accName = selected.fromAccountName;
            fromAccount.bank = bank;

            let biller = new BillerBean();
            biller.billerID = selected.billerID;
            biller.categoryId = selected.categoryId;
            biller.billerAliasName = selected.billerAliasName;
            biller.billerName = selected.billerName;
            biller.promptPayBillerId = selected.promptpayBillerId;
            biller.profileCode = selected.profileCode;
            biller.ref1 = selected.reference1;
            biller.ref2 = selected.reference2;
            biller.ref3 = selected.reference3 || '';
            biller.ref1TextEn = selected.ref1Text;
            biller.ref2TextEn = selected.ref2Text;
            biller.ref3TextEn = selected.ref3Text;

            let billPaymentBean = new BillPaymentBean();
            billPaymentBean.fromAccount = fromAccount;
            billPaymentBean.toBiller = biller;
            billPaymentBean.payAmount = selected.payAmount;
            billPaymentBean.feeAmount = selected.feeAmount;
            billPaymentBean.paymentDate = selected.debitDate;
            billPaymentBean.msgLanguage = selected.msgLanguage;
            billPaymentBean.recurringType = selected.editType != '0' ? selected.recurringType : this.constants.RECURRING_TYPE_NO;
            billPaymentBean.recurringTime = this.recurringTimes.find(x => x.time == recurringTime) || {};
            billPaymentBean.scheduleType = this.scheduleTypes.find(x => x.schedTypeId == scheduleType) || {};
            billPaymentBean.memo = selected.memo || '';
            billPaymentBean.editType = selected.editType || "";
            billPaymentBean.transactionID = selected.transactionID || "";
            billPaymentBean.masterTransactionID = selected.masterTransactionID || "";
            billPaymentBean.referenceNO = selected.referenceNO;
            billPaymentBean.custName = this.permissionService.getUserData().fullNameEN + ' ' + this.permissionService.getUserData().fullSurNameEN;
            billPaymentBean.immediateType = this.today == selected.effectiveDate ? this.constants.IMMEDIATE_TYPE_TODAY : this.constants.IMMEDIATE_TYPE_LATER;
            billPaymentBean.rtpReferenceNo = selected.rtpRef || '';
            billPaymentBean.transactionDate = selected.transactionDate;
            billPaymentBean.accountFromNoMarking = selected.maskingFromAccount;
            billPaymentBean.submitStatusDesc = selected.paymentStatusDesc;
            billPaymentBean.categoryId = selected.categoryId;
            billPaymentBean.transactionRef = selected.transactionRef;
            billPaymentBean.qrData = this.qrData;

            let modal = this.modalCtrl.create(PIBMobileTransactionDetail, {payment: billPaymentBean});
            modal.present();
        }
    }

    public isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }

    private extractRefInfos(refInfos: any) {
        for (let i=0; i < refInfos.length; i++) {
            let refInfo = refInfos[i];
            let label = this.getDisplayLabel(refInfo);
            switch (i) {
                case 0:
                    //ref1
                    if(!label) {
                        this.detailData.ref1Text = this.translateService.instant('lbl.ref1')
                    }else {
                        this.detailData.ref1Text = label
                    }
                    this.detailData.reference1 = this.getDisplayValue(refInfo);
                    break;
                case 1:
                    //ref2
                    if(!label) {
                        this.detailData.ref2Text = this.translateService.instant('lbl.ref2')
                    }else {
                        this.detailData.ref2Text = label
                    }
                    this.detailData.reference2 = this.getDisplayValue(refInfo);
                    break;
                case 2:
                    //ref1
                    if(!label) {
                        this.detailData.ref3Text = this.translateService.instant('lbl.ref3')
                    }else {
                        this.detailData.ref3Text = label
                    }
                    this.detailData.reference3 = this.getDisplayValue(refInfo);
                    break;
            }
        }
    }

    private getDisplayLabel(refInfo: any) {
        let label = null;
        if (refInfo && refInfo.textEn) {
            label = refInfo.textEn;
        }
        return label;
    }

    private getDisplayValue(refInfo: any) {
        return refInfo.value;
    }
}


