import { Component, OnInit } from '@angular/core';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { BillPaymentService } from '../../../bill-payment/bill-payment.service';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { RequestToPayService } from '../../request-to-pay.service'
import { MasterDataService } from '../../../../share/service/master-data.service';
@Component({
    selector: 'history-request-to-pay-list-page',
    templateUrl: './history-request-to-pay-list-page.html'
})
export class HistoryRequestToPayListPageComponent implements OnInit {

    selectOptions: { title: string; };
    timeline: { 
        data: {
            rawData: Array<any>
        },
        title: string; 
    };
    monthList: { options: Array<any>; selected: any; };
    alertConfig: { title: string, type: string, message: string, show: boolean, option: any };

    protected historyData: { period: string; paging: { page: number; pageSize: string; }, accountNo: string; };
    constructor(
        public constants: Constants,
        public translate: TranslateService,
        public permissionService:PermissionService,
        public requestToPayService:RequestToPayService,
        public billPaymentService:BillPaymentService,
        public permissionChangeRoute:PermissionChangeRoute,
        public masterDataService:MasterDataService) {
    }

    ngOnInit(): void {

         this.selectOptions = {
            title: this.translate.instant('label.selectPeriod')
        };

        this.timeline = {
            data: {
                rawData: []
            },
            title: 'RTP',
        };

        this.monthList = {
            options: [],
            selected: {
                label: "",
                value: ""
            }
        };

        this.historyData = {
            period: '',
            paging: {
                page: 0,
                pageSize: '0'
            },
            accountNo: ''
        };

        this.masterDataService.getAllAnyIDTypes().then((result: any) => {

        });
        this.initialMonthList().then((result: any) => {
            this.historyData.period = this.monthList.selected.value;
            this.getRTPHistory();
        });
        // init data
    }

    onChangePeriod(): void {
        this.historyData.paging.page = 1;
        this.getRTPHistory();
    }

    onActions(data: any): void {
        if (data.name === 'info') {
            let tmpdata = data.data;
           this.requestToPayService.SelectedRTPIncomingData = tmpdata.rawData;
           this.permissionChangeRoute.changeRoute('HISTORY_RTP_DETAIL');
        }
    }

    protected getRTPHistory() {
        this.billPaymentService.requestRTPHistory(this.historyData).then((result: any) => {
            let tmpresult = result.responseJSON.result;
            if (tmpresult.responseStatus.responseCode === this.constants.RESP_CODE_SUCCESS) {
                this.timeline.data = {
                    rawData: tmpresult.value.rtpInfoDetailList
                };
            } else {
                this.timeline.data = {
                    rawData: []
                };
                this.onError(tmpresult);
            }

        }, function (error) {

        });
    }

    protected initialMonthList() {
        let p = new Promise((resolve, reject) => {
            this.masterDataService.getCurrentDate().then((result: any) => {
                let currDate = result;
                let currMonth = currDate.getMonth() + 1;
                let currYear = currDate.getFullYear();
                let month;

                for (let count = 0; count < 6; count++) {
                    currMonth--;
                    if (currMonth < 0) {
                        currYear = currDate.getFullYear() - 1;
                        currMonth = 12 + currMonth;
                    }
                    if (currMonth < 9) {
                        month = "0" + (currMonth + 1);
                    } else {
                        month = currMonth + 1;
                    }
                    let json = {
                        "label": this.constants.MONTH_FULLNAME[this.translate.currentLang][currMonth] + " " + currYear,
                        "value": currYear + "-" + month
                    };
                    this.monthList.options.push(json);
                    if (count === 0) {
                        this.monthList.selected = json;
                    }
                }
                resolve(true);
            });
        });

        return p;
    }

    private onError(responseData: any) {

        this.alertConfig = {
            title: '',
            type: 'danger',
            message: 'nodata',
            show: true,
            option: {}
        };
    }

    
}



