import { Component, OnInit } from '@angular/core';
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
@Component({
    selector: 'schedule-sub-menu-page',
    template: ` <ion-header>
                    <ion-navbar>
                        <ion-title>Manage schedule menu</ion-title>
                    </ion-navbar>
                </ion-header>

                <ion-content>
                    <button class="grid-center" *ngFor="let m of menus; let i = index;" (click)="navigate(m)">
                        <ion-icon class="circle icon-menu {{m.icon}}"></ion-icon>
                        <p>{{m.name}}<p>
                    </button> 
                </ion-content>`
})
export class ScheduleSubMenuComponent  implements OnInit {
    private menus: any;
    constructor(public permissionChangeRoute: PermissionChangeRoute){
    }
    ngOnInit() {
         this.menus = [{ name: 'Fund Transfer', icon: 'ios-home', menuCode: 'MANAGE_SCHEDULE.FUND'},
             { name: 'Bill Payment', icon: 'ios-home', menuCode: 'MANAGE_SCHEDULE.BILL_PAYMENT'}
         ];
    }

    navigate(params){

        this.permissionChangeRoute.prevUrl=params.name;
        this.permissionChangeRoute.changeRoute(params.menuCode);
    }
} 


