import { Component } from '@angular/core';
import { AlertController } from 'ionic-angular';
import { Constants } from '../../../share/service/constants';
import { OtherAccountService } from '../other-account.service';
import { UtilService } from '../../../share/service/util.service';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionAction } from '../../../share/service/permission.service';
import { OtherAccountAddEditConfirm } from '../other-account-add-edit-confirm';
import { PermissionChangeRoute } from '../../../share/service/permission.service';
import { BankCodeDataService } from '../../../share/service/bankcode-data.service';
import { StepWizard } from '../../../share/component/step-wizard/step-wizard.component';
import { NavController } from 'ionic-angular';

@Component({
    selector: 'add-edit-other-account-confirm',
    templateUrl: './other-account-add-edit.html'
})
export class MobileOtherAccountAddEditConfirm extends OtherAccountAddEditConfirm {
    public stepWizard: StepWizard;
    public verifyOTPStyle: string;
    public verifyOTPAction: string;
    public confirmAddEditExtAccountData: any;
    disabledAccountNo: any;

    constructor(public permissionAction: PermissionAction,
        public constants: Constants,
        public otherAccountService: OtherAccountService,
        public permissionChangeRoute: PermissionChangeRoute,
        public alertCtrl: AlertController,
        public translateService: TranslateService,
        public bankCodeDataService: BankCodeDataService,
        public utilService: UtilService,
        public nav: NavController) {
        super(permissionAction, constants, otherAccountService, translateService, bankCodeDataService, permissionChangeRoute, utilService);
        this.confirmAddEditExtAccountData = this.otherAccountService.confirmAddExtAccountData;
    }

    public showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translateService.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translateService.instant('btn.close')]
        });

        alert.present();
    }

    public submitAddOtherAccount(params) {
        this.otherAccountService.setParam(params);
        this.otherAccountService.setActionCode('ACT_EXTERNAL_ACCOUNT_ADD_SUBMIT');
        this.otherAccountService.setProcedure('addExternalAccountSubmitProcedure');
        this.otherAccountService.masterData().then((objResponse: any) => {
            let result = objResponse.responseJSON.result;

            if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                this.permissionChangeRoute.changeRoute('OTHER_ACCOUNTS');
            } else {
                let messageAlert = result.responseStatus.errorMessage;
                this.showAlert(messageAlert);
            }
        }, function (error) {
            
        });
    }

    public submitExternalAccountWithoutOTP(data) {
      
        this.otherAccountService.requestSubmitExternalAccountWithoutOTP(data).then((objResponse: any) => {
            let result = objResponse.responseJSON.result;
              if (result.responseStatus.responseCode == this.constants.RESP_CODE_SUCCESS) {
                let alert = this.alertCtrl.create({
                    title: this.translateService.instant('label.Success'),
                    subTitle: this.translateService.instant('label.addOtherAccountSuccess'),
                    buttons: [{
                        text: this.translateService.instant('button.backToHome'),
                        role: 'cancel',
                        handler: () => {
                            this.nav.popToRoot();
                        }
                    }]
                });
                alert.present();
            } else {
                let messageAlert = result.responseStatus.errorMessage;
                this.showAlert(messageAlert);
            }

        }, function (error) {
            
        });
    }

    // Override a base class method
    public requestOTPChanged(result) {
        this.isRequestOTP = true;

        if (typeof result.responseCode === "undefined") {
            this.OTPData.otpRefcode = result.otpRefcode;
            this.OTPData.tokenOtp = result.tokenOtp;
            this.OTPData.pin = '';
        } else {
            let messageAlert = result.errorMessage;
            this.showAlert(messageAlert);
        }
    }

    ngOnDestroy(): void {
        if (this.permissionChangeRoute.prevUrl !== 'OTHER_ACCOUNTS.add1') {
            this.otherAccountService.tempAddExtAccountData = undefined;
        }

        this.permissionChangeRoute.prevUrl = null;
    }
}


