import { Component } from '@angular/core';
import { NavController } from 'ionic-angular';
import { Constants } from '../../../../share/service/constants';
import { TranslateService } from "ng2-translate/src/translate.service";
import { PermissionChangeRoute } from '../../../../share/service/permission.service';
import { StepWizard } from '../../../../share/component/step-wizard/step-wizard.component';
import { PromptPayRegisterSuccessComponent } from '../../prompt-pay-register-success.component';
import { PromptPayRegisterServiceMain } from '../../../../pages/prompt-pay/prompt-pay-register.service';

@Component({
  selector: 'pib-mobile-prompt-pay-register-success',
  templateUrl: './pib-mobile-prompt-pay-register-success.html'
})
export class PIBMobilePromptPayRegisterSuccessComponent extends PromptPayRegisterSuccessComponent {
  public stepWizard: StepWizard;

  constructor(public constants: Constants,
    public promptPayRegisterServiceMain: PromptPayRegisterServiceMain,
    public permissionChangeRoute: PermissionChangeRoute,
    public translateService: TranslateService,
    public nav: NavController) {
    super(constants, promptPayRegisterServiceMain, translateService, permissionChangeRoute);
  }

  ngOnInit(): void {
    this.stepWizard = {
      input: {
        data: [{ name: '1', label: 'stepWizard.enterDetails' }, { name: '2', label: 'stepWizard.confirm' }, { name: '3', label: 'stepWizard.complete' }],
        step: 2,
        style: this.constants.STYLE_PIB_MOBILE
      }
    };

    this.confirmRegisterAnyIDData = this.promptPayRegisterServiceMain.confirmRegisterAnyIDData;
    this.verifyConfirmRegisterAnyIDData();
  }

  gotoMenuList() {
    this.nav.popToRoot();
  }
}


