import { Constants } from '../../../../share/service/constants';
import { Component, OnInit, OnDestroy, Output, EventEmitter } from '@angular/core';
import { TranslateService } from "ng2-translate/src/translate.service";
import { FundTransferService } from '../../../../share/service/fund-transfer.service';
import { BankCodeDataService } from '../../../../share/service/bankcode-data.service';
import { ModalController, NavController, AlertController } from 'ionic-angular';
import { PermissionChangeRoute, PermissionService } from '../../../../share/service/permission.service';
import { Complete } from '../../../../share/component/step-process/mobile/complete.component';
import { BillPaymentService } from '../../bill-payment.service';
import { BillPaymentBean } from '../../../../share/bean/bill-payment-bean';
import { PIBMobileTransactionDetail } from './pib-mobile-transaction-detail.component'
import { UtilService } from '../../../../share/service/util.service';

@Component({
    selector: 'bill-payment-complete',
    templateUrl: './bill-payment-complete.html'
})

export class PIBMobileBillPaymentComplete extends Complete implements OnInit, OnDestroy {
    
    authorized: any;
    billPaymentObj: BillPaymentBean;
    private userCardType: string;
    isWaitToSaveSlip = true;

    @Output() isExistingBillerInfo = new EventEmitter();

    constructor(
        public constants: Constants,
        public navCtrl: NavController,
        public modalCtrl: ModalController,
        public alertCtrl: AlertController,
        public translate: TranslateService,
        public permissionChangeRoute: PermissionChangeRoute,
        public bankCodeDataService: BankCodeDataService,
        public fundTransferService: FundTransferService,
        public permissionService: PermissionService,
        public billPaymentService: BillPaymentService,
        public utilService: UtilService
    ) { 
        super(constants,navCtrl,modalCtrl,alertCtrl,translate,permissionChangeRoute,bankCodeDataService,fundTransferService,permissionService);
    }

    ngOnInit(): void {
        

        this.authorized = {
            printSlip: this.permissionService.getActionCode().PRINT_SLIP_PAY_BILL,
            makeTransaction: this.permissionService.getActionCode().PAY_BILL
        };        
        
        this.billPaymentObj = this.billPaymentService.getBillPaymentObj();

        this.isExistingBillerInfo.emit(this.billPaymentObj.existingBillerInfo === 'N');
        this.billPaymentService.setBillPaymentObj(null);

        this.billPaymentService.getCustomerType().then((result: any) => {
            this.userCardType = result;
        });

        setTimeout(() => {
            if (this.billPaymentObj.canPrintSlip) {
                this.openModal('VIEW_TRANSACTION_DETAIL', this.billPaymentObj);
            }else {
                this.isWaitToSaveSlip = false;
            }
        }, 1000);
    }

    ngOnDestroy(): void {
        //this.billPaymentService.setBillPaymentObj(null);
    }

    newBillPay() {
        this.permissionChangeRoute.targetAction = "NEW";
        this.permissionChangeRoute.pushRoute("PAY_BILL");
    }

    openModal(action: string, selected: any) {
        console.log(selected)
        if (action == 'VIEW_TRANSACTION_DETAIL') {
            let modal = this.modalCtrl.create(PIBMobileTransactionDetail, { payment: selected });
            modal.present();
            this.isWaitToSaveSlip = false;
        }
    }

    showAlert(messageAlert) {
        let alert = this.alertCtrl.create({
            title: this.translate.instant('label.warning'),
            subTitle: messageAlert,
            buttons: [this.translate.instant('btn.close')]
        });
        
        alert.present();
    }

    isEDonationCategory(id: string): boolean {
        return id === this.constants.E_DONATION_CATEGORY_ID;
    }

    isCitizenCardType() {
        return this.userCardType === 'I';
    }
}


